/* BLACKJACK CONTENT IMAGE SKIN */
/* =================== */

$sdc-article-factbox-break-small: 480px !default;
$sdc-article-factbox-break-medium: 680px !default;

$sdc-article-factbox-glint: greetings-to-world !default;
$sdc-article-factbox-base-font-size: 1em !default;
$sdc-article-factbox-title-align: center !default;
$sdc-article-factbox-text-align: left !default;

$sdc-article-factbox-caption-background: #fff !default;

$sdc-article-factbox-text-color: #4a4a4a !default;

// Switch this to use --gradient-linear-light when global var has been defined
$sdc-article-factbox-text-background: linear-gradient(180deg, #fafafa 0%, #f1f2f2 100%) !default;

$sdc-article-factbox-border-color: #eaeaea !default;
$sdc-article-factbox-border-top: 1px solid $sdc-article-factbox-border-color !default;
$sdc-article-factbox-border-right: 0 !default;
$sdc-article-factbox-border-bottom: $sdc-article-factbox-border-top !default;
$sdc-article-factbox-border-left: 0 !default;

$sdc-article-factbox-text-width: 700px !default;
$sdc-article-factbox-vertical-padding: 2.2em !default;
$sdc-article-factbox-vertical-padding-small: 1.5rem !default;
$sdc-article-factbox-text-indent: 32px !default;
