.sdc-article-quote--full-bleed {
    background-color: $sdc-article-quote-bg;

    .sdc-article-quote__content {
        margin: $sdc-article-quote-margin-top $sdc-article-quote-edge-margin;
        max-width: $sdc-article-quote-text-max-width;
        border: none;
    }
}

@media (min-width: $sdc-article-quote-break-xlarge) {
    .sdc-article-quote--full-bleed {
        .sdc-article-quote__text {
            font-size: $sdc-article-quote-text-xlarge;
            margin-bottom: 0.5em;
        }
    }
}

@media (min-width: $sdc-article-quote-break-xxlarge) {
    .sdc-article-quote--full-bleed {
        .sdc-article-quote__text {
            font-size: $sdc-article-quote-text-xxlarge;
            line-height: 1.05;
        }

        &__citation {
            font-size: $sdc-article-quote-citation-text-xxlarge;
        }
    }
}
