/* Default styling paramters for this component that can be overwritten at project level */

$sdc-site-localnav-background: #eee !default;
$sdc-site-localnav-background-filter: none !default;
$sdc-site-localnav-background-filter-more: white !default;
$sdc-site-localnav-color: inherit !default;
$sdc-site-localnav-more-background: white !default;
$sdc-site-localnav-header-keyline: currentColor !default;
$sdc-site-localnav-more-keyline: #ccc !default;
$sdc-site-localnav-more-item-keyline: #ccc !default;
$sdc-site-localnav-link-mobile-keyline: #ccc !default;
$sdc-site-localnav-item-link-underline-color: blue !default;
$sdc-site-localnav-mobile-icon-color: #444 !default;
$sdc-site-localnav-z-index: 9 !default;
$sdc-site-localnav-margin-top-default: 0 !default;
$sdc-site-localnav-margin-top-xlarge: 0 !default;
$sdc-site-localnav-margin-bottom: 1em !default;

$sdc-site-localnav-header-gap-desktop: 1.3em !default;
$sdc-site-localnav-header-gap-mobile: .9em !default;
$sdc-site-localnav-links-height-desktop: 3em !default;
$sdc-site-localnav-links-indent: 0.67em !default;
$sdc-site-localnav-links-border-hover: 3px !default;
$sdc-site-localnav-links-border-current: 3px !default;
$sdc-site-localnav-transition: 0.2s ease-in-out !default;
$sdc-site-localnav-mobile-indent: 0.65em !default;
$sdc-site-localnav-additional-spacing: 2px !default;
$sdc-site-localnav-select-menu-color: $auriga !default;
$sdc-site-localnav-select-menu-keyline: #e2e2e2 !default;
$sdc-site-localnav-select-menu-hover: #f4f5f7;

// Breakpoints
$sdc-site-localnav-break-medium: 540px !default;
$sdc-site-localnav-break-xlarge: 801px !default;