.sdc-site-sponsor {
  position: relative;

  &__inner {
    width: 100%;
    position: relative;
    overflow: hidden;
  }

  &__link {
    z-index: 1;
    // position content vertically and horizontally centred:
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
  }

  &__label {
    display: block;
    margin-right: $sdc-site-sponsor-label-logo-separation-default;
    color: $sdc-site-sponsor-label-colour;
    font-size: $sdc-site-sponsor-label-size-default;
    text-align: right;

    .sdc-site-sponsor__link:hover &,
    .sdc-site-sponsor__link:focus & {
      color: $sdc-site-sponsor-label-colour-hover;
      text-decoration: underline;
      text-decoration-color: $sdc-site-sponsor-label-colour-hover;
    }
  }

  &__logo {
    display: block;
    height: $sdc-site-sponsor-logo-height-default;

    @media (min-width: $sdc-site-sponsor-break-medium) {
      height: $sdc-site-sponsor-logo-height-medium;
    }
  }
}
