// Article Widget defaults
.sdc-article-body {

  .sdc-article-widget {
    clear: both;
    box-sizing: $sdc-article-body-box-sizing;
    transition: margin-left $sdc-article-body-responsive-transition;

    // reset default font-size for widgets:
    font-size: 1rem;
  }

  .sdc-article-widget:not([class*="--full-bleed"]) {
    @extend #{$sdc-article-body-wrap-class} !optional;
    @extend %sdc-article-body-width-limiter !optional;

    @if $simplify-for-amp == false {
      // override max-width for widgets:
      max-width: $sdc-article-widget-default;

      @media all and (min-width: $sdc-article-body-break-small) {
        max-width: $sdc-article-widget-small;
      }

      @media all and (min-width: $sdc-article-body-break-medium) {
        max-width: $sdc-article-widget-medium;
      }
    }
  }
}
