@mixin desktop-view {

  .sdc-site-localnav__wrap {
    display: flex;
    margin-left: -$sdc-site-localnav-links-indent;
    margin-right: -$sdc-site-localnav-links-indent;
  }

  .sdc-site-localnav__header {
    vertical-align: middle;
    white-space: nowrap;
    position: relative;
    font-size: 1.05em;
    font-weight: bold;
  }

  .sdc-site-localnav__header-title {
    display: flex;
    align-items: center;
    height: $sdc-site-localnav-links-height-desktop - 0.15;
    padding-right: $sdc-site-localnav-header-gap-desktop;
    padding-left: $sdc-site-localnav-links-indent;
    vertical-align: middle;
  }

  .sdc-site-localnav__header .sdc-site-localnav__icon {
    display: none;
  }

  .sdc-site-localnav__header:after {
    content: "";
    height: .8em;
    width: 1px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background: currentColor;
    opacity: 0.8;
  }

  // ensure the gap left on first link matches gap right on header above
  .sdc-site-localnav__header + .sdc-site-localnav__body .sdc-site-localnav__item:first-of-type > .sdc-site-localnav__item-link:not(.sdc-site-localnav__item-link--additional) {
    padding-left: $sdc-site-localnav-header-gap-desktop;
  }

  .sdc-site-localnav__header + .sdc-site-localnav__body .sdc-site-localnav__item[data-visibility="mobile"]:first-of-type + .sdc-site-localnav__item > .sdc-site-localnav__item-link:not(.sdc-site-localnav__item-link--additional) {
    padding-left: $sdc-site-localnav-header-gap-desktop;
  }

  .sdc-site-localnav__body {
    display: block;
    vertical-align: middle;
    position: relative;
    width: 100%;
    height: $sdc-site-localnav-links-height-desktop;
  }

  .localnav__body-navigation {
    display: flex;
    justify-content: space-between;
    flex-flow: row nowrap;
  }

  .localnav__body-navigation__group {
    position: relative;
    flex: 1;
  }

  .localnav__body-navigation__group--additional[class] {
    padding-right: $sdc-site-localnav-links-indent;
    white-space: nowrap;
    flex: 0 0 auto;
  }

  .sdc-site-localnav__items--additional[class] {
    padding-right: 0;
  }

  .sdc-site-localnav__item--additional[class] {
    text-align: right;
    margin-left: $sdc-site-localnav-additional-spacing;
  }

  .sdc-site-localnav__items {
    width: 100%;
    width: fit-content;
    list-style: none;
    overflow: hidden;
    position: relative;
    padding-right: 80px;
  }

  .no-js .sdc-site-localnav__items {
    overflow: visible;
  }

  .sdc-site-localnav__item[aria-hidden="true"] {
    visibility: hidden;
  }

  .no-js .sdc-site-localnav__item[aria-hidden="true"],
  .sdc-site-localnav__item[aria-hidden="false"] {
    visibility: visible;
  }

  .sdc-site-localnav__item--more {
    position: absolute;
    right: 0;
    top: 0;
    visibility: hidden;
  }

  .sdc-site-localnav__item-link--more:after {
    content: none !important;
  }

  .sdc-site-localnav__item-link > .sdc-site-localnav__icon {
    margin: 0 0 0 .2em;
  }

  .sdc-site-localnav__item-link[aria-expanded="true"] > .sdc-site-localnav__icon {
    transform: rotate(180deg);
  }

  .sdc-site-localnav__item-link:focus {
    z-index: 2;
  }

  // overflow nav menu
  .sdc-site-localnav__more {
    position: absolute;
    right: 0;
    background: $sdc-site-localnav-more-background;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
    width: 75vw;
    max-width: 220px;
    display: none;
    font-size: 0.9em;
    padding: 0.75em 1em 1em;
    border-radius: 0 0 5px 5px;
  }

  .sdc-site-localnav__more[aria-hidden="false"] {
    display: block;
    min-width: 180px;
  }

  .sdc-site-localnav__more > .sdc-site-localnav__item {
    display: none;
    width: 100%;
  }

  .sdc-site-localnav__more .sdc-site-localnav__item-link {
    line-height: 2.6em;
  }

  .sdc-site-localnav__more > .sdc-site-localnav__item:last-of-type {
    border: 0;
  }

  [data-visibility="mobile"] {
    display: none;
  }
}