.sdc-article-author {
  font-size: $sdc-article-author-text-default;
  display: flex;
  padding-top: 2em;
  padding-bottom: 1.75em;

  &__byline {
    font-weight: bold;
  }

  &__avatar {
    flex: 0 0 calc(40px + 1em);
    padding-right: 1em;
  }

  &__image {
    display: block;
    width: 100%;
    border-radius: $author-image-border-radius;
  }

  &__details {
    flex: auto;
    vertical-align: middle;
  }

  &__link {
    color: $sdc-article-author-link-colour;
  }

  &__name {
    display: block;
    margin: 0 0 .3em;
    font-weight: bold;
  }

  &__role {
    color: $sdc-article-author-role-colour;
    margin: 0;
    font-size: $sdc-article-author-text-default;
  }
}

@media all and (min-width: $sdc-article-author-break-medium) {

  .sdc-article-author {
    font-size: $sdc-article-author-text-medium;
    padding-top: 2.5em;
    padding-bottom: 2em;

    &__avatar {
      flex: 0 0 calc(50px + 1em);
      padding-right: 1em;
    }
  }
}
