.sdc-trust-project {
    padding-bottom: 36px;

    &__link::before {
        width: 1.188em;
        height: 1.125em;
        margin-right: 0.6em;
        content: "";
        display: inline-block;
        vertical-align: middle;
        position: relative;
        top: -2px;
        background-image: url(data:image/svg+xml;base64,);
        background-size: 100%;
    }

    &__arrow-icon {
        width: 4px;
        height: 8px;
        object-fit: contain;
        display: inline-block;
        margin: 0 0 0 3px;
        background-color: #707070;
        background: url('data:image/svg+xml;base64,');
    }
}