.amp-page-limit .ob-smartfeed-wrapper .OUTBRAIN {
  margin-top: 40px !important;
}

.sdc-article-gallery:not(.sdc-article-widget--full-bleed),
.sdc-site-video:not(.sdc-article-widget--full-bleed),
.sdc-article-image:not(.sdc-article-image--full-bleed) {
  max-width: 976px !important;
  margin-left: auto;
  margin-right: auto;
}

.sdc-article-gallery:not(.sdc-article-widget--full-bleed),
.sdc-article-body--narrate .sdc-article-image,
.sdc-article-body--timeline .sdc-article-image,
.sdc-article-body--feature .sdc-article-image,
.sdc-site-component-top__media .sdc-article-image:not(.sdc-article-widget--full-bleed){
  padding: 0 !important;
}

.article-elections-map-widget {
  width: 100%;
  border: 0;
  height: 550px;
}

.sdc-site-embedded .sdc-site-video {
  overflow: hidden;
}

// Disable full screen ads on iPad
.is-ipad .vjs-ad-control-bar .vjs-fullscreen-control {
  display: none;
}
