.sdc-article-tweet {
  overflow: hidden;
  margin-left: 0;
  margin-right: 0;
  margin-top: $sdc-article-tweet-margin;
  margin-bottom: $sdc-article-tweet-margin;
  clear: both;
  display: block;
  position: relative;

  &__content {
    text-align: center;
    max-width: 960px;
    box-sizing: border-box;
    width: 100%;

    & > div {
      margin: 0 auto;
    }

    & > twitter-widget {
      margin: 0 auto;
    }

    .twitter-tweet-rendered {
      /* Override standard twitter margins */
      margin-top: 0 !important;
      margin-bottom: 0 !important;
    }
  }

  &--full-bleed {
    background-color: $sdc-article-tweet-background;

    .sdc-article-tweet__content {
      margin-top: $sdc-article-tweet-padding;
      margin-bottom: $sdc-article-tweet-padding;
    }
  }
}

@media (min-width: $sdc-article-tweet-width-threshold) {

  .sdc-article-tweet {

    &__inner {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

@media (min-width: $sdc-article-tweet-break-xlarge) {

  .sdc-article-tweet {

    &--half-width {
      margin-left: $sdc-article-tweet-edge-margin;

      .sdc-article-tweet {
        background-color: transparent;

        &__content {
          margin-right: 0;
          max-width: 100%;
        }
      }
    }
  }
}
