
.sdc-interactive-hot-spots__pin {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: white;
  position: absolute;
  transform: translate(-50%, -50%);
  will-change: transform;
  z-index: 4;
  transition: transform 0.33s;
  box-sizing: content-box;
  background-clip: content-box;
  text-align: center;
  color: $sdc-interactive-hot-spots-tooltip-color;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  -webkit-user-select: none;
  user-select: none;
  -webkit-user-drag: none;
  filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.4));
  pointer-events: all;
  border: 0;
  cursor: pointer;
  font-family: inherit;
}

.sdc-interactive-hot-spots__pin--cluster {
  width: 20px;
  height: 20px;
}

.sdc-interactive-hot-spots__pin[data-selected] {
  z-index: 5;
  box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.3);
  width: 12px;
  height: 12px;
  top: 1px;
  left: -3px;
}

.sdc-interactive-hot-spots__pin:after {
  background: white;
  content: attr(data-title);
  display: block;
  position: absolute;
  padding: 4px 3px;
  font-size: 14px;
  line-height: 1;
  left: 100%;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 0 0 4px;
  width: 95px;
  border-radius: 3px;
  font-weight: 600;
  box-sizing: border-box;
}

.sdc-interactive-hot-spots__pin--mirror:after {
  right: 100%;
  left: auto;
  margin: 0 4px 0 0;
}

.sdc-interactive-hot-spots__pin--mirror-below:after {
  right: auto;
  left: 50%;
  top: 150%;
  transform: translateX(-50%);
  margin: 0;
}

.sdc-interactive-hot-spots__pin[data-selected]:after {
  visibility: visible !important;
  left: calc(100% + 3px);
}

.sdc-interactive-hot-spots__pin--mirror[data-selected]:after {
  left: auto;
  right: calc(100% + 3px);
}

.sdc-interactive-hot-spots__pin--mirror-below[data-selected]:after {
  left: 50%;
}

.sdc-interactive-hot-spots__overlay[data-zoomed] .sdc-interactive-hot-spots__pin {
  transform: translate(-50%, -50%) scale(0.5);
}

.sdc-interactive-hot-spots__overlay[data-zoomed] .sdc-interactive-hot-spots__pin[data-selected] {
  transform: translate(-50%, -50%) scale(0.75);
}

@media all and (hover: hover) {

  .sdc-interactive-hot-spots__pin:hover:after {
    visibility: visible;
  }
}

@media all and (max-width: 580px) {

  .sdc-interactive-hot-spots__pin:after {
    visibility: hidden;
  }
}