// margins for widgets
.sdc-article-body {

  // Article Widgets
  .sdc-article-widget {
    margin-top: $sdc-article-body-widget-margin-default;
    margin-bottom: $sdc-article-body-widget-margin-default;

    @if $simplify-for-amp == false {
      @media all and (min-width: $sdc-article-body-break-large) {
        margin-top: $sdc-article-body-widget-margin-large;
        margin-bottom: $sdc-article-body-widget-margin-large;
      }

      @media all and (min-width: $sdc-article-body-break-xlarge) {
        margin-top: $sdc-article-body-widget-margin-xlarge;
        margin-bottom: $sdc-article-body-widget-margin-xlarge;
      }
    }

    @if $simplify-for-amp == false {
      // remove bottom margin from last item
      &:last-child {
        margin-bottom: 0;
      }
      // override widget margin when following a main heading
      h1 + .sdc-article-widget,
      h2 + .sdc-article-widget,
      h3 + .sdc-article-widget {
        @media all and (min-width: $sdc-article-body-break-xlarge) {
          margin-top: $sdc-article-body-widget-margin-large;
        }
      }
    }
  }

  @if $simplify-for-amp == true {

    > .sdc-article-widget {
      // remove bottom margin from last item
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  // make the space between adjacent strapline widgets half the normal spacing
  .sdc-article-strapline--default-layout + .sdc-article-strapline--default-layout {
    margin-top: -0.5*($sdc-article-body-widget-margin-default);

    @if $simplify-for-amp == false {
      @media all and (min-width: $sdc-article-body-break-large) {
        margin-top: -0.5*($sdc-article-body-widget-margin-large);
      }

      @media all and (min-width: $sdc-article-body-break-xlarge) {
        margin-top: -0.5*($sdc-article-body-widget-margin-xlarge);
      }
    }
  }
}
