@import "skin";

.sdc-site-topic-banner {
  margin-top: $sdc-site-topic-banner-top-margin-default;

  &--single-line {
    margin-top: 0;
  }

  &__inner {
    font-size: $sdc-site-topic-banner-font-size-default;
    line-height: 1;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    position: relative;
  }

  &__icon {
    width: $sdc-site-topic-banner-icon-size-default;
    height: $sdc-site-topic-banner-icon-size-default;
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__header {
    padding: $sdc-site-topic-banner-header-padding-default;
    padding-left: $sdc-site-topic-banner-icon-size-default + $sdc-site-topic-banner-header-padding-left-default;
    margin-bottom: $sdc-site-topic-banner-header-margin-bottom-default;
    display: inline-block;
    min-height: $sdc-site-topic-banner-icon-size-default;
  }

  &__supplementary-title {
    display: block;
    font-size: $sdc-site-topic-banner-font-size-title-default;
    font-weight: normal;
  }

  &__main-title {
    display: block;
    font-size: $sdc-site-topic-banner-font-size-subtitle-default;
    margin-top: 5px;
    font-weight: normal;
  }

  &__link {
    color: inherit;
    text-decoration: none;
  }

  @media all and (min-width: $sdc-site-topic-banner-break-medium) {
    margin-top: $sdc-site-topic-banner-top-margin-medium;

    &--single-line {
      margin-top: 0;
    }

    &__header {
      padding: $sdc-site-topic-banner-header-padding-desktop;
      padding-left: $sdc-site-topic-banner-icon-size-desktop + $sdc-site-topic-banner-header-padding-left-desktop;
      margin-bottom: $sdc-site-topic-banner-header-margin-bottom-desktop;
      min-height: $sdc-site-topic-banner-icon-size-desktop;
    }

    &__icon {
      width: $sdc-site-topic-banner-icon-size-desktop;
      height: $sdc-site-topic-banner-icon-size-desktop;
    }

    &__supplementary-title {
      font-size: $sdc-site-topic-banner-font-size-title-desktop;
    }

    &__main-title {
      font-size: $sdc-site-topic-banner-font-size-subtitle-desktop;
      margin-top: 0;
    }
  }

  // Overrides for a single line banner
  &.sdc-site-topic-banner--single-line {

    .sdc-site-topic-banner {

      &__icon {
        width: $sdc-site-topic-banner-icon-size-single-line-default;
        height: $sdc-site-topic-banner-icon-size-single-line-default;
      }

      &__header {
        min-height: $sdc-site-topic-banner-icon-size-single-line-default;
        padding-left: $sdc-site-topic-banner-icon-size-single-line-default + $sdc-site-topic-banner-header-padding-left-default;
        margin-bottom: $sdc-site-topic-banner-header-margin-bottom-default/2;
      }

      &__main-title {
        margin-top: 0;
      }

      @media all and (min-width: $sdc-site-topic-banner-break-medium) {

        &__icon {
          width: $sdc-site-topic-banner-icon-size-single-line-desktop;
          height: $sdc-site-topic-banner-icon-size-single-line-desktop;
          top: -5px;
        }

        &__header {
          min-height: $sdc-site-topic-banner-icon-size-single-line-desktop - 5px;
          padding-left: $sdc-site-topic-banner-icon-size-single-line-desktop + $sdc-site-topic-banner-header-padding-left-desktop;
          margin-bottom: $sdc-site-topic-banner-header-margin-bottom-desktop/2;
        }
      }
    }
  }
}
