/* BLACKJACK CONTENT IMAGE SKIN */

/* =================== */

// Colours
$sdc-article-image-content-background: #eaeaea !default;
$sdc-article-image-caption-background: #eee !default;
$sdc-article-image-caption-color: #aaa !default;

// Font sizes/layout
$sdc-article-image-caption-text-default: .9em !default;
$sdc-article-image-caption-align: center !default;

// Image sizes/layout
$sdc-article-image-fill-width: 100% !default;
$sdc-article-image-fill-height-tablet: 320px !default;
$sdc-article-image-fill-height-mobile: 192px !default;
$sdc-article-image-fill-height-sm-mobile: 161px !default;

// Spacing
$sdc-article-image-max-width: 100% !default;
$sdc-article-image-margin: 32px !default;
$sdc-article-image-caption-h-padding: 1em !default;
$sdc-article-image-caption-v-padding: 1em !default;

// Breakpoints
$sdc-article-image-break-xsmall: 321px !default;
$sdc-article-image-break-small: 481px !default;
$sdc-article-image-break-medium: 600px !default;
$sdc-article-image-break-large: 701px !default;
$sdc-article-image-break-xlarge: 851px !default;
$sdc-article-image-break-xxlarge: 1020px !default;
