.sdc-site-charts[data-type="progress"] .sdc-site-charts__chart-wrap {
  padding-bottom: 75%;

  .sdc-site-charts__chart-point-label-text {
    font-size: calc(0.005em + 0.005vw);
    text-shadow: 0 5em 50em rgba(0, 0, 0, 0.25);
  }
}

@media all and (min-width: 600px) {

  .sdc-site-charts[data-type="progress"] .sdc-site-charts__chart-wrap {
    padding-bottom: 45%;
  }
}
