// SHARING SKIN
// *===================

$sdc-site-share-top-margin: 0 !default;
$sdc-site-share-right-margin: 0 !default;
$sdc-site-share-bottom-margin: 0 !default;
$sdc-site-share-left-margin: 0 !default;

$sdc-site-share-bottom-padding: 0 !default;
$sdc-site-share-button-spacing: 0 !default;

// Button bg
$sdc-site-share-button-bg: #fff !default;
$sdc-site-share-button-bg-hover: darken($sdc-site-share-button-bg, 5%) !default;

// Button border
$sdc-site-share-border-color: #eee !default;
$sdc-site-share-border-color-hover: darken($sdc-site-share-border-color, 10%) !default;
$sdc-site-share-border-radius: 0 !default;

// Icon
$sdc-site-share-icon-color: #4a4a4a !default;
$sdc-site-share-icon-color-hover: darken($sdc-site-share-icon-color, 10%) !default;

$sdc-site-share-icon-color-hover-facebook: #3b5998 !default;
$sdc-site-share-icon-color-hover-whatsapp: #25d366 !default;
$sdc-site-share-icon-color-hover-twitter: #00aced !default;
$sdc-site-share-icon-color-hover-email: #dd4b39 !default;


// Transitions
$hover: .2s ease-in-out !default;

// Breakpoints
$sdc-site-share-icon-break-mobile: 900px !default;
