.sdc-site-au {
  text-align: center;
  line-height: 0;
  display: flex !important;
  justify-content: center;
  flex-direction: column-reverse;

  .advert--site-takeover &[data-ad-format="leaderboard"] {
    display: none !important;
  }

  &__label {
    display: none;
    margin-bottom: $sdc-site-mpu-label-padding;
  }

  &__label-text {
    font-size: $sdc-site-mpu-label-font-size;
    color: $sdc-site-mpu-label-color;
  }

  // prevent margin collapsing when no adverts shown in Safari
  &--full-bleed:after {
    content: " ";
    display: table;
  }

  &--full-bleed .sdc-site-au__leaderboard {
    width: 100%;
    background-color: $sdc-site-mpu-full-bleed-background;
    min-height: calc(90px + #{$sdc-site-mpu-full-bleed-padding} * 2);

    & > div {
      padding: $sdc-site-mpu-full-bleed-padding 0;
      box-sizing: content-box;
    }
  }
}

.sdc-article-body .sdc-site-au {
  clear: both;
}

[data-advert-rendered] .sdc-site-au__label {
  display: block;
}

[data-role="ad-container"][style*="display: none"] + .sdc-site-au__label {
  display: none;
}

// mpu
[class*="sdc-site-au__mpu"] {
  display: inline-block;
  min-height: 250px;
  min-width: 300px;
}

// leaderboard
.sdc-site-au__leaderboard {
  display: inline-block !important;
  min-width: 728px;
  min-height: 90px;
}

// takeover ad
// Use font size: 0 to prevent white space appearing when ad is blank
.sdc-site-au[data-ad-format="takeover"] {
  font-size: 0;
}

.sdc-site-au__takeover {
  display: none;
}

// sponsor position
.sdc-site-au--sponsor-position-container {
  position: relative;
  direction: rtl;
}

.sdc-site-au[data-ad-format="sponsor-position"] {
  width: 88px;
  height: 31px;
}

.sdc-site-au--sponsor-position-container > .sdc-site-au[data-ad-format="sponsor-position"] {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.sdc-site-au--sponsor-position-container .sdc-site-component-header__body {
  padding: 0 136px 0 0;
  direction: ltr;
}

.sdc-site-au__standalone-sponsor-container {
  @extend .site-component-vertical-margin!optional;
  @extend .site-wrap!optional;
  @extend .site-wrap-padding!optional;
  display: flex;
  justify-content: flex-end;
}

@media all and #{$sdc-site-mpu-media-mobile} {

  .sdc-site-au__leaderboard {
    min-width: 300px;
    min-height: 50px;
  }

  .sdc-site-au--full-bleed .sdc-site-au__leaderboard {
    min-height: calc(50px + #{$sdc-site-mpu-full-bleed-padding} * 2);
  }

  .sdc-site-au--sponsor-position-container .sdc-site-component-header__body {
    padding: 0 100px 0 0;
  }
}

@media all and #{$sdc-site-mpu-media-display} {

  .sdc-site-au__takeover {
    display: inline-block;
    min-width: 1px;
  }

  .sdc-site-au[data-ad-format="sponsor-position"] {
    width: 120px;
    height: 60px;
  }
}

@media all and #{$sdc-site-mpu-media-edge-to-edge} {

  .sdc-site-au--sponsor-position-container .sdc-site-component-header__body {
    padding-top: 25px;
  }

  .sdc-site-au__standalone-sponsor-container {
    justify-content: center;
  }
}
