.sdc-article-date {
  position: relative;
  padding: $sdc-article-date-padding;
  max-width: 700px;
  margin: $sdc-article-date-margin;

  @media all and ($sdc-article-date-priority-3-breakingpoint) {
    padding-top: $sdc-article-date-padding-top-pad;
    padding-bottom: $sdc-article-date-padding-bottom-pad;
  }

  @media all and ($sdc-article-date-priority-4-breakingpoint) {
    padding-top: $sdc-article-date-padding-top-mobile;
    padding-bottom: $sdc-article-date-padding-bottom-mobile !important;
  }

  &--with-margin[class] {
    margin-bottom: $sdc-article-date-margin-bottom-desktop;

    @media all and ($sdc-article-date-priority-4-breakingpoint) {
      margin-bottom: $sdc-article-date-margin-bottom-mobile;
    }
  }

  &__wrap {
    margin-top: $sdc-article-date-margin-top-desktop;

    @media all and ($sdc-article-date-priority-4-breakingpoint) {
      margin-top: $sdc-article-date-margin-top-mobile;
    }

    &:empty {
      display: none;
    }

    &__icon > svg {
      width: $sdc-article-date-icon-width;
      height: $sdc-article-date-icon-height;
    }
  }

  &__date-time {
    font-size: $sdc-article-date-date-time-font-size;
    color: $sdc-article-date-date-time-text-colour;
    display: inline;

    @media all and ($sdc-article-date-priority-3-breakingpoint) {
      font-size: $sdc-article-date-date-time-font-size-pad;
    }
  }

  &__icon {
    margin-right: $sdc-article-date-time-icon-margin-right;
  }

  &__icon > svg {
    padding-top: $sdc-article-date-time-icon-padding-top;
  }
}
