// ----------------------------------------------------------
// watch-live page overrides
// ----------------------------------------------------------

iframe[src*="youtube.com"] {
  width: 100%;
}

.video-embed {
  position: relative;
  padding-bottom: 56.25%;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.watch-live {
  .sdc-article-custom-markup {
    max-width: 976px !important;
  }

  .sdc-site-share__link {
    padding: 9px 19px !important;
    border-color: #e2e2e2 !important;
  }

  .sdc-site-share:before {
    background-image: none !important;
  }

  .sdc-article-body {
    padding-bottom: 0 !important;
  }

  .section-wrap--shade {
    background-image: linear-gradient(0deg, #fff, #e1e0e4);
  }
}
