/*ARTICLE TABLE COMPONENT*/
/*===================*/

.sdc-article-table {
  position: relative;
}

.sdc-article-table__heading {
  font-weight: $sdc-article-table-heading-weight;
  font-size: $sdc-article-table-heading-size;
  margin: 0 0 0.75em 0;
  padding: 0 $sdc-article-table-spacing-unit;
  text-align: center;
}

.sdc-article-table__body {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

.sdc-article-table__body::-webkit-scrollbar {
  width: $sdc-article-table-scrollbar-size;
  height: $sdc-article-table-scrollbar-size;
}

.sdc-article-table__body::-webkit-scrollbar-thumb {
   background: $sdc-article-table-scrollbar-thumb-colour;
   border-radius: $sdc-article-table-scrollbar-size;
}

.sdc-article-table__body::-webkit-scrollbar-track {
  background: $sdc-article-table-scrollbar-track-colour;
  border-radius: $sdc-article-table-scrollbar-size;
}

.sdc-article-table table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  background: $sdc-article-table-background;
  padding: $sdc-article-table-spacing-unit;
}

.sdc-article-table tr:last-of-type > td {
  border-bottom: 0;
}

.sdc-article-table th,
.sdc-article-table td {
  padding: $sdc-article-table-spacing-unit;
  border-bottom: 1px solid $sdc-article-table-keyline;
  white-space: nowrap;
  vertical-align: top;
  font-weight: normal;
  font-style: normal;
  color: $sdc-article-table-text-colour;
}

.sdc-article-table b,
.sdc-article-table strong {
  font-weight: $sdc-article-table-text-bold;
}

.sdc-article-table th {
  color: $sdc-article-table-header-colour;
  text-align: left;
}


@media (hover: none) {

  .sdc-article-table:before {
    content: "";
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
    bottom: $sdc-article-table-scrollbar-size;
    width: 30px;
    background: linear-gradient(to left, white 0%, rgba(255,255,255,0) 100%);
  }

}
