@import "skin";

.sdc-timeline-node {
  clear: both;
  position: relative;

  // Default margins
  .sdc-article-body--timeline & {
    margin-top: $sdc-timeline-node-margin-top-default;
    margin-bottom: $sdc-timeline-node-margin-bottom-default;
  }

  &:before {
    content: "";
    box-sizing: border-box;
    display: block;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: $sdc-timeline-node-fill;
    border: 1px solid $sdc-timeline-node-outline;
    position: absolute;
    top: 7px;
    transform: translate3d(0, 0, 0);
    left: 5px;
  }

  &__date {
    display: block;
    color: $sdc-timeline-node-date-colour;

    margin: 0 0 .25rem;
    line-height: 1.3;
    font-size: 1.25rem;

    top: -.25rem;
    position: relative;
  }

  &__title {
    font-weight: normal;
    color: $sdc-timeline-node-title-colour;

    line-height: 1.143;
    font-size: 1.75rem;
  }
}

@media all and (min-width: $sdc-timeline-node-break-x-small) {

  .sdc-timeline-node {

    &:before {
      left: 3px;
    }
  }
}

@media all and (min-width: $sdc-timeline-node-break-small) {

  .sdc-timeline-node {

    &:before {
      left: 10px;
      width: 11px;
      height: 11px;
    }

    &__date {
      top: 0;
      margin-bottom: .5rem;
      line-height: 1.09;
      font-size: 1.375rem;
    }

    &__title {
      line-height: 1.111;
      font-size: 2.25rem;
    }
  }
}

@media all and (min-width: $sdc-timeline-node-break-medium) {

  .sdc-timeline-node {

    &:before {
      background: $sdc-timeline-node-fill;
      border: 1px solid $sdc-timeline-node-outline;
      left: 10px;
    }

    &--active {

      &:before {
        background: $sdc-timeline-node-fill-active;
        border-color: $sdc-timeline-node-outline-active;
        animation-duration: .35s;
        animation-name: sdc-timeline-node-become-active;
      }
    }
  }
}

@media all and (min-width: $sdc-timeline-node-break-xx-large) {

  .sdc-timeline-node {

    &:before {
      left: -14px;
      width: 17px;
      height: 17px;
      top: 4px;
    }
  }
}

@keyframes sdc-timeline-node-become-active {
  from { transform: scale(1); }
  20% { transform: scale(1.3); }
  to { transform: scale(1); }
}
