@mixin mobile-view {

  .sdc-site-localnav__header-title {
    display: block;
    font-size: 1.1em;
    font-weight: bold;
    padding: 0.85em 0 0.85em 0.7em;
  }

  .sdc-site-localnav__header-title[aria-expanded="true"] > .sdc-site-localnav__icon,
  .sdc-site-localnav__header-logo-wrap[aria-expanded="true"] > .sdc-site-localnav__icon {
    transform: rotate(180deg);
  }

  .sdc-site-localnav__items {
    padding-right: 0 !important;
    padding-bottom: 0.8em !important;
  }

  .sdc-site-localnav__item--more,
  .sdc-site-localnav__more {
    display: none;
  }

  .sdc-site-localnav__item {
    width: 100%;
  }

  .sdc-site-localnav__item-link {
    border-top: 1px solid $sdc-site-localnav-link-mobile-keyline;
  }

  .sdc-site-localnav__item--additional {
    display: inline-block;
    width: auto;
    border: 0;
    margin-left: $sdc-site-localnav-mobile-indent;
  }

  .sdc-site-localnav__item-link--additional {
    border: 0;
  }
}
