.sdc-interactive-hot-spots {
  max-height: 610px;
  overflow: hidden;
  position: relative;
  line-height: 1.3;
  margin: 0 auto 1.2em;
}

.sdc-interactive-hot-spots:before {
  content: attr(data-label);
  position: absolute;
  z-index: 2;
  bottom: 1em;
  left: 1em;
  color: white;
  font-size: 16px;
  text-shadow: 1px 1px 3px black;
}

.sdc-interactive-hot-spots a {
  text-decoration: none;
}

.sdc-interactive-hot-spots:after {
  content: "";
  display: block;
  padding-bottom: 133%;
}

.sdc-interactive-hot-spots__inner {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.sdc-interactive-hot-spots__wrap {
  will-change: transform;
  transform-origin: 50% 0;
  touch-action: none !important;
  pointer-events: all;
}

.sdc-interactive-hot-spots__overlay,
.sdc-interactive-hot-spots__img {
  pointer-events: none;
}

.sdc-interactive-hot-spots__wrap[data-zoomable] {
  transition: transform 0.33s;
  pointer-events: none;
}

.sdc-interactive-hot-spots__overlay {
  width: 100%;
  padding-bottom: 100%;
  position: relative;
}

.sdc-interactive-hot-spots__img[class] {
  max-width: 100%;
  width: 100%;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  max-height: none;
}

.sdc-interactive-hot-spots__reset[class] {
  text-decoration: none;
  display: inline-block;
  padding: 6px 12px;
  position: absolute;
  background: rgba(0, 0, 0, 0.7);
  font-weight: 500;
  top: 20px;
  left: 20px;
  z-index: 9;
  visibility: hidden;
  border-radius: 3px;
  color: white !important;
  border: 0;
}

.sdc-interactive-hot-spots__reset[data-active] {
  visibility: visible;
}

@media all and (max-width: 580px) {

  .sdc-interactive-hot-spots {
    max-height: 480px;
  }

  .sdc-interactive-hot-spots__reset {
    top: 9px;
    left: 9px;
  }
}
