.sdc-article-image-grid {

  &__video-container {
    box-sizing: border-box;
    position: relative;
    margin-bottom: 1rem;
    overflow: hidden;

    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 0;
    }

    &--4-3:before {
      padding-top: 75%;
    }

    &--16-9:before {
      padding-top: 56.25%;
    }

    &--16-10:before {
      padding-top: 62.5%;
    }
  }

  &__video-iframe {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    border: none;
  }
}
