$sdc-site-header-bg-red: #e10000 !default;
$sdc-site-header-bg-blue: #002672 !default;
$sdc-site-header-bg-fade: rgba(0, 0, 0, .1) !default;

.sdc-site-header--sports {
  background-image: linear-gradient(0deg, $sdc-site-header-bg-fade, transparent 66%), linear-gradient(90deg, $sdc-site-header-bg-blue 0, $sdc-site-header-bg-blue 50%, $sdc-site-header-bg-red 0, $sdc-site-header-bg-red) !important;
  background-repeat: no-repeat;
  color: #4a4a4a;

  .sdc-site-header__inner {
    background-image: linear-gradient(0deg, $sdc-site-header-bg-fade 0, rgba(0, 0, 0, 0) 76%), linear-gradient(to right, $sdc-site-header-bg-blue 0%, $sdc-site-header-bg-blue 55px, $sdc-site-header-bg-red 55px, $sdc-site-header-bg-red 100%);
    background-origin: content-box;
    background-repeat: no-repeat;
  }

  body.advert--site-takeover .site-header__body {
    overflow: hidden;
  }

  .sdc-site-header__logo {
    position: relative;
  }

  .sdc-site-header__logo:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -100%, 0) rotate(-2.55deg);
    pointer-events: none;
    width: 90vw;
    max-width: 900px;
    height: 200px;
    background-image: radial-gradient(closest-side, rgba(255, 255, 255, 0.25), transparent);
    background-position: 0 100px;
  }

  .sdc-site-header__menu-item-link:hover,
  .sdc-site-header__menu-item-link[aria-current="true"] {
    background-image: linear-gradient(0deg, #0a1388, #0a1388) !important;
  }

  .sdc-site-header__subnav {
    color: #fff;
  }

  @media #{$sdc-site-header-desktop-bp} {

    &:before,
    .sdc-site-header__inner:before {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: $sdc-site-header-menu-height;
      background-color: #fff;
      z-index: -1;
    }

    .sdc-site-header__inner {
      background-image: linear-gradient(0deg, $sdc-site-header-bg-fade 0, rgba(0, 0, 0, 0) 76%), linear-gradient(to right, $sdc-site-header-bg-blue 0%, $sdc-site-header-bg-blue 70px, $sdc-site-header-bg-red 70px, $sdc-site-header-bg-red 100%);
    }
  }

  @media #{$sdc-site-header-small-mobile-bp} {

    .sdc-site-header__inner {
      background-image: linear-gradient(0deg, $sdc-site-header-bg-fade 0, rgba(0, 0, 0, 0) 76%), linear-gradient(to right, $sdc-site-header-bg-blue 0%, $sdc-site-header-bg-blue 46px, $sdc-site-header-bg-red 46px, $sdc-site-header-bg-red 100%);
    }
  }
}