.livefyre-pinned {
  font-size: var(--base-font-size);
  display: none;
  margin-bottom: $block-margin-s;
  color: $text-primary;

  &--active {
    display: block;
  }
}

.livefyre-pinned a {
  -ms-word-break: break-all;

  /* Be VERY careful with this, breaks normal words wh_erever */
  word-break: break-all;

  /* Non standard for webkit */
  word-break: break-word;

  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  hyphens: auto;
}

.livefyre-pinned__item {
  background: $light90;
  border-bottom: 1px solid $light80;
  padding: 24px;
  line-height: $text-line-height;
}

.livefyre-pinned__item p {
  margin: 0 0 1.1em;
}

.livefyre-pinned__item p:last-child {
  margin-bottom: 0;
}

.livefyre-pinned__item ul {
  list-style: disc;
}

.livefyre-pinned__item ul,
.livefyre-pinned__item ol {
  margin-bottom: .6em;
  padding-left: 24px;
}

.livefyre-pinned__item a {
  color: $link-text;
  text-decoration: underline;
  font-weight: 600;
  text-decoration-skip: ink;
}

.livefyre-pinned__item strong {
  font-size: var(--base-font-size);
  color: $text-primary;
  font-weight: bold;
}
