.sdc-site-outbrain {
  clear: both;
  margin-bottom: $sdc-site-outbrain-margin-bottom;

  &--match-article-body-copy-width {

    // Allow outbrain to fill its contianer width
    .AR_3.ob-widget {
      max-width: 100%;
    }

    // Increase width limit for outbrain items
    .AR_3.ob-two-rows-strip-layout .ob-dynamic-rec-container {
      max-width: 300px;
    }
  }
}
