$breakpoint-small-device: 320px;

.smartbanner {
  position: relative;
  width: 100%;
  --link-color: #2aa2e1;
  z-index: 100000;
  background: #252525;
  color: var(--shade-2);
  font-size: 14px;
  padding: var(--component-gap);
  display: flex;
  align-items: center;
  line-height: 1;
}

.smartbanner__icon {
  border-radius: 9px;
  width: 45px;
  height: 45px;
  background-size: 100%;
}

.smartbanner__exit {
  position: relative;
  width: 30px;
  height: 30px;
  transform: translateX(-20%);
}

.smartbanner__exit:before,
.smartbanner__exit:after {
  content: "";
  position: absolute;
  left: 48%;
  top: 27%;
  width: 1px;
  height: 45%;
  background: var(--shade-2);
  transform: rotate(45deg);
  opacity: 0.8;
}

.smartbanner__exit:after {
  transform: rotate(-45deg);
}

.smartbanner__info {
  flex: 1;
  margin: 0 0.55em;
  font-size: 95%;
}

.smartbanner__info__title {
  font-weight: bold;
  color: white;
  margin-bottom: 0.2em;
}

@media screen and (max-width: $breakpoint-small-device) {

  .smartbanner__info__price, .smartbanner__info__author {
    font-size: 10px;
  }
}

.smartbanner__button {
  display: block;
  color: var(--link-color);
  font-size: 18px;
  padding: 0.75em 0.75em;
  font-weight: 500;
}