// Colours
$sdc-article-quote-bg: #f4f4f4 !default;
$sdc-article-quote-border-top: 1px solid #e2e2e2 !default;
$sdc-article-quote-border-right: $sdc-article-quote-border-top !default;
$sdc-article-quote-border-bottom: $sdc-article-quote-border-top !default;
$sdc-article-quote-border-left: $sdc-article-quote-border-top !default;
$sdc-article-quote-border-radius: 4px !default;
$sdc-article-quote-text-bg: #fff !default;
$sdc-article-quote-text-colour: #333 !default;
$sdc-article-quote-citation-colour: #666 !default;
$sdc-article-quote-image-overlay-text-colour: #fff !default;
$sdc-article-quote-image-overlay-text-shadow: 0 1px 5px rgba(0, 0, 0, .75) !default;
$sdc-article-quote-image-transparency-color: #000 !default;
$sdc-article-quote-image-transparency: .6 !default;

// Font sizes
$sdc-article-quote-base-font-size: 1rem !default;
$sdc-article-quote-text-default: 1.333333em !default;
$sdc-article-quote-text-medium: 1.555555em !default;
$sdc-article-quote-text-xlarge: 2.11111em !default;
$sdc-article-quote-text-xxlarge: 2.666667em !default;
$sdc-article-quote-citation-text-default: .88889em !default;
$sdc-article-quote-citation-text-medium: 1em !default;
$sdc-article-quote-citation-text-xxlarge: 1.11111em !default;

// Spacing
$sdc-article-quote-margin-top: 32px !default;
$sdc-article-quote-margin-bottom: $sdc-article-quote-margin-top !default;
$sdc-article-quote-margin-left: 0 !default;
$sdc-article-quote-margin-right: 0 !default;

$sdc-article-quote-padding: 64px !default;
$sdc-article-quote-half-width-padding: 48px !default;
$sdc-article-quote-edge-margin: 14px !default;
$sdc-article-quote-max-width: 100% !default;
$sdc-article-quote-text-max-width: 996px !default;

// Breakpoints
$sdc-article-quote-break-small: 481px !default;
$sdc-article-quote-break-medium: 681px !default;
$sdc-article-quote-break-large: 701px !default;
$sdc-article-quote-break-xlarge: 851px !default;
$sdc-article-quote-break-xxlarge: 1020px !default;

// Other options
$simplify-for-amp: false !default;
