// sdc-article-image-grid skin

// Colours
$sdc-article-image-grid-bg: #fff !default;
$sdc-article-image-grid-caption-colour: #4a4a4a !default;

$sdc-article-image-grid-caption-link-colour: #0072c9 !default;
$sdc-article-image-grid-hover-highlight-colour: transparentize($sdc-article-image-grid-caption-link-colour, .2) !default;

$sdc-article-image-grid-background-panel-bg: $sdc-article-image-grid-bg !default;
$sdc-article-image-grid-background-panel-button-colour: #5e5e5e !default;
$sdc-article-image-grid-background-panel-button-hover-colour: #0072c9 !default;

$sdc-article-image-grid-background-panel-shadow: 1px 5px 14px 0 rgba(0, 0, 0, .2) !default;
$sdc-article-image-grid-hover-highlight-width: 7px !default;
$sdc-article-image-grid-caption-max-width: 650px !default;

// Breakpoints
$sdc-article-image-grid-small: 370px !default;
$sdc-article-image-grid-medium: 581px !default;
