/* SKIN */
/* Default styling paramters for this component that can be overwritten at project level */
/* ========= */

// Fonts
$sdc-site-component-header-h1-font-size: 2.3em !default;
$sdc-site-component-header-h2-font-size: 2em !default;
$sdc-site-component-header-h3-font-size: 1.4em !default;
$sdc-site-component-header-h4-font-size: 1.1em !default;
$sdc-site-component-header-h5-font-size: 1em !default;
$sdc-site-component-header-h1-font-size-mobile: 1.65em !default;
$sdc-site-component-header-h2-font-size-mobile: 1.45em !default;
$sdc-site-component-header-h3-font-size-mobile: 1.25em !default;
$sdc-site-component-header-font-weight: 400 !default;
$sdc-site-component-header-line-height: 1.1 !default;
$sdc-site-component-header-h2-line-height: 1.1 !default;
$sdc-site-component-header-h2-text-colour: #fff !default;

// Logo
$sdc-site-component-header-logo-height: 0.8em !default;
$sdc-site-component-header-h1-logo-height: 1em !default;

// Breaking point
$sdc-site-component-header-mobile-bp: "all and (max-width: 600px)" !default;
$sdc-site-component-header-small-mobile-bp: "all and (max-width: 480px)" !default;

// Text align
$sdc-site-component-header-h1-align: auto !default;
$sdc-site-component-header-h2-align: auto !default;
$sdc-site-component-header-h3-align: auto !default;
$sdc-site-component-header-h4-align: auto !default;
$sdc-site-component-header-h5-align: auto !default;

// Margin
$sdc-site-component-header-h1-margin-bottom: 0.75em !default;
$sdc-site-component-header-h2-margin-bottom: 0.75em !default;
$sdc-site-component-header-h3-margin-bottom: 0.75em !default;
$sdc-site-component-header-h4-margin-bottom: 0.75em !default;
$sdc-site-component-header-h5-margin-bottom: 0.75em !default;
$sdc-site-component-header-mobile-h1-margin-bottom: 0.75em !default;
$sdc-site-component-header-mobile-h2-margin-bottom: 0.75em !default;
$sdc-site-component-header-mobile-h3-margin-bottom: 0.75em !default;
$sdc-site-component-header-small-mobile-h1-margin-bottom: 0.75em !default;
$sdc-site-component-header-small-mobile-h2-margin-bottom: 0.75em !default;
$sdc-site-component-header-small-mobile-h3-margin-bottom: 0.75em !default;