// EXTENDS
%sdc-article-body-width-limiter {
  box-sizing: content-box;

  @if $simplify-for-amp == false {
    margin-left: auto;
    margin-right: auto;
    max-width: $sdc-article-body-text-width-default;
    transition: padding $sdc-article-body-responsive-transition;

    @media all and (min-width: $sdc-article-body-break-small) {
      max-width: $sdc-article-body-text-width-small;
    }

    @media all and (min-width: $sdc-article-body-break-medium) {
      max-width: $sdc-article-body-text-width-medium;
    }
  }
}

.sdc-article-body-width-limiter {
  @extend %sdc-article-body-width-limiter;
}

// MIXINS
// Based on https://medium.design/crafting-link-underlines-on-medium-7c03a9274f9#.38xdp2wdn
@mixin magic-underlines($line-colour: rgba(0,0,0,1), $background-colour: rgba(0,0,0,0), $shadow-offset: .05em) {
  @if $simplify-for-amp == false {

    &:link {
      text-decoration: none;
      background-image: linear-gradient(to bottom, $background-colour 50%, $line-colour 50%);
      background-repeat: repeat-x;
      background-size: 1px .125rem;
      background-position: 0 .96em;
      text-shadow: #fff 0 $shadow-offset 0, #fff $shadow-offset $shadow-offset 0, #fff (-$shadow-offset) $shadow-offset 0;

      @media all and (min-width: $sdc-article-body-break-medium) {
        background-position: 0 .99em;
      }
    }

    &:hover {
      background-image: linear-gradient(to bottom, $background-colour 50%, fade-out($line-colour, .5) 50%);
    }

    &:focus {
      text-shadow: none;
      color: #fff;
      background: $line-colour;
    }
  }

  @else {

    &:link {
      text-decoration: underline;
      text-decoration-color: $line-colour;
    }

    &:hover {
      text-decoration-color: fade-out($line-colour, .5);
    }

    &:focus {
      text-decoration: none;
      color: #fff;
      background: $line-colour;
    }
  }
}

// FUNCTIONS
@function baseline-line-height($font-size, $line-height-tweak: 0) {
  // $font-size, in px
  // $line-height-tweak, as multiple of baseline

  $baseline-height: $sdc-article-body-baseline-grid;
  $line-height: (ceil($font-size/$baseline-height) + $line-height-tweak) * .5rem;

  @return $line-height;
}

@function font-size-in-rem($font-size) {
  // $font-size, in px

  $site-base-font-size: $site-base-font-size;
  $font-size-in-rem: $font-size/$site-base-font-size*1rem;

  @return $font-size-in-rem;
}
