.sdc-article-body {

  .sdc-article-body__annotation {
    background: $sdc-article-body-annotation-highlight;
    position: relative;
    padding: 0 .25em;
  }

  .sdc-article-body__annotation:hover {
    border-bottom: 1px solid $sdc-article-body-annotation-highlight-hover;
    background: $sdc-article-body-annotation-highlight;
    cursor: help;
  }

  .sdc-article-body__annotation[data-active] {
    background: $sdc-article-body-annotation-highlight-active;
  }

  .sdc-article-body__annotation-icon {
    right: 0;
    display: inline-block;
    position: relative;
    font-style: italic;
    border-radius: 50%;
    font-size: 16px;
    margin: 0 0 0 .35em;
    width: 16px;
    height: 16px;
    line-height: 1;
    background: $sdc-article-body-annotation-text;
    color: white;
    font-weight: bold;
    display: inline-block !important; // stylelint-disable-line declaration-block-no-duplicate-properties
    display: inline-flex !important; // stylelint-disable-line declaration-block-no-duplicate-properties
    justify-content: center;
    align-items: center;
    vertical-align: -.25em;
    transform: translateY(-40%);
    font-family: Georgia, Times, "Times New Roman", serif;
    border: none;
    cursor: help;
  }

  .sdc-article-body__annotation[data-active] .sdc-article-body__annotation-icon {
    color: $sdc-article-body-annotation-text;
    background: white;
    outline: none;
  }

  // annotation arrow
  .sdc-article-body__annotation-icon:before {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    top: -27px;
    left: calc(50% - 7px);
    background: $sdc-article-body-annotation-box-bg;
    transform: rotate(45deg);
    display: none;
    transition: opacity .166s;
  }

  .sdc-article-body__annotation-icon:after {
    box-sizing: border-box;
    display: none;
    content: attr(data-annotation-title);
    width: 200px;
    max-width: 60vw;
    background: $sdc-article-body-annotation-box-bg;
    position: absolute;
    bottom: 100%;
    left: 50%;
    padding: 1em;
    font-size: 16px;
    line-height: 1.2;
    transform: translate(-50%, -17px);
    font-weight: 400;
    font-style: normal;
    border-radius: 5px;
    font-family: "Sky Text", sans-serif;
  }

  .sdc-article-body__annotation-icon[aria-hidden="false"] {
    filter: drop-shadow(0 0 3px rgba(0, 0, 0, .5));
  }

  .sdc-article-body__annotation-icon[aria-hidden="false"]:before,
  .sdc-article-body__annotation-icon[aria-hidden="false"]:after {
    display: block;
  }

  .sdc-article-body__annotation-icon[data-offset-x="left"]:after {
    right: auto;
    left: 0;
    transform: translate(-10px, -17px);
  }

  .sdc-article-body__annotation-icon[data-offset-x="right"]:after {
    left: auto;
    right: 0;
    transform: translate(10px, -17px);
  }

  .sdc-article-body__annotation-icon[data-offset-y="bottom"]:before {
    top: auto;
    bottom: -27px;
  }

  .sdc-article-body__annotation-icon[data-offset-y="bottom"]:after {
    bottom: auto;
    top: 100%;
    transform: translate(-50%, 17px);
  }

  .sdc-article-body__annotation-icon[data-offset-y="bottom"][data-offset-x="right"]:after {
    transform: translate(10px, 17px);
  }

  .sdc-article-body__annotation-icon[data-offset-y="bottom"][data-offset-x="left"]:after {
    transform: translate(-10px, 17px);
  }
}
