// Colours
$sdc-article-body-text-color: #4a4a4a !default;
$sdc-article-body-heading-text-color: #4a4a4a !default;
$sdc-article-body-link-colour: #0072c9 !default;
$sdc-article-body-mid-text-colour: #666 !default;
$sdc-article-body-figure-text-bg: #e6e6ea !default;
$sdc-article-body-timeline-default-colour: #dcdcdc !default;
$sdc-article-body-timeline-active-colour: #3157a1 !default;
$sdc-article-body-border-colour: #eaeaea !default;

// Font sizes - set in px for readability, converted to rems in SCSS file
$site-base-font-size: 16px !default; // This needs to be the site default font size
$sdc-article-body-baseline-grid: 8px !default;

$sdc-article-body-link-weight: normal !default;

$sdc-article-body-body-text-default: 18px !default;
$sdc-article-body-body-text-medium: 22px !default;

$sdc-article-body-lead-font-weight: bold !default;

$sdc-article-body-h1-text-default: 36px !default;
$sdc-article-body-h1-text-medium: 48px !default;

$sdc-article-body-h2-text-default: 32px !default;
$sdc-article-body-h2-text-medium: 36px !default;

$sdc-article-body-h3-text-default: 28px !default;
$sdc-article-body-h3-text-medium: 32px !default;

$sdc-article-body-h4-text-default: 24px !default;
$sdc-article-body-h4-text-medium: 26px !default;

$sdc-article-body-h5-text-default: 20px !default;
$sdc-article-body-h5-text-medium: 22px !default;

// Annotations
$sdc-article-body-annotation-highlight: #fe9 !default;
$sdc-article-body-annotation-highlight-hover: #e4b046 !default;
$sdc-article-body-annotation-highlight-active: #ffc850 !default;
$sdc-article-body-annotation-text: #4a4a4a !default;
$sdc-article-body-annotation-text-hover: #4a4a4a !default;
$sdc-article-body-annotation-text-active: white !default;

$sdc-article-body-annotation-box-text: pink !default;
$sdc-article-body-annotation-box-bg: white !default;

// Spacing & Sizing
$sdc-article-body-width: 1024px !default;
$sdc-article-body-wrap-class: "" !default;
$sdc-article-body-bottom-margin: 0 !default;

$sdc-article-body-box-sizing: border-box !default;

$sdc-article-body-text-width-default: 390px !default;
$sdc-article-body-text-width-small: 540px !default;
$sdc-article-body-text-width-medium: 700px !default;

$sdc-article-widget-default: $sdc-article-body-text-width-default !default;
$sdc-article-widget-small: $sdc-article-body-text-width-small !default;
$sdc-article-widget-medium: $sdc-article-body-text-width-medium !default;

$sdc-article-body-text-margin-default: 1.5rem !default;
$sdc-article-body-widget-margin-default: $sdc-article-body-text-margin-default !default;
$sdc-article-body-widget-margin-large: 2rem !default;
$sdc-article-body-widget-margin-xlarge: 3rem !default;

$sdc-article-body-sponsor-margin-default: 2rem !default;
$sdc-article-body-sponsor-margin-medium: 3.5rem !default;
$sdc-article-body-sponsor-margin-large: 5rem !default;

$sdc-article-body-edge-margin: 16px !default;

$sdc-article-body-v-margin: 32px !default;
$sdc-article-body-v-margin-medium: 64px !default;
$sdc-article-body-v-margin-large: $sdc-article-body-v-margin-medium !default;

$sdc-article-body-timeline-progress-width-default: 2px !default;
$sdc-article-body-timeline-progress-width-small: 3px !default;
$sdc-article-body-timeline-progress-h-offset-default: 0 !default;
$sdc-article-body-timeline-progress-h-offset-small: -1px !default;
$sdc-article-body-timeline-content-padding-default: $sdc-article-body-edge-margin !default;
$sdc-article-body-timeline-content-padding-small: 32px !default;

$sdc-article-body-bullet-point-width: 4px !default;
$sdc-article-body-bullet-point-height: 4px !default;
$sdc-article-body-bullet-point-padding: 0 0 0 1.5em !default;

// Breakpoints
$sdc-article-body-break-xsmall: 431px !default;
$sdc-article-body-break-small: 481px !default;
$sdc-article-body-break-medium: 681px !default;
$sdc-article-body-break-large: 701px !default;
$sdc-article-body-break-xlarge: 851px !default;
$sdc-article-body-break-xxlarge: 900px !default;

// Transitions
$sdc-article-body-responsive-transition: 0s ease-in-out !default;
$sdc-article-body-hover-transition: .3s ease-in-out !default;
$sdc-article-body-no-transition: 0s ease-in-out !default;

// Other options
$simplify-for-amp: false !default;
