// Article header overrides
.sdc-article-header--feature {

  .sdc-site-localnav {
    background-color: transparent;
  }

  .sdc-site-localnav__sticky-content:not([data-stuck="true"]) {
    background: $sdc-site-localnav-background;
    position: absolute;
    max-width: 100vw;
    width: 100vw;
    left: 50%;
    transform: translateX(-50%);
  }
}