@import "skin";

.sdc-article-segmented-bar-chart {
    line-height: normal;

    &__inner {
        width: 100%;
    }

    &__title {
        width: 100%;
        font-weight: normal;
        font-size: 1.25rem;
        background-color: $sdc-article-segmented-bar-chart-title-background;
        padding: $sdc-article-segmented-bar-chart-padding-unit;
        margin-bottom: 1px;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        background: $sdc-article-segmented-bar-chart-bar-background;
    }

    &__item {
        background-color: $sdc-article-segmented-bar-chart-list-background;
        display: block;
        overflow: visible;
        flex: 0 0 50%;
        padding: $sdc-article-segmented-bar-chart-padding-unit;
        max-width: 50%;

        &--right {
            text-align: right;
            direction: rtl;
        }
    }

    &__value {
        width: 50%;
        flex: 1 1 50%;
        max-width: 50%;
        order: -1;
    }

    &__container {
        width: 200%;
    }

    &__bar {
        padding: $sdc-article-segmented-bar-chart-padding-unit;
        white-space: nowrap;
        font-size: 2em;
    }

    &__text-value {
        position: relative;
        z-index: 10;
    }

    &__source {
        display: block;
        font-size: .875em;
        clear: both;
        color: $sdc-article-segmented-bar-chart-source-colour;
        margin-top: $sdc-article-segmented-bar-chart-padding-unit;
        padding-left: $sdc-article-segmented-bar-chart-padding-unit;
    }
}
