.topic-listing {
  .sdc-site-hero {
    @extend .site-component-vertical-margin-negative !optional;
    margin-bottom: 30px;
  }

  .sdc-site-hero {
    height: 22vh;
    min-height: 260px;
  }

  .sdc-site-hero__body {
    padding: 0;
  }

  .sdc-site-hero__inner {
    justify-content: center;
    align-items: center;
  }

  .sdc-site-hero__snippet {
    max-width: 100%;
  }

  .sdc-site-hero__download-links {
    display: block;
    margin-top: 15px;
  }

  .sdc-site-hero__download-link {
    &--first {
      margin-right: 20px;
    }

    &:hover {
      text-decoration: none;

      strong {
        text-decoration: underline;
      }
    }

    img {
      height: 13px;
      margin-left: 7px;
    }
  }

  @media all and (max-width: 600px) {
    .sdc-site-hero {
      height: 18vh;
      min-height: 200px;
    }

    .sdc-site-hero__title {
      padding-bottom: 9px;
    }

    .sdc-site-hero__snippet {
      display: block;
      font-size: 1em;
    }

    .sdc-site-hero__download-link {
      &--first {
        margin-right: 20px;
      }

      &:after {
        position: relative;
        top: 8px;
      }
    }
  }

  @media all and (max-width: 480px) {
    .sdc-site-hero {
      margin-bottom: 8px;
      margin-top: 0px;
    }

    .sdc-site-hero__snippet {
      display: block;
      font-size: 0.95em;
    }
  }

  &--coronavirus {
    .sdc-site-hero__title {
      font-size: 2.7em;
      text-transform: uppercase;
      font-weight: 700;
    }

    @media all and (max-width: 2560px) {
      .sdc-site-hero--bg-img {
        background-position: center 37%;
        background-size: 2950px 1659px;
      }
    }


    @media all and (max-width: 1200px) {
      .sdc-site-hero__title {
        font-size: 2.5em;
      }

      .sdc-site-hero--bg-img {
        background-size: 2600px 1463px;
      }
    }

    @media all and (max-width: 992px) {
      .sdc-site-hero--bg-img {
        background-size: 2000px 1125px;
      }
    }

    @media all and (max-width: 768px) {
      .sdc-site-hero__title {
        font-size: 2.3em;
      }

      .sdc-site-hero--bg-img {
        background-size: 1700px 956px;
      }
    }

    @media all and (max-width: 600px) {
      .sdc-site-hero__title {
        font-size: 1.8em;
      }

      .sdc-site-hero--bg-img {
        background-size: 1250px 703px;
      }
    }

    @media all and (max-width: 400px) {
      .sdc-site-hero--bg-img {
        background-size: 1000px 562px;
      }
    }
  }
}
