/* Default styling paramters for this component that can be overwritten at project level */

.sdc-site-tiles {
  --sdc-site-tile-transition-time: 0.5s;
  --sdc-site-tile-hover-color: inherit;
  --sdc-site-tile-hover-background: inherit;
  --sdc-site-tile-hover-image-transform: scale(1.1);
}

/* breakpoints */
$sdc-site-tiles-bp-desktop: "all and (min-width: 860px) !default";
$sdc-site-tiles-bp-tablet-desktop: "all and (min-width: 600px) and (max-width: 859px) !default";
$sdc-site-tiles-bp-tablet: "all and (max-width: 599px) !default";
$sdc-site-tiles-bp-mobile: "all and (max-width:480px) !default";