$sdc-site-header-color: var(--text-color);
$sdc-site-header-icon-color: #000;

.sdc-site-header[class] {
  background-image: var(--gradient-linear);
  color: var(--text-color);
}

.sdc-site-header__subnav-item-link {
  color: #000;
}

.sdc-site-header__nav-wrap[class],
.sdc-site-header__menu-group-overflow .sdc-site-header__menu-item-link[class], 
.sdc-site-header__subnav-menu-item-link[class] {
  color: inherit;
}

.sdc-site-header__logo {
  height: 42px;
}

.sdc-site-header__logo {
  transform: rotate(-1.8deg);
  transform-origin: 0;
}

.sdc-site-header__logo img {
  transform: rotate(1.8deg);
  transform-origin: inherit;
}

.sdc-site-header__logo:before {
  content: "";
  position: absolute;
  left: calc(((100vw - 972px) / 2) * -1);
  height: calc(100% + 40px);
  bottom: 48%;
  width: calc(100vw - 15px);
  z-index: -1;
  pointer-events: none;
  background-image: 
  linear-gradient(182deg, rgba(255,255,255,0) 85%, rgba(255,255,255,0.70)),
  linear-gradient(90deg,rgba(255,255,255,0.8) 10%,rgba(255,255,255,0.05))
}

@media all and (max-width: 1024px) {
  .sdc-site-header__logo:before {
    left: calc(var(--component-padding) * -1);
  }
}

.sdc-site-header__menu-item-link:hover,
.sdc-site-header__menu-item-link[aria-current=true] {
  background-image: linear-gradient(0deg, var(--brand-1), var(--brand-1)) !important;
}

