/* Add project one overrides here - use css and css vars, avoid sass */

/* significance tags 
 - increase font size to 14
 - Use sentence case
 - use new colors for breaking and live for sky news
 */

.sdc-article-significance[class] {
  text-transform: capitalize;
  background: var(--brand-3);
  font-size: 14px;
}

.sdc-article-significance--live[class] {
  background: var(--live-bg);
}

.sdc-article-significance--breaking[class] {
  background: var(--breaking-bg);
  color: var(--text-color-dark);
}

.sdc-article-significance--transfer[class] {
  background: var(--text-color-dark);
}

.sdc-article-significance--sponsored[class] {
  background: none;
}

/* component header
  - used as a header for each section on index pages
  - switch to using sky headline font
  - include H2 inside the information-list component
*/
.sdc-site-component-header--project-one[class],
.sdc-site-information-list h2 {
  font-family: var(--branding-sky-headline-font);
  font-size: calc(var(--display-2) + 4px) !important;
  text-transform: uppercase;
  line-height: 1;
}

/* watch live button
  - override --live var with --live-bg gradient
*/
.sdc-site-header__action-button[class] {
  background: var(--live-bg);
  border: 0;
}

/* article topic tags
 - increase font size to 14
 - Use sentence case
 - hide from article header in regular articles - now bottom left
 */
.sdc-article-tags__link {
  font-size: 14px;
  text-transform: capitalize;
}

/* author header
  - override old gradient/glint bg with --brand-3 gradient
*/
.sdc-site-author-header-wrapper {
  @extend .sdc-site-author-header-width-limiter !optional;
}

.sdc-site-author-header-author-introduction__name {
  font-size: var(--display-1);
  font-family: var(--branding-sky-headline-font);
  text-transform: uppercase;
}

/* Breaking news on site tile
  - Adds yellow gradient background when top story is breaking news 
  - inverts breaking news label
*/
.sdc-site-tile--breaking[data-type~=hero-horizontal] {
  background: var(--breaking-bg);
  background-clip: content-box;
}

.sdc-site-tile--breaking[data-type~=hero-horizontal] .sdc-article-significance--breaking[class] {
  background: var(--text-color-dark);
  color: var(--breaking) !important;
}

/* watch-live header
- adds new font and adjusts spacing
*/
.sdc-article-header--project-one {
  font-family: var(--branding-sky-headline-font);
  font-size: calc(var(--display-2) + 4px) !important;
  text-transform: uppercase;
  line-height: 1;
  padding-top: 1.5em !important;
  padding-bottom: 0 !important;
  .sdc-article-header__title {
    padding-bottom: 0.2em !important;
    line-height: 0.9 !important;
  }
  .sdc-article-header__details {
    display: none;
  } 
}

.sdc-article-factbox__title {
  font-family: var(--branding-sky-headline-font) !important;
  font-size: var(--display-3) !important;
  line-height: 1;
  text-transform: uppercase;
}