$sdc-article-related-stories-link-colour: var(--link-color);
$sdc-article-related-stories-title-font-size: 18px;
h4.sdc-article-related-stories__headline {
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: .3px;
}
h3.sdc-article-related-stories__title {
  font-weight: 600;
  line-height: 22px;
  letter-spacing: .3px;
}
.sdc-article-related-stories__content {
  padding-right: 0 !important;
  padding-left: 0 !important;
}
.sdc-article-related-stories__item {
  margin-bottom: 18px !important;
}