// fonts
$sdc-site-topic-banner-font-size-default: 1rem !default;
$sdc-site-topic-banner-font-size-title-default: 1.25rem !default;
$sdc-site-topic-banner-font-size-title-desktop: 1.75rem !default;
$sdc-site-topic-banner-font-size-subtitle-default: 2.25rem !default;
$sdc-site-topic-banner-font-size-subtitle-desktop: 3.25rem !default;

// icon size
$sdc-site-topic-banner-icon-size-default: 68px !default;
$sdc-site-topic-banner-icon-size-desktop: 86px !default;

$sdc-site-topic-banner-icon-size-single-line-default: 50px !default;
$sdc-site-topic-banner-icon-size-single-line-desktop: 65px !default;

// spacings
$sdc-site-topic-banner-top-margin-default: 4px !default;
$sdc-site-topic-banner-top-margin-medium: 20px !default;
$sdc-site-topic-banner-header-padding-default: 6px 10px 0 0 !default;
$sdc-site-topic-banner-header-padding-desktop: 1px 10px 0 0 !default;
$sdc-site-topic-banner-header-padding-left-default: 11px !default;
$sdc-site-topic-banner-header-padding-left-desktop: 19px !default;

$sdc-site-topic-banner-header-margin-bottom-default: 24px !default;
$sdc-site-topic-banner-header-margin-bottom-desktop: 40px !default;

// breakpoints
$sdc-site-topic-banner-break-medium: 600px !default;
