// Colours
$sdc-timeline-node-title-colour: #000 !default;
$sdc-timeline-node-date-colour: #7f7f7f !default;
$sdc-timeline-node-fill: #fff !default;
$sdc-timeline-node-fill-active: #fff !default;
$sdc-timeline-node-outline: #dcdcdc !default;
$sdc-timeline-node-outline-active: #3157a1 !default;

// Spacing
$sdc-timeline-node-margin-top-default: 3rem !default;
$sdc-timeline-node-margin-bottom-default: 2rem !default;
$sdc-timeline-node-margin-bottom-medium: 2rem !default;

// Breakpoints
$sdc-timeline-node-break-x-small: 431px !default;
$sdc-timeline-node-break-small: 481px !default;
$sdc-timeline-node-break-medium: 681px !default;
$sdc-timeline-node-break-large: 701px !default;
$sdc-timeline-node-break-x-large: 851px !default;
$sdc-timeline-node-break-xx-large: 900px !default;
