/* digrev header skin
  =========================================== */

// variables
$blackjack-sdc-digrev-header-zindex: 999999!default;

// extends
.site-header__body,
.site-nav-desktop__wrap {
  @extend .site-wrap!optional;
  @extend .site-wrap-padding!optional
}

// red watch button
a.site-nav-desktop__watch-button {
  background: $pictor;
  border-radius: 0;
}

a.site-nav-desktop__watch-button:after {
  content: none;
}

.site-header a:hover {
  background-color: rgba(0,0,0,0.08);
}

@media #{$bp-xs}{
  .blackjack-sdc-digrev-header {
    margin-bottom: 0
  }
}