/* SITE FOOTER */
/* =================== */

.sdc-news-footer {
  padding: $sdc-news-footer-padding;
  background: $sdc-news-footer-bg;
  color: $sdc-news-footer-color;
  box-shadow: $sdc-news-footer-box-shadow;

  &__wrapper {
    margin: 0 auto;
    padding: 0 $sdc-news-footer-max-width-indent;
    max-width: $sdc-news-footer-max-width;
  }
  /* fix overzealous css clear fix in old v5 parts of sky sports */
  &__cell-wrap:after {
    content: none !important;
  }

  &__row {
    margin-bottom: $sdc-news-footer-row-margin-bottom;
    padding-bottom: $sdc-news-footer-row-padding-bottom;
    border-bottom: $sdc-news-footer-row-outline $sdc-news-footer-keyline;

    &:last-of-type {
      margin: 0;
      padding: 0;
      border: 0;
    }
  }

  &__cell-wrap {
    display: table;
    width: 100%;
    table-layout: fixed;
  }

  &__cell {
    display: table-cell;
    box-sizing: border-box;
    margin: $sdc-news-footer-cell-margin;
    padding-right: $sdc-news-footer-cell-padding-right;
    vertical-align: top;
  }

  &__heading {
    position: relative;
    margin: $sdc-news-footer-heading-margin;
    color: $sdc-news-footer-color-light;
    font-weight: $sdc-news-footer-heading-font-weight;
    font-size: $sdc-news-footer-heading-font-size;
  }

  &__icon-down {
    position: absolute;
    top: $sdc-news-footer-icon-top;
    right: $sdc-news-footer-icon-right;
    display: none;

    & svg {
      width: $sdc-news-footer-icon-width;
      fill: $sdc-news-footer-color-light;
    }
  }

  &__list {
    margin: $sdc-news-footer-list-margin;
    padding: $sdc-news-footer-list-padding;
    list-style: none;
  }

  &__list-item {
    display: block;
    margin-bottom: $sdc-news-footer-list-item-margin-bottom;

    &--social {
      margin: 0;
    }

    &--legal {
      font-size: $sdc-news-footer-list-link-line-font-size;
    }
  }

  &__list--horizontal > &__list-item,
  &__list--social > &__list-item {
    display: inline-block;
    margin-right: $sdc-news-footer-list-item-margin-right;
    vertical-align: middle;
  }

  &__list-link {
    display: block;
    padding: $sdc-news-footer-list-link-padding;
    color: $sdc-news-footer-link-color;
    text-decoration: none;
    line-height: $sdc-news-footer-list-link-line-height;

    &:hover {
      outline: $sdc-news-footer-link-hover-outline $sdc-news-footer-link-hover-background;
      background: $sdc-news-footer-link-hover-background;
      color: $sdc-news-footer-link-color;
    }

    &-icon {
      position: relative;
      top: $sdc-news-footer-link-icon-top;
      display: inline-block;
      margin-right: $sdc-news-footer-link-icon-margin-right;
      width: $sdc-news-footer-link-icon-width;
      height: $sdc-news-footer-link-icon-height;
      vertical-align: middle;

      & > svg {
        position: absolute;
        width: 100%;
        height: 100%;
        fill: $sdc-news-footer-link-icon-color;
      }
    }

    &--facebook:hover svg {
      fill: $sdc-news-footer-facebook;
    }

    &--twitter:hover svg {
      fill: $sdc-news-footer-twitter;
    }

    &--youtube:hover svg {
      fill: $sdc-news-footer-youtube;
    }

    &--instagram:hover svg {
      fill: $sdc-news-footer-instagram;
    }

    &--google:hover svg {
      fill: $sdc-news-footer-google;
    }

    &--email:hover svg {
      fill: $sdc-news-footer-email;
    }
  }

  &__list--social &__list-link:hover {
    outline: 0;
    background: transparent;
  }

  &__list-logo {
    margin: $sdc-news-footer-list-logo-margin;

    &-image {
      margin-right: $sdc-news-footer-list-logo-image-margin-right;
      min-width: $sdc-news-footer-list-logo-image-min-width;
      max-width: $sdc-news-footer-list-logo-image-max-width;
      vertical-align: middle;
    }
  }

  &__list-copyright {
    display: inline-block;
    vertical-align: middle;
  }
}

@media #{$sdc-news-footer-pad-breakingpoint} {

  .sdc-news-footer {

    &__wrapper {
      padding: 0 $sdc-news-footer-max-width-indent-medium;
    }

    &__row--columns {
      padding-bottom: $sdc-news-footer-medium-row-padding-bottom;
    }

    &__cell-wrap {
      display: block;
      font-size: $sdc-news-footer-medium-cell-wrap-font-size;
    }

    &__cell {
      display: inline-block;
      padding-right: $sdc-news-footer-medium-cell-padding-right;
      width: $sdc-news-footer-medium-cell-width;
      font-size: $sdc-news-footer-medium-cell-font-size;
    }
  }
}

@media #{$sdc-news-footer-mobile-breakingpoint} {

  .sdc-news-footer {
    padding: $sdc-news-footer-mobile-padding;

    &__list-link {
      padding: $sdc-news-footer-list-link-padding-mobile;
    }

    &__wrapper {
      padding: $sdc-news-footer-mobile-wrapper-padding;
    }

    &__row {
      margin-bottom: $sdc-news-footer-mobile-row-margin-bottom;
      padding-bottom: $sdc-news-footer-mobile-row-padding-bottom;

      &--columns {
        border-bottom: $sdc-news-footer-mobile-column-border-bottom;
      }
    }

    &__list--social {
      padding: $sdc-news-footer-mobile-list-padding;
    }

    &__cell {
      display: block;
      margin-bottom: $sdc-news-footer-mobile-cell-margin-bottom;
      padding-right: $sdc-news-footer-mobile-cell-padding-right;
      width: 100%;
    }

    &__accordion {
      border-bottom: $sdc-news-footer-keyline-outline $sdc-news-footer-keyline;

      .js & {
        border-bottom: 0;

        .sdc-news-footer__list {
          overflow: hidden;
          margin: $sdc-news-footer-mobile-keyline-margin;
          max-height: 0;
          transition: max-height .3s ease-in-out;

          &-item:first-child {
            margin-top: $sdc-news-footer-keyline-item-margin-top;
          }

          &-item:last-child {
            margin-bottom: $sdc-news-footer-keyline-item-margin-bottom;
          }
        }

        &--active {
          border-bottom: $sdc-news-footer-keyline-outline $sdc-news-footer-keyline;

          .sdc-news-footer__list {
            max-height: $sdc-news-footer-keyline-list-max-height;
          }

          .sdc-news-footer__icon-down {
            transform: rotate(180deg);
          }
        }
      }
    }

    &__heading {
      margin: 0;
      padding: $sdc-news-footer-mobile-heading-padding;
      border-bottom: $sdc-news-footer-keyline-outline $sdc-news-footer-keyline;
      font-size: $sdc-news-footer-mobile-heading-font-size;

      &:hover {
        background: $sdc-news-footer-link-hover-background;
        cursor: pointer;
      }
    }

    &__list {
      margin: $sdc-news-footer-mobile-list-margin;
    }

    &__list-item {
      margin: $sdc-news-footer-mobile-list-item-margin;
    }

    &__list-link-icon {
      width: $sdc-news-footer-mobile-icon-width;
      height: $sdc-news-footer-mobile-icon-height;
    }

    .js &__icon-down {
      display: block;
      transition: transform .3s ease-in-out;
    }

    &__list--horizontal:not(&__row--social) > &__list-item {
      display: block;
      margin-right: 0;
    }

    &__list-logo {
      margin: $sdc-news-footer-mobile-icon-logo-margin;
    }

    &__list--social &__list-item {
      margin-right: $sdc-news-footer-mobile-icon-margin-right;
    }

    &__list--social &__list-link {
      overflow: hidden;
      max-width: $sdc-news-footer-mobile-icon-max-width;
      white-space: nowrap;
    }

    &__list--legal {
      margin: $sdc-news-footer-mobile-legal-margin;
    }
  }
}