// Margin
$sdc-article-date-margin-bottom-desktop: 3em !default;
$sdc-article-date-margin-bottom-mobile: 1em !default;
$sdc-article-date-margin-top-desktop: 0 !default;
$sdc-article-date-margin-top-mobile: 0 !default;
$sdc-article-date-amp-margin-bottom: 1em !default;
$sdc-article-date-date-time-amp-margin: 0 !default;
$sdc-article-date-date-time-margin-right: .3em !default;
$sdc-article-date-time-icon-margin-right: 6px !default;

// Padding
$sdc-article-date-padding: 0 0 40px !default;
$sdc-article-date-margin: 0 auto !default;
$sdc-article-date-padding-top-pad: 0 !default;
$sdc-article-date-padding-top-mobile: 0 !default;
$sdc-article-date-padding-bottom-mobile: 28px !default;
$sdc-article-date-padding-bottom-pad: 40px !default;
$sdc-article-date-time-icon-padding-top: 0 !default;

// Breaking point
$sdc-article-date-priority-3-breakingpoint: unquote("max-width: 768px") !default;
$sdc-article-date-priority-4-breakingpoint: unquote("max-width: 480px") !default;

// Text
$sdc-article-date-date-time-font-size: 16px !default;
$sdc-article-date-date-time-text-colour: #9f9f9f !default;
$sdc-article-date-date-time-font-size-pad: 16px !default;
$sdc-article-date-date-time-line-height: 1.3 !default;
$sdc-article-date-date-time-width: 11px !default;
$sdc-article-date-date-time-height: 11px !default;
$sdc-article-date-date-time-vertical-align: -1px !default;

// Icon
$sdc-article-date-icon-width: 16px !default;
$sdc-article-date-icon-height: 16px !default;
