// TYPOGRAPHY: Lists
.sdc-article-body {

  > ul,
  > ol,
  > dl {
    @extend #{$sdc-article-body-wrap-class} !optional;
    @extend %sdc-article-body-width-limiter !optional;
    @extend %vertical-spacing !optional;
    font-size: 1em;
    font-weight: 400;
    list-style: none;

    & > li,
    & > dd {
      @extend %link-behaviour !optional;
      margin: 0 0 .5rem 0;
      position: relative;
      padding: $sdc-article-body-bullet-point-padding;
      color: $sdc-article-body-text-color;

      &:before {
        display: block;
        position: absolute;
        left: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  > ol {
    counter-reset: my-counter;

    & > li:before {
      content: counter(my-counter);
      counter-increment: my-counter;
      color: $sdc-article-body-mid-text-colour;
    }
  }

  > ul {

    & > li:before {
      content: "";
      width: $sdc-article-body-bullet-point-width;
      height: $sdc-article-body-bullet-point-height;
      background: $sdc-article-body-mid-text-colour;
      border-radius: 50%;
      top: .7em;
    }
  }

  > dl {

    dt {
      font-weight: 600;

      @extend %link-behaviour !optional;
    }
  }
}
