.fyre-liveblog {

  .fyre-comment-wrapper {
    float: left;
    clear: both;
    width: 100%;
    padding: $block-padding-s 0 0 0 !important;
  }

  // Use the head as the first column
  .fyre-comment-head {
    float: left;
    width: 20%;
    margin: 0 !important;
    padding-right: 18px;
  }

  // Use the head as the second column
  .fyre-comment-body {
    font-size: var(--base-font-size);
    float: left;
    width: 80%;
    margin: 0 !important;
    padding: 0 0 0 1px;
    color: $text-primary;
  }

  // Clear the head and content columns and span the footer full width
  .fyre-comment-footer {
    float: left; // IE8
    clear: both;
    width: 100%; // IE8
    height: 24px !important;
    margin: 0 !important;
  }

  // Required to display the divider
  .fyre-comment-divider {
    clear: both;
  }

  // Hide stuff
  .fyre-comment-user,
  .fyre-comment-username,
  .fyre-comment-user-rating,
  .fyre-comment-author-tag,
  .fyre-comment-tag,
  .fyre-comment-action-button,
  .fyre-comment-like-count,
  .fyre-comment-head .fyre-comment-title,
  .fyre-comment p > br:first-child,
  .fyre-comment-from-text {
    display: none !important;
  }

  // Hide embedlyable links
  .fyre-comment a[rel="nofollow"][href*="twitter.com"][href*="status"],
  .fyre-comment a[rel="nofollow"][href*="vine.co"],
  .fyre-comment a[rel="nofollow"][href*="vimeo.com"],
  .fyre-comment a[rel="nofollow"][href*="soundcloud.com"],
  .fyre-comment a[rel="nofollow"][href*="audioboo.fm"],
  .fyre-comment a[rel="nofollow"][href*="facebook.com"][href*="posts"],
  .fyre-comment a[rel="nofollow"][href*="youtube.com"],
  .fyre-comment a[rel="nofollow"][href*="audioboom.com"] {
    display: none;
  }

  // fade comments in rather than animate their height
  // this saves on multiple full page reflows
  .article.fyre-comment-article {
    display: block !important;
    height: auto !important;
    transition: opacity 1s ease-on-out;
  }

  .article.fyre-comment-article[style^="display: none"] {
    opacity: 0;
  }

  // LIVEFYRE DATE
  .fyre-comment-date {
    float: none !important;
    height: auto !important;
    text-align: center;
    line-height: 22px !important;
    background-image: none !important;
    padding: 0 !important;
    color: $dark80 !important;
  }

  // LIVEFYRE TYPOGRAPHY
  .fyre-comment-wrapper a,
  .fyre-comment-wrapper a:hover {
    color: $link-text !important;
  }

  // link bolding
  .fyre-comment strong a,
  .fyre-comment a strong {
    font-weight: bold;
  }

  .fyre-comment-wrapper p {
    line-height: $text-line-height;
    margin-bottom: 1.1em !important;
  }

  .fyre-comment-wrapper .fyre-comment ul,
  .fyre-comment-wrapper .fyre-comment ol {
    margin: 0 0 1em 0 !important;
  }

  .fyre-comment-wrapper li {
    list-style-position: outside !important;
    margin: 0 0 .5em 1em !important;
    line-height: $text-line-height !important;
  }

  .fyre-comment-wrapper strong {
    font-size: var(--base-font-size);
    color: $text-primary;
    font-weight: bold;
  }

  .fyre-comment-wrapper p:last-child,
  .fyre-comment-wrapper .fyre-comment ul:last-child,
  .fyre-comment-wrapper .fyre-comment ol:last-child {
    margin-bottom: 0 !important;
  }

  // LIVEFYRE REPLIES
  .fyre-liveblog-expanded > .fyre-comment-wrapper .fyre-comment-reply {
    margin-right: 0 !important;
  }

  .fyre-liveblog-expanded > .fyre-comment-wrapper .fyre-comment-footer .fyre-comment-actions {
    margin: 0 !important;
  }

  .fyre-liveblog-expanded .fyre-comment-footer,
  .fyre-liveblog-expanded.fyre-comment-source-13 .fyre-comment-footer {
    margin: 0 !important;
  }

  .fyre-liveblog .fyre-liveblog-expanded .fyre-comment-replies {
    margin-top: -152px !important;
  }

  .fyre-comment-wrapper > .fyre-comment-footer > .fyre-editor {
    margin-top: $block-margin-s !important;
  }

  // LIVEFYRE MEDIA
  .fyre-comment-media {
    margin-top: 1.5em;
  }

  .fyre-comment-raw-html {
    text-align: left;
  }

  .fyre-comment-media img {
    height: auto;
  }

  .fyre-comment-media .fyre-comment-media-play {
    background-image: url("data:image/svg+xml;base64,");
    height: 100px;
    margin: -50px 0 0 -50px;
    width: 100px;
    background-position: 0 0;
    background-size: 100px;
  }

  // LIVEFYRE QUOTES
  .fyre-comment-media blockquote {
    font-size: var(--base-font-size);
    line-height: $text-line-height;
    margin-bottom: 1.25em;
    padding: 0 1em;
  }

  .fyre-comment-media blockquote:last-child {
    margin-bottom: 0;
  }

  // LIVEFYRE THUMBS
  .fyre-comment-media-thumbnails {
    margin-top: 5px !important;
    width: 100%;
    float: left;
  }

  .fyre-comment-media-thumbnails img.fyre-img-thumb,
  .fyre-comment-media-thumbnails .fyre-video-thumbnail {
    max-height: none !important;
  }

  .fyre-comment-media-thumbnails > div,
  .fyre-comment-media-thumbnails .fyre-img-thumb {
    display: inline-block;
    width: 24.25% !important;
    margin-right: 1% !important;
    margin-bottom: 1%;
    border-radius: 0 !important;
  }

  .fyre-comment-media-thumbnails > div {
    height: 0;
    // 16:9 ratio of 24.25%
    padding-bottom: 13.7%;
  }

  .fyre-comment-media-thumbnails .fyre-video-thumbnail {
    width: 100% !important;
    height: 100% !important;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 0;
  }

  .fyre-comment-media-thumbnails .fyre-video-play {
    height: 100%;
    left: 0;
    top: 0;
    background-size: contain;
    background-position: center center;
  }

  .fyre-comment-media-thumbnails > *:nth-child(4n+4) {
    margin-right: 0 !important;
  }

  // LIVEFYRE TAGS
  // Colors and styles not in the individual icon files
  .fyre-tag-content-icon-goal .fyre-comment-body,
  .fyre-tag-content-icon-wicket .fyre-comment-body,
  .fyre-tag-content-icon-game-set-match .fyre-comment-body {
    background: $dark90;
    padding: 24px;
    color: #fff !important;

    strong {
      color: #fff;
      font-weight: bold;
    }
  }

  .fyre-tag-content-icon-bn .fyre-comment-body {
    font-size: var(--base-font-size);
    color: $text-primary;
    background: $yellow1;
    padding: 24px;
  }

  .fyre-tag-content-icon-bn .fyre-comment-body p {
    line-height: $text-line-height;
  }

  .fyre-tag-content-icon-bn .fyre-comment-head:after {
    display: none;
  }

  .fyre-tag-content-icon-betting .fyre-comment-body {
    background: #284f9a;
    padding: 24px;
    color: #fff !important;

    strong {
      color: #fff;
      font-weight: bold;
    }
  }

  .fyre-tag-content-icon-red-card .fyre-comment-body {
    background: $light80;
    padding: 24px;
  }

  .fyre-tag-content-icon-bn .fyre-comment-body a,
  .fyre-tag-content-icon-bn .fyre-comment-body a:hover,
  .fyre-tag-content-icon-betting .fyre-comment-body a,
  .fyre-tag-content-icon-betting .fyre-comment-body a:hover {
    color: inherit !important;
    text-decoration: underline !important;
  }
}

// fyre-static classes for static HTML rendered content on page load
.fyre-static {
  display: block;

  .fyre-comment-date {
    display: block;
  }

  .fyre-comment-divider {
    background: #eee;
    height: 1px;
    margin: -2px 0 10px 0;
    position: relative;
  }

  .fyre-comment-media {
    overflow-x: auto;
  }

  .fyre-comment-media * {
    max-width: 100%;
  }

  .fyre-comment-media img {
    max-height: 480px;
  }
}

// Scope everything to fyre-liveblog for specificity reasons...
.fyre .fyre-comment-wrapper .fyre-comment-head time.fyre-comment-date {
  font-size: var(--display-4);
}

// FIREFOX 34 IMAGE BUG (SSFOOTFEED-3956)
@-moz-document url-prefix("") {

  .fyre .fyre-comment-media .fyre-comment-media-expanded img {
    width: 100%;
    max-height: none !important;
  }
}

// Livefyre Icons
// Icons are inserted into the dom with a class of livefyre-blog__icon
// nodeInserted is used to detect news/updated posts to create icons for
// see README.md for more info
@keyframes nodeInserted {
  from { opacity: .99; }
  to { opacity: 1; }
}

.fyre-comment-wrapper {
  animation-duration: .001s;
  animation-name: nodeInserted;
}

.livefyre-blog__icon {
  width: 48px;
  height: 48px;
  margin: 12px auto 0;
  display: block;
}

.fyre-tag-content-icon-twitter .livefyre-blog__icon {
  color: #4099ff;
}

// MEDIA HOLDER STYLES
.media-holder {
  position: relative;
}

.media-holder--video {
  width: 100%;
  padding-bottom: 56%;
}

.media-holder > iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.fyre-edit-profile-link {
  display: none !important;
}

// Make embeds fit the screen
// Prevents things getting cut off
.embedly-embed {
  max-width: 100% !important;
}

@media all and ($bp30-max) {

  .livefyre-blog__icon {
    font-size: 32px;
    line-height: 32px;
    position: absolute;
    top: -2px;
    right: 0;
    width: 32px;
    height: 32px;
    margin: 0;
  }

  .fyre-liveblog {

    // Add a little padding around the stream on mobile
    .fyre-comment-stream {
      padding: 0 $site-indent-s;
    }

    // Remove column layout at mobile view
    // Make head span full width
    // Set line-height to equal the icon height
    .fyre-comment-head {
      line-height: 32px;
      position: relative;
      float: none;
      width: 100%;
      padding: 0 !important;
    }

    .fyre-comment-body {
      font-size: var(--supporting-text) !important;
      float: none;
      width: 100%;
    }

    .fyre-comment-wrapper strong {
      font-size: var(--supporting-text);
      font-weight: bold;
    }

    .fyre-comment-date {
      font-size: var(--display-5) !important;
      margin-bottom: $block-margin-s;
      text-align: left;
      line-height: inherit !important;
    }

    .fyre-comment-media .fyre-comment-media-play {
      height: 60px;
      margin: -30px 0 0 -30px;
      width: 60px;
      background-size: 60px;
    }
  }
}
