.sdc-article-header {
  font-size: $sdc-article-header-base-font-size;
  color: $sdc-article-header-text-colour;

  &__titles {
    @media all and ($sdc-article-header-priority-1-breakpoint) {
      font-size: .9em;
    }

    @media all and ($sdc-article-header-priority-2-breakpoint) {
      font-size: .85em;
    }

    @media all and ($sdc-article-header-priority-3-breakpoint) {
      font-size: .8em;
    }

    @media all and ($sdc-article-header-priority-4-breakpoint) {
      font-size: .75em;
    }
  }

  &__title {
    font-size: $sdc-article-header-title-size;
    font-weight: $sdc-article-header-title-font-weight;
    line-height: $sdc-article-header-title-line-height;
  }

  &__sub-title {
    font-size: $sdc-article-header-sub-title-size;
    font-weight: normal;
    line-height: 1.35;
  }

  &__date-time {
    font-size: $sdc-article-header-date-time-size;
    color: $sdc-article-header-light-text-colour;

    @media all and ($sdc-article-header-priority-3-breakpoint) {
      font-size: .8888em;
    }
  }

  &__author-name {
    font-size: $sdc-article-header-author-name-size;
    font-weight: 400;

    .sdc-article-header__titles & {
      font-size: 1em;
      font-weight: 600;
    }

    @media all and ($sdc-article-header-priority-3-breakpoint) {
      font-size: 1em;
    }
  }
}
