.sdc-site-charts[data-type="pie"] .sdc-site-charts__chart-wrap {
  padding-bottom: 75%;

  .sdc-site-charts__chart-point-label-text {
    font-size: 0.005em;
    text-shadow: 0 5em 50em rgba(0, 0, 0, 0.25);
  }
}

@media all and (min-width: 600px) {

  .sdc-site-charts[data-type="pie"] .sdc-site-charts__chart-wrap {
    padding-bottom: 45%;
  }
}
