.sdc-site-header--dark-mode {
  background: $sdc-site-header-dark-mode-background;
  color: $sdc-site-header-dark-mode-color;
  // action button - usually the red watch video button that appears right of main nav
  .sdc-site-header__action-button {
    background: $sdc-site-header-dark-mode-action-button-background;
  }

  // sub nav - login, more, search etc
  .sdc-site-header__subnav-menu,
  .sdc-site-header__menu-group-overflow {
    background: $sdc-site-header-dark-mode-subnav-menu-background;
    border-top: $sdc-site-header-dark-mode-subnav-menu-border;
    border-bottom: $sdc-site-header-dark-mode-subnav-menu-border;
  }

  .sdc-site-header__subnav-menu:after {
    border: $sdc-site-header-dark-mode-subnav-menu-border;
    background: $sdc-site-header-dark-mode-subnav-menu-background;
  }

  .sdc-site-header__subnav-menu-item-link,
  .sdc-site-header__menu-group-overflow .sdc-site-header__menu-item-link {
    color: $sdc-site-header-dark-mode-subnav-menu-color;
    border-top: none;
  }

  .sdc-site-header__menu-group-overflow .sdc-site-header__menu-item-link {
    border-bottom: $sdc-site-header-dark-mode-subnav-menu-item-border;
  }

  // icons
  .sdc-site-header__icon {
    fill: $sdc-site-header-dark-mode-icon-color;
  }

  .sdc-site-header__subnav-item--login-nav {
    border-right: $sdc-site-header-dark-mode-nav-subnav-link-border;
  }

  .sdc-site-header__subnav-item--login-nav .sdc-site-header__subnav-item-link[aria-expanded="true"]:after {
    border: $sdc-site-header-dark-mode-subnav-menu-border;
    background: $sdc-site-header-dark-mode-subnav-menu-background;
  }

  @media #{$sdc-site-header-desktop-bp} {

    .sdc-site-header__menu-item-link-icon {
      fill: $sdc-site-header-dark-mode-color;
    }

    .sdc-site-header__menu-group-overflow[class] {
      border: $sdc-site-header-dark-mode-subnav-menu-item-border;
      border-top: 0;
    }

    .sdc-site-header__subnav-menu,
    .sdc-site-header__menu-group-overflow {
      border: $sdc-site-header-dark-mode-subnav-menu-border;
      border-top: 0;
    }
  }

  @media #{$sdc-site-header-mobile-bp} {

    .sdc-site-header__nav-wrap {
      background: $sdc-site-header-dark-mode-nav-mobile-background;
      color: $sdc-site-header-dark-mode-nav-mobile-color;
    }

    .sdc-site-header__menu-item-link {
      border-bottom: $sdc-site-header-dark-mode-nav-mobile-link-border;
    }

    .sdc-site-header__subnav-item--mobile-nav > .sdc-site-header__subnav-item-link[aria-expanded="true"] {
      color: $sdc-site-header-dark-mode-color;
      fill: $sdc-site-header-dark-mode-color;
    }
  }
}
