// ARTICLE HEADER SKIN
// ===================

// Width
$sdc-article-header-inner-width: 748px !default;

// Breakpoints
$sdc-article-header-priority-1-breakpoint: unquote("max-width: 940px") !default;
$sdc-article-header-priority-2-breakpoint: unquote("max-width: 820px") !default;
$sdc-article-header-priority-3-breakpoint: unquote("max-width: 768px") !default;
$sdc-article-header-priority-4-breakpoint: unquote("max-width: 480px") !default;

// Colours
$sdc-article-header-text-colour: #4a4a4a !default;
$sdc-article-header-sub-title-colour: #707070 !default;
$sdc-article-header-light-text-colour: #9f9f9f !default;

// Fonts
$sdc-article-header-base-font-size: inherit !default;

$sdc-article-header-title-size: 2.675em !default;
$sdc-article-header-title-line-height: 1.15em !default;
$sdc-article-header-sub-title-size: calc(0.88889em + 6px) !default;
$sdc-article-header-date-time-size: 1em !default;
$sdc-article-header-author-name-size: 1.1111em !default;
$sdc-article-header-title-font-weight: normal !default;

// Sizes
$sdc-article-header-wrap-class: greetings-to-world !default;

// Feature
$sdc-article-header-background-colour: #222 !default;
$sdc-article-header-feature-height: 80vh !default;
$sdc-article-header-body-background: linear-gradient(to top, rgba(0, 0, 0, .8), rgba(0, 0, 0, 0)) !default;
$sdc-article-header-feature-text-colour: #fff;

// With Margin
$sdc-article-header-margin-bottom-desktop: 3em !default;
$sdc-article-header-margin-bottom-mobile: 2em !default;
$sdc-article-header-wrap-margin-top: 1.5em !default;

