// TYPOGRAPHY: Headings
.sdc-article-body {
  // Heading defaults
  %heading {
    @extend #{$sdc-article-body-wrap-class} !optional;
    @extend %sdc-article-body-width-limiter !optional;
    @extend %link-behaviour !optional;
    color: $sdc-article-body-heading-text-color;
    line-height: 1.1;
    margin-top: 3rem;
    margin-bottom: 1rem;

    &:first-child {
      margin-top: 0;
    }

    em, b, i, strong {
      font-weight: inherit;
      font-style: normal;
    }
  }

  > h1 {
    @extend %heading !optional;
    font-size: font-size-in-rem($sdc-article-body-h1-text-default);
    line-height: 1.11;
    font-weight: normal;

    @media all and (min-width: $sdc-article-body-break-medium) {
      font-size: font-size-in-rem($sdc-article-body-h1-text-medium);
      line-height: 1;
    }
  }

  > h2 {
    @extend %heading !optional;
    font-size: font-size-in-rem($sdc-article-body-h2-text-default);
    line-height: 1.13;
    font-weight: normal;

    @media all and (min-width: $sdc-article-body-break-medium) {
      font-size: font-size-in-rem($sdc-article-body-h2-text-medium);
      line-height: 1.11;
    }
  }

  > h3 {
    @extend %heading !optional;
    font-size: font-size-in-rem($sdc-article-body-h3-text-default);
    line-height: 1.14;
    font-weight: bold;

    @media all and (min-width: $sdc-article-body-break-medium) {
      font-size: font-size-in-rem($sdc-article-body-h3-text-medium);
      line-height: 1.13;
    }
  }

  > h4 {
    @extend %heading !optional;
    font-size: font-size-in-rem($sdc-article-body-h4-text-default);
    line-height: 1.25;
    font-weight: bold;

    @media all and (min-width: $sdc-article-body-break-medium) {
      font-size: font-size-in-rem($sdc-article-body-h4-text-medium);
      line-height: 1.15;
    }
  }

  > h5,
  > h6 {
    @extend %heading !optional;
    font-size: font-size-in-rem($sdc-article-body-h5-text-default);
    line-height: 1.3;

    @media all and (min-width: $sdc-article-body-break-medium) {
      font-size: font-size-in-rem($sdc-article-body-h5-text-medium);
      line-height: 1.18;
    }
  }

  > h5 {
    font-weight: bold;
  }

  > h6 {
    font-weight: normal;
  }
}
