// header body
$sdc-site-header-background: #777 !default;
$sdc-site-header-color: white !default;
$sdc-site-header-z-index: 99 !default;
$sdc-site-header-width: auto !default;
$sdc-site-header-top-padding-desktop: 24px !default;
$sdc-site-header-top-padding-mobile: 13px !default;

// logo
$sdc-site-header-logo-height: 38px !default;

// icons
$sdc-site-header-icon-color: white !default;

// main nav
$sdc-site-header-menu-item-link-indent: .6em !default;
$sdc-site-header-menu-height: 65px !default;

// subnav
$sdc-site-header-subnav-menu-color: #444 !default;
$sdc-site-header-subnav-menu-background: white !default;
$sdc-site-header-subnav-menu-border: 2px solid #ccc !default;
$sdc-site-header-subnav-menu-item-border: 1px solid #ccc !default;
$sdc-site-header-subnav-menu-z-index: 99 !default;

// mobile nav
$sdc-site-header-nav-mobile-background: white !default;
$sdc-site-header-nav-mobile-color: #444 !default;
$sdc-site-header-nav-mobile-link-border: 1px solid #ccc !default;
$sdc-site-header-nav-mobile-z-index: 100 !default;
$sdc-site-header-nav-subnav-link-border: 1px solid rgba(255, 255, 255, .5) !default;

// action button
$sdc-site-header-action-button-background: red !default;
$sdc-site-header-action-button-color: white !default;
$sdc-site-header-action-button-border-radius: 3px !default;

/* breakpoints */
$sdc-site-header-mobile-bp: "all and (max-width: 579px)" !default;
$sdc-site-header-desktop-bp: "all and (min-width: 580px)" !default;
$sdc-site-header-desktop-wide-bp: "all and (min-width: 800px)" !default;
$sdc-site-header-small-mobile-bp: "all and (max-width: 420px)" !default;

// ---------------
// dark-mode skin:
// ---------------

// dark-mode header body
$sdc-site-header-dark-mode-background: #191919 !default;
$sdc-site-header-dark-mode-color: white !default;

// dark-mode icons
$sdc-site-header-dark-mode-icon-color: #d0021b !default;

// dark-mode subnav
$sdc-site-header-dark-mode-subnav-menu-color: $sdc-site-header-dark-mode-color !default;
$sdc-site-header-dark-mode-subnav-menu-background: $sdc-site-header-dark-mode-background !default;
$sdc-site-header-dark-mode-subnav-menu-border: 2px solid #222 !default;
$sdc-site-header-dark-mode-subnav-menu-item-border: 1px solid #222 !default;

// dark-mode mobile nav
$sdc-site-header-dark-mode-nav-mobile-background: $sdc-site-header-dark-mode-background !default;
$sdc-site-header-dark-mode-nav-mobile-color: $sdc-site-header-dark-mode-color !default;
$sdc-site-header-dark-mode-nav-mobile-link-border: 1px solid #222 !default;
$sdc-site-header-dark-mode-nav-subnav-link-border: 1px solid rgba(255, 255, 255, .5) !default;

// dark-mode action button
$sdc-site-header-dark-mode-action-button-background: #d0021b !default;
