// Timeline progress bar:
.sdc-article-body--timeline {
  position: relative;
  padding-top: 1em;
  z-index: 0;
}

.sdc-article-body__progress-bar {
  @extend %sdc-article-body-width-limiter;
  @extend #{$sdc-article-body-wrap-class} !optional;

  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: -1;
  height: 100%;
  width: 100%;

  & + h1,
  & + h2 {
    margin-top: 0;
  }

  &::before {
    content: "";
    height: 100%;
    position: absolute;
    left: 8px;
    top: 0;
    width: 1px;
    background-attachment: fixed;
    background-image: linear-gradient(to bottom, $sdc-article-body-timeline-active-colour 0%, $sdc-article-body-timeline-active-colour 50%, $sdc-article-body-timeline-default-colour 50%, $sdc-article-body-timeline-default-colour 100%);
    background-color: none;

    @media all and (min-width: $sdc-article-body-break-xsmall) {
      left: 6px;
    }

    @media all and (min-width: $sdc-article-body-break-small) {
      left: 15px;
    }

    @media all and (min-width: $sdc-article-body-break-xxlarge) {
      left: -6px;
    }
  }

  &::after {
    content: "";
    box-sizing: border-box;
    display: block;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: $sdc-article-body-timeline-default-colour;
    border: 1px solid $sdc-article-body-timeline-default-colour;
    position: absolute;
    top: 0;
    transform: translateZ(0);
    left: 5px;

    @media all and (min-width: $sdc-article-body-break-xsmall) {
      left: 3px;
    }

    @media all and (min-width: $sdc-article-body-break-small) {
      left: 11px;
      width: 9px;
      height: 9px;
    }

    @media all and (min-width: $sdc-article-body-break-medium) {
      background: $sdc-article-body-timeline-active-colour;
      border: 1px solid $sdc-article-body-timeline-active-colour;
    }

    @media all and (min-width: $sdc-article-body-break-xxlarge) {
      left: -10px;
    }
  }
}
