// site sponsor overrides
.sdc-article-body {
  // Reduce the top margin by 50% when sponsor is the first item in the article body
  .ad + .sdc-site-sponsor,
  .sdc-site-sponsor:first-child {
    margin-top: .5*$sdc-article-body-widget-margin-default;

    @if $simplify-for-amp == false {
      @media all and (min-width: $sdc-article-body-break-large) {
        margin-top: .5*$sdc-article-body-widget-margin-large;
      }

      @media all and (min-width: $sdc-article-body-break-xlarge) {
        margin-top: .5*$sdc-article-body-widget-margin-xlarge;
      }
    }
  }

  // Remove the bottom margin when sponsor is the last item in the article body
  .sdc-site-sponsor[class]:last-child {
    margin-bottom: -$sdc-article-body-v-margin;

    @media all and (min-width: $sdc-article-body-break-small) {
      margin-bottom: -$sdc-article-body-v-margin-medium;
    }

    @media all and (min-width: $sdc-article-body-break-medium) {
      margin-bottom: -$sdc-article-body-v-margin-large;
    }
  }
}
