
.sdc-article-body {
  max-width: $sdc-article-body-width;
  margin-left: auto;
  margin-right: auto;
  padding-right: $sdc-article-body-edge-margin;
  padding-bottom: $sdc-article-body-v-margin;
  padding-left: $sdc-article-body-edge-margin;

  @media all and (min-width: $sdc-article-body-break-small) {
    padding-bottom: $sdc-article-body-v-margin-medium;
  }

  @media all and (min-width: $sdc-article-body-break-medium) {
    padding-bottom: $sdc-article-body-v-margin-large;
  }

  // Clearfix:
  &:after {
    content: "";
    display: block;
    clear: both;
    width: 100%;
    height: 0;
  }
}
