@mixin mobile-single-item-view {

  @include hide-when-no-js;

  &:not(.sdc-site-localnav--mobile) .sdc-site-localnav__items {
    width: 100%;
    position: relative;
  }

  .sdc-site-localnav__item {
    width: 100%;

    &--more {
      width: auto;
    }
  }

  .sdc-site-localnav__current-section-name {
    display: table-cell;
    font-weight: bold;
  }

  .sdc-site-localnav__item-link[aria-current="true"]:before {
    display: none;
  }
}

@mixin mobile-single-item-view-desktop-tweaks {

  @include hide-when-no-js;

  .sdc-site-localnav__item-link {
    transition: text-shadow $sdc-site-localnav-transition;

    &[aria-current="true"]:before {
      transition: height $sdc-site-localnav-transition;
    }
  }
}

// Only show nav if JS is enabled
@mixin hide-when-no-js {

  & {
    display: none;
  }

  .js & {
    display: block;
  }
}
