.sdc-site-load-more {
  margin-bottom: $sdc-site-load-more-margin-bottom;

  & [data-role="load-more-item"][aria-hidden] {
    display: none !important;
  }

  &__button {
    position: relative;
    font-family: inherit;
    display: block !important;
    padding: 1em;
    margin: 10px auto;
    background: transparent;
    border: $sdc-site-load-more-border-width solid $sdc-site-load-more-button-color;
    border-radius: $sdc-site-load-more-border-radius;
    color: $sdc-site-load-more-button-color;
    cursor: pointer;

    &:hover {
      background: $sdc-site-load-more-button-color;
      color: $sdc-site-load-more-button-color-hover;
    }

    &--is-hidden {
      display: none;
    }

    &[aria-hidden] {
      display: none !important;
    }

    &.loading, &.loading[class]:hover, &.loading[class]:focus {
      border: 2px solid transparent;
      background: transparent;
      color: transparent;
      cursor: default;
      outline: none;
    }

    &.loading:after {
      @extend %spinner;
    }
  }
}
