// SHARING COMPONENT
// ===================

.sdc-site-share {
  margin-top: $sdc-site-share-top-margin;
  margin-right: $sdc-site-share-right-margin;
  margin-bottom: $sdc-site-share-bottom-margin;
  margin-left: $sdc-site-share-left-margin;

  &__title {
    margin-bottom: 1em;
  }

  &__snippet {
    margin: .5em 0 1.5em;
    line-height: 1.35em;
    max-width: 60%;
    display: inline-block;
  }

  &__list {
    margin: 0;
    padding: 0;
  }

  &__item {
    display: inline;
  }

  &__link {
    padding: 8px 9px 6px 9px;
    display: inline-block;
    border: 1px solid $sdc-site-share-border-color;
    vertical-align: middle;
    margin-right: 7px;
    background-color: $sdc-site-share-button-bg;
    border-radius: $sdc-site-share-border-radius;
    transition: background-color $hover, border-color $hover;

    &:hover[href],
    &:focus[href] {
      background-color: $sdc-site-share-button-bg-hover;
      border-color: $sdc-site-share-border-color-hover;
    }

    .sdc-site-share__item:last-child & {
      margin-right: 0;
    }

    &--mobile-only {
      @media screen and (min-width: $sdc-site-share-icon-break-mobile) {
        display: none;
      }
    }
  }

  &__icon {
    position: relative;
    width: 20px;
    height: 20px;
  }

  &__icon > svg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  &__icon > svg * {
    fill: $sdc-site-share-icon-color;
    transition: fill $hover;
  }

  &__link:hover &__icon > svg *,
  &__link:focus &__icon > svg * {
    fill: $sdc-site-share-icon-color-hover;
  }

  &__link--facebook:hover &__icon > svg *,
  &__link--facebook:focus &__icon > svg * {
    fill: $sdc-site-share-icon-color-hover-facebook;
  }

  &__link--whatsapp:hover &__icon > svg *,
  &__link--whatsapp:focus &__icon > svg * {
    fill: $sdc-site-share-icon-color-hover-whatsapp;
  }

  &__link--twitter:hover &__icon > svg *,
  &__link--twitter:focus &__icon > svg * {
    fill: $sdc-site-share-icon-color-hover-twitter;
  }

  &__link--email:hover &__icon > svg *,
  &__link--email:focus &__icon > svg * {
    fill: $sdc-site-share-icon-color-hover-email;
  }

  // Center and space out the buttons for narrate & timeline:
  &.sdc-site-share--timeline,
  &.sdc-site-share--narrate {
    text-align: center;
    margin-bottom: 2em;
  }
}
