// action button is in the mobile header
// is a red link with flashing dot
@keyframes action-button-pulse {

  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.1;
  }

  100% {
    opacity: 1;
  }
}

@media #{$sdc-site-header-mobile-bp} {

  .sdc-site-header__subnav-item--action-button-in-mobile-header {
    display: inline-block;
  }

  .sdc-site-header__subnav-item--action-button-in-mobile-header .sdc-site-header__action-button {
    background: none;
    color: var(--live);
    border-style: none;
    font-size: 14px;
    display: flex;
    align-items: center;
  }

  .sdc-site-header__subnav-item--action-button-in-mobile-header .sdc-site-header__action-button:before {
    content: "";
    width: 8px;
    height: 8px;
    background: currentColor;
    border-radius: 50%;
    margin-right: 6px;
  }

  @media (prefers-reduced-motion: no-preference) {

    .sdc-site-header__subnav-item--action-button-in-mobile-header .sdc-site-header__action-button:before {
      animation: action-button-pulse 1.7s ease infinite;
    }
  }
}
