.sdc-article-gallery {
    display: block;
    background: $sdc-article-gallery-background;
    font-size: $sdc-article-gallery-caption-font-size-default;
    position: relative;
    overflow: hidden;

    &:focus {
        outline: none;
    }

    &__container {
        overflow: hidden;
    }

    &__list {
        margin: 0 !important;
        padding: 0 !important;
        transition: transform 350ms;
    }

    &__item {
        display: block;
        position: relative;
        margin: 0 !important;
        padding: 0 !important;
        width: 100%;
        float: left;
    }

    &__figure {
        margin: 0;
    }

    // Default to 16/9 ratio for image
    &__ratio {
        position: relative;
        overflow: hidden;
        padding-bottom: 56.25%;
    }

    &__image {
        box-sizing: border-box;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        height: auto;
        width: 100%;
        max-height: 100%;

        &--hasjs {
            display: none;
        }
    }

    &--single-image &__image--hasjs {
        display: block;
    }

    &__caption,
    &__info-wrap {
        padding: $sdc-article-gallery-caption-v-padding $sdc-article-gallery-caption-h-padding;
        line-height: 1.4;
        box-sizing: border-box;
        min-height: calc(2 * (#{$sdc-article-gallery-caption-v-padding}) + (1.3 * #{$sdc-article-gallery-caption-font-size-default}));
        color: $sdc-article-gallery-caption-color;
    }

    &__caption-text {
        display: block;
        margin: 0 auto;
        padding-right: 3em;
        max-width: 100%;

        &:after {
            content: "\0000a0";
        }
    }

    &__caption-text,
    &__caption[class] &__caption-text:last-of-type {
        box-sizing: border-box;
    }

    &__visually-hidden {
        position: absolute;
        height: 1px;
        width: 1px;
        overflow: hidden;
        clip: rect(1px, 1px, 1px, 1px);
    }

    &__info {
        color: $sdc-article-gallery-caption-color;
        pointer-events: none;
        position: absolute;
        top: 0;
        margin: 56.25% auto 0;
        width: 100%;
        z-index: 9999;
    }

    &__info-wrap {
        margin: 0 auto;
        position: relative;
    }

    &__info-text {
        position: absolute;
        top: $sdc-article-gallery-caption-v-padding;
        right: $sdc-article-gallery-caption-h-padding;
        text-align: right;
    }

    &__controls {
        padding-top: 56.25%;
        width: 100%;
        position: absolute;
        top: 0;
    }

    &__area {
        cursor: pointer;
        height: 100%;
        width: 50%;
        position: absolute;
        top: 0;
        user-select: none;

        &--prev {
            left: 0;
        }

        &--next {
            right: 0;
            text-align: right;
        }
    }

    &__control {
        background: $sdc-article-gallery-control-background;
        border: 0;
        margin: -35px 0 0 -35px;
        padding: 0 10px;
        height: 70px;
        width: 70px;
        position: absolute;
        top: 50%;
        transition: all .2s;

        .sdc-article-gallery__area--next:hover &, &:focus,
        .sdc-article-gallery__area--prev:hover &, &:focus {
            cursor: pointer;
            background: $sdc-article-gallery-control-background-hover;
        }

        &--prev {
            clip: rect(0, 70px, 70px, 35px);
            text-align: right;
            left: 0;
        }

        &--next {
            clip: rect(0, 35px, 70px, 0);
            text-align: left;
            right: -35px;
        }

        & svg {
            position: relative;
            vertical-align: middle;
        }

        &--prev svg {
            right: 2px;
        }

        &--next svg {
            left: 2px;
        }
    }

    // Adjust for displaying 4/3 ratio imagery
    &__ratio--4\/3 {
        padding-bottom: 75%;
    }

    &--4\/3 {
        .sdc-article-gallery__controls,
        .sdc-article-gallery__info {
            padding-top: 75%;
        }
    }

    // Inactive Styles
    &--inactive {
        .sdc-article-gallery__controls,
        .sdc-article-gallery__info {
            display: none;
        }
    }

    // Active Styles
    &--active {
        .sdc-article-gallery__list {
            width: 10000%;
            transform: translate3d(0, 0, 0);
        }

        .sdc-article-gallery__image--hasjs {
            display: block;
        }

        .sdc-article-gallery__item {
            width: 1%;
        }
    }

    // Prevent animation to reset the items at either end
    &__list--notransition {
        transition: none !important;
    }

    // Hide the captions whilst animating
    &__list--animating .sdc-article-gallery__caption-text {
        visibility: hidden;
    }
    &--active .sdc-article-gallery__caption-text {
        animation: showCaption .1s;
        height: 100%;
    }
}

@media screen and (min-width: 600px) {
    .sdc-article-gallery {
        font-size: $sdc-article-gallery-caption-font-size-medium;
    }
}

@media screen and (min-width: $sdc-article-gallery-medium) {
    .sdc-article-gallery {
        &__text {
            min-height: calc((2 * #{$sdc-article-gallery-caption-v-padding}) + (1.3 * #{$sdc-article-gallery-caption-font-size-medium}));
        }
        &__text,
        &__info {
            font-size: $sdc-article-gallery-caption-font-size-medium;
        }
    }
}

@keyframes showCaption {
    0% {
        opacity: 0;
        height: 0;
    }

    35% {
        opacity: 0;
        height: 100%;
    }

    100% {
        opacity: 1;
        height: 100%;
    }
}
