// BLACKJACK CONTENT IMAGE COMPONENT
// ===================

.sdc-article-image {
  font-size: $sdc-article-image-caption-text-default;

  &__figure {
    margin: 0;
  }

  // use padding bottom to force various aspect ratios for images  - default = 16/9
  &__wrapper {
    position: relative;
    padding-bottom: 56.25%;

    &[data-aspect-ratio="4/3"] {
      padding-bottom: 75%;
    }

    &[data-aspect-ratio="1/1"] {
      padding-bottom: 100%;
    }
  }

  &__item {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
  }

  &__caption {
    padding: $sdc-article-image-caption-v-padding $sdc-article-image-caption-h-padding;
    line-height: 1.4;
    text-align: $sdc-article-image-caption-align;
    background-color: $sdc-article-image-caption-background;
    color: $sdc-article-image-caption-color;
  }

  &__caption-text {
    display: block;
    max-width: 100%;
    margin: 0 auto;
  }

  &__visually-hidden {
    position: absolute;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
  }

  &__simple {

    &--fill {
      position: relative;
      width: $sdc-article-image-fill-width;
      height: $sdc-article-image-fill-height-sm-mobile;

      img {
        object-fit: contain;
      }
    }
  }
}

@media screen and (min-width: $sdc-article-image-break-xsmall) {

  .sdc-article-image__simple--fill {
    height: $sdc-article-image-fill-height-mobile;
  }
}

@media screen and (min-width: $sdc-article-image-break-small) {

  .sdc-article-image__simple--fill {
    height: $sdc-article-image-fill-height-tablet;
  }
}
