@import "skin";

.sdc-article-topic-articles-list {
    &__inner {
        font-size: $sdc-article-topic-articles-list-font-size-default;
        box-sizing: border-box;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    &__name {
        font-size: $sdc-article-topic-articles-list-font-size-name-default;
        margin: 0;
        padding: $sdc-article-topic-articles-list-padding-name-default;
    }

    &__list {
        display: table;
        position: relative;
        width: 100%;
    }

    &__header,
    &__item {
        display: table-row;
    }

    &__title {
        display: table-cell;
        padding: $sdc-article-topic-articles-list-padding-title-default;
        max-width: 40%;
        border-bottom: $sdc-article-topic-articles-list-border-bottom-topic-default;
    }

    &__topic {
        display: table-cell;
        max-width: 40%;
        width: $sdc-article-topic-articles-list-width-topic-default;
        border-bottom: $sdc-article-topic-articles-list-border-bottom-topic-default;
        vertical-align: $sdc-article-topic-articles-list-vertical-align-item;
        line-height: 1.2;
    }

    &__topic-link {
        color: $sdc-article-topic-articles-list-link-color-default;
        padding: $sdc-article-topic-articles-list-padding-topic-default;
        display: inline-block;
        text-decoration: none;
        font-weight: bold;
        width: 100%;
        border-bottom: none;

        &:focus {
            outline: $sdc-article-topic-articles-list-outline-focus-default;
        }

        &:hover {
            color: $sdc-article-topic-articles-list-color-hover-default;
        }
    }

    &__articles {
        display: table-cell;
        border-bottom: $sdc-article-topic-articles-list-border-bottom-article-default;
        line-height: 1.2;
        vertical-align: $sdc-article-topic-articles-list-vertical-align-item;
        list-style: none;
    }

    &__article-item {

    }

    &__article-link {
        position: relative;
        color: $sdc-article-topic-articles-list-link-color-default;
        display: inline-block;
        width: 100%;
        padding: $sdc-article-topic-articles-list-padding-article-default;
        text-decoration: none;
        border-bottom: $sdc-article-topic-articles-list-border-bottom-article-link-default;

        .sdc-article-topic-articles-list__article-item:last-child & {
            border-bottom: none;
        }

        &:focus {
            outline: $sdc-article-topic-articles-list-outline-focus-default;
        }

        &:hover {
            color: $sdc-article-topic-articles-list-color-hover-default;
        }

        &:after {
            content: "";
            width: $sdc-article-topic-articles-list-width-chevron-default;
            height: $sdc-article-topic-articles-list-height-chevron-default;
            display: block;
            vertical-align: 0;
            background-repeat: no-repeat;
            opacity: .5;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(-50%, -50%);
            background-image: url($sdc-article-topic-articles-list-image-chevron-default);
        }
    }

    @media all and (min-width: $sdc-article-topic-articles-list-break-medium) {
        &__name {
            font-size: $sdc-article-topic-articles-list-font-size-name-desktop;
        }

        &__topic,
        &__article-item {
            font-size: $sdc-article-topic-articles-list-font-size-text-desktop;
        }

        &__topic {
            width: $sdc-article-topic-articles-list-width-topic-medium;
        }

        &__topic-link {
            padding: $sdc-article-topic-articles-list-padding-topic-desktop;
        }

        &__article-link {
            padding: $sdc-article-topic-articles-list-padding-article-desktop;
        }
    }
}
