 .custom-index {

   .sdc-site-hero__inner[class],
   .sdc-site-countdown-timer__inner[class] {
     @extend .site-wrap !optional;
     @extend .site-wrap-padding !optional;
   }

   .sdc-site-feature-tile[class] {
     @extend %glints-box !optional;
     @extend %glints-box-hover !optional;
     @extend %glints-box--mobile-edge !optional;
   }

   .sdc-site-countdown-timer[class] {
     box-shadow: inset 0 2px 7px rgba(0, 0, 0, 0.13)
   }

   .sdc-site-countdown-timer {
     @extend .site-component-vertical-margin-negative;
     background-size: cover;
     background-position: 50%;
     background-repeat: no-repeat;
   }

   .sdc-site-hero[data-type="index"] {
     margin-bottom: -90px;
     position: relative;
   }

   @supports (object-fit:cover) {
     .sdc-site-hero[data-type="index"] {
       height: 72vh;
     }
   }

   .sdc-site-hero[data-type="index"]
   .sdc-site-hero__inner {
     padding-bottom: 78px;
   }

   .sdc-site-hero[data-type="index"]
   .sdc-site-hero__snippet,
   .sdc-site-hero[data-type="index"]
   .sdc-site-hero__author {
     display: none;
   }

   .sdc-site-hero[data-type="index"] + div {
     position: relative;
     z-index: 2;
   }

   @media all and (max-width: 600px) {
     .sdc-site-hero[data-type="index"] {
       margin-bottom: -50px;
       height: auto;
     }

     .sdc-site-hero[data-type="index"],
     .sdc-site-hero__inner {
       padding-bottom: 41px;
     }

     @supports (object-fit: cover) {
       .sdc-site-hero[data-type="index"] {
         height: 46vh;
       }
     }
   }
 }