/* COMPONENT */
/* The main structural styles to create a vanilla version of this component */
/* ========= */

@mixin sdc-site-component-header {
  font-weight: $sdc-site-component-header-font-weight;
  line-height: $sdc-site-component-header-line-height;
}

.sdc-site-component-header__body {
  display: block;
}

/* Header size/importance hieracies in descending orders */
.sdc-site-component-header--h1 {
  @include sdc-site-component-header;
  font-size: $sdc-site-component-header-h1-font-size !important;
  text-align: $sdc-site-component-header-h1-align;
  margin-bottom: $sdc-site-component-header-h1-margin-bottom !important;
}

.sdc-site-component-header--h2 {
  @include sdc-site-component-header;
  font-size: $sdc-site-component-header-h2-font-size !important;
  text-align: $sdc-site-component-header-h2-align;
  margin-bottom: $sdc-site-component-header-h2-margin-bottom !important;
  line-height: $sdc-site-component-header-h2-line-height;
}

.sdc-article-header--feature {

  .sdc-site-component-header--h2 {
    color: $sdc-site-component-header-h2-text-colour;
  }
}

.sdc-site-component-header--h3 {
  @include sdc-site-component-header;
  font-size: $sdc-site-component-header-h3-font-size !important;
  text-align: $sdc-site-component-header-h3-align;
  margin-bottom: $sdc-site-component-header-h3-margin-bottom !important;
}

.sdc-site-component-header--h4 {
  @include sdc-site-component-header;
  font-size: $sdc-site-component-header-h4-font-size !important;
  text-align: $sdc-site-component-header-h4-align;
  margin-bottom: $sdc-site-component-header-h4-margin-bottom !important;
}

.sdc-site-component-header--h5 {
  @include sdc-site-component-header;
  font-size: $sdc-site-component-header-h5-font-size !important;
  text-align: $sdc-site-component-header-h5-align;
  margin-bottom: $sdc-site-component-header-h5-margin-bottom !important;
}

.sdc-site-component-header__logo {
  height: $sdc-site-component-header-logo-height;
}

.sdc-site-component-header--h1 .sdc-site-component-header__logo {
  height: $sdc-site-component-header-h1-logo-height;
}

@media #{$sdc-site-component-header-mobile-bp} {

  .sdc-site-component-header--h1 {
    font-size: $sdc-site-component-header-h1-font-size-mobile !important;
    margin-bottom: $sdc-site-component-header-mobile-h1-margin-bottom !important;
  }

  .sdc-site-component-header--h2 {
    font-size: $sdc-site-component-header-h2-font-size-mobile !important;
    margin-bottom: $sdc-site-component-header-mobile-h2-margin-bottom !important;
  }

  .sdc-site-component-header--h3 {
    font-size: $sdc-site-component-header-h3-font-size-mobile !important;
    margin-bottom: $sdc-site-component-header-mobile-h3-margin-bottom !important;
  }
}

@media #{$sdc-site-component-header-small-mobile-bp} {

  .sdc-site-component-header__body[data-short-text] > [data-role="short-text-target"] {
    display: none;
  }

  .sdc-site-component-header__body[data-short-text]:before {
    content: attr(data-short-text);
  }

  .sdc-site-component-header--h1 {
    margin-bottom: $sdc-site-component-header-small-mobile-h1-margin-bottom !important;
  }

  .sdc-site-component-header--h2 {
    margin-bottom: $sdc-site-component-header-small-mobile-h2-margin-bottom !important;
  }

  .sdc-site-component-header--h3 {
    margin-bottom: $sdc-site-component-header-small-mobile-h3-margin-bottom !important;
  }
}
