.sdc-article-quote--with-image {
    box-sizing: border-box;
    min-height: 93vw;
    display: flex;
    background-color: $sdc-article-quote-image-transparency-color;

    .sdc-article-quote {
        &__inner {
            background-color: transparent;
            top: 0;
            left: 0;
            min-height: 100%;
            height: 100%;
            width: 100%;
        }

        &__content {
            background: transparent;
            border: none;
            margin-top: 0;
            margin-bottom: 0;
            width: 100%;
        }

        &__image {
            position: absolute;
            width: 100%;
            height: 100%;
            overflow: hidden;
            top: 0;
            left: 0;
            margin: 0;
            z-index: 0;
            opacity: $sdc-article-quote-image-transparency;
        }

        &__image-container {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        &__image-element {
            display: block;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, 0);
            height: 100%;
            object-fit: cover;
        }

        &__citation,
        &__text {
            color: $sdc-article-quote-image-overlay-text-colour;
            text-shadow: $sdc-article-quote-image-overlay-text-shadow;
        }
    }
}

@media (min-width: $sdc-article-quote-break-small) {
    .sdc-article-quote--with-image {
        min-height: 62.5vw;
    }
}

@media (min-width: $sdc-article-quote-break-limit-scaling) {
    .sdc-article-quote--with-image {
        min-height: 0;
        display: block;

        &:before {
            content: "";
            display: block;
            width: 100%;
            height: 0;
            padding-top: 62.5%;
        }

        .sdc-article-quote {
            &__inner {
                position: absolute;
            }
        }
    }
}
