
$text-family: var(--base-font-name), Helvetica, Arial, sans-serif !default;

// SITE BREAKPOINTS
$bp30-max: unquote("max-width:600px") !default;

// GENERAL SKY SPORTS PALETTE

// base palette
$red1: #d6181f !default;
$blue1: #214cb8 !default; // Possible new blue: #0070a5
$light90: #f4f4f4 !default;
$light80: #eaeaea !default;
$light70: #dcdcdc !default;
$dark90: #333 !default;
$dark80: #4c4c4c !default;
$dark70: #999 !default;
$yellow1: #ffe001 !default;
$twitter-blue: #4099ff;
$white: #fff !default;

// general
$webkit-tap-highlight-color: unquote("rgba(0, 0, 0, .08)") !default;
$livefyre-logo: "//zor.livefyre.com/wjs/v1.0/images/icons/poweredbylivefyre.png" !default;

// text colours
$text-primary: $dark90;
$text-body: $dark90;
$link-text: $blue1;

$text-line-height: 1.4em !default;

@mixin button--light() {
  @include primary-text($text-m);
  background: none;
  border: 1px solid $light70;
  color: $text-primary;
}

@mixin button--fluid() {
  display: block;
  width: 100%;
  padding-top: .5em;
  padding-bottom: .5em;
}

// GLOBAL BLOCK UNITS
// use to set margins, paddings etc

// margin bottom on main content blocks
$block-margin-s: 12px;
$block-margin-m: 18px;

// padding inside blocks
$block-padding-s: 12px;

// gaps and margins within components
$component-gap-s: 9px;

// site indents - the margins either side of the wrapper
$site-indent-s: 0;

// remove margins from mobile site
$site-indent-bp30: 0;

$sdc-article-live-blog-button-border-width: 1px !default;
$sdc-article-live-blog-button-border-radius: 3px !default;
