.sdc-site-charts[data-type="barvert"],
.sdc-site-charts[data-type="barhori"] {

  .sdc-site-charts__chart-point {
    fill: #4a4a4a;
  }
}

.sdc-site-charts[data-type="barvert"][animated] .sdc-site-charts__chart-point {
  transition: height 1s, opacity 0.33s;
}

.sdc-site-charts[data-type="barhori"][animated] .sdc-site-charts__chart-point {
  transition: width 1s, opacity 0.33s;
}

.sdc-site-charts[data-type="barvert"] .sdc-site-charts__chart-points {
  transform: rotate(180deg);
  transform-origin: center;
}
