.sdc-site-localnav[data-swipe-nav] {
  overscroll-behavior: contain;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  overflow: auto;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  scroll-behavior: smooth;
}

.sdc-site-localnav__swipe {
  content: "";
  position: absolute;
  left: 100%;
  transform: translateX(-100%);
  height: $sdc-site-localnav-links-height-desktop;
  width: $sdc-site-localnav-links-height-desktop * 0.8;
  margin-top: $sdc-site-localnav-links-height-desktop * -1;
  background: url("data:image/svg+xml;base64,") no-repeat center;
  background-color: inherit;
  background-size: 60%;
  cursor: pointer;
  border: 0;
  display: none;
  background-blend-mode: difference;
  opacity: 0.8;
}

.sdc-site-localnav[data-swipable="true"] .sdc-site-localnav__swipe {
  display: block;
}

.sdc-site-localnav[data-swipe-nav]::-webkit-scrollbar {
  display: none;
}

.sdc-site-localnav[data-swipe-nav] .sdc-site-localnav__items[class] {
  white-space: nowrap;
  padding-right: inherit;
}