/* SDC site share skin
  =========================================== */

.sdc-site-share[class] {
  @extend .site-wrap !optional;
  @extend .site-wrap-padding!optional;
  margin: 0 auto;
  text-align: center;
}

.sdc-article-body ~ .sdc-site-share,
.sdc-article-video ~ .sdc-site-share,
.sdc-article-gallery ~ .sdc-site-share,
.sdc-site-view-manager ~ .sdc-site-share {
  @extend %glints-border-top !optional;
  padding: $component-vertical-margin-l 0 $component-vertical-margin-xl;
}

.sdc-site-share__title[class] {
  @extend .sdc-site-component-header--h4 !optional;
  font-weight: bold !important;
}

.sdc-site-share--article-top[class] {
  @extend %sdc-article-body-width-limiter !optional;
  text-align: left;
}