@media (min-width: $sdc-article-quote-break-xlarge) {
    .sdc-article-quote--half-width {

        .sdc-article-quote {
            &__content {
                margin: 0;
                padding: $sdc-article-quote-half-width-padding;
            }

            &__text {
                font-size: $sdc-article-quote-text-default;
            }

            &__citation {
                font-size: $sdc-article-quote-citation-text-default;
            }
        }
    }
}
