/* tiles skin */
/* ======= */
$sdc-site-tiles-gap-y-mobile: 8px !default;
$sdc-site-tile-listing-page-counter-increment: items-to-show-6 !default;

// extends
.sdc-site-tiles[class] {
  --sdc-site-tile-hover-background: var(--gradient-radial);
  margin-bottom: var(--group-margin);
}

.sdc-site-tiles__inner {
  @extend .site-wrap !optional;
  @extend .site-wrap-padding !optional;
  @extend .site-wrap--mobile-edge !optional;
}

.sdc-site-tile__body {
  background: none !important;
}

.sdc-site-tile,
.blackjack-sdc-site-tile {
  @extend %glints-box !optional;
  @extend %glints-box-hover !optional;
  @extend %glints-box--mobile-edge !optional;
}

.sdc-site-tile__related[class] {
  @extend .sdc-ui-bullet-list !optional;
}

.sdc-site-tile__tag[class] {
  @extend .sdc-ui-label !optional;
}

// load more counter increment
.listing-wrap .sdc-site-tiles__item {
  counter-increment: $sdc-site-tile-listing-page-counter-increment;
}