/* sdc-article-image-grid */
.sdc-article-image-grid {
  line-height: 1;

  &__visually-hidden {
    border: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    white-space: nowrap;
  }

  &__list {
    padding: 0;
    font-size: 0;
    position: relative;
  }

  &__list--panel-visible {

    .sdc-article-image-grid__caption-link {
      opacity: .5;
    }

    .sdc-article-image-grid__caption-link[aria-expanded="true"] {
      opacity: 1;
    }
  }

  &__item {
    display: inline-block;
    width: 50%;
    vertical-align: top;

    // one item fill width
    &:nth-child(1):nth-last-child(1) {
      width: 100%;
    }

    .no-js & {
      width: 100%;
    }

    figure {
      padding: 1px;
      margin: 0;
    }
  }

  // Default image crop is 1:1 square.
  &__caption-link {
    display: list-item;
    position: relative;
    padding-bottom: 100%;
    overflow: hidden;
    width: 100%;

    &[class] {
      border-bottom: none;
    }

    &:focus,
    &:hover {
      z-index: 5;
      border: 0;
      outline: 0;
      opacity: .5;
    }

    // Override for 16:9 crops (landscape)
    .sdc-article-image-grid--16\/9 & {
      padding-bottom: (9/16)*100%;
    }

    // Override for 4:3 crops (landscape)
    .sdc-article-image-grid--3\/4 & {
      padding-bottom: (4/3)*100%;
    }

    // Override for 3:4 crops (portrait)
    .sdc-article-image-grid--4\/3 & {
      padding-bottom: (3/4)*100%;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate3d(-50%, 15px, 0);
      transition: transform .1s ease-in-out;
      width: 0;
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      border-bottom: 15px solid $sdc-article-image-grid-background-panel-bg;
      z-index: 5;
    }

    &[aria-expanded="true"] {
      z-index: 5;
    }

    &[aria-expanded="true"]:after {
      transform: translate3d(-50%, 1px, 0);
    }
  }

  &__image {
    border: none;
    width: 100%;
    height: auto;
    display: block;
    position: absolute;
  }

  &__caption {
    z-index: 1;
    font-size: 1.125rem;
    position: absolute;
    overflow: hidden;
    left: 0;
    width: 100%;
    display: none;
    box-shadow: $sdc-article-image-grid-background-panel-shadow;
    line-height: 1.5;
    background-color: $sdc-article-image-grid-background-panel-bg;

    .no-js & {
      position: relative;
      display: block;
      margin-bottom: 1rem;
    }

    [aria-expanded="true"] + & {
      display: block;
    }
  }

  &__caption-content {
    padding: 30px 20px 0;
    margin: 2rem auto;
    max-width: $sdc-article-image-grid-caption-max-width;
    color: $sdc-article-image-grid-caption-colour;
    position: relative;
    z-index: 1;

    > *:first-child {
      margin-top: 0;
    }

    .no-js & {
      padding-bottom: 30px;
    }

    p {
      font-size: 1rem;
      margin-bottom: 1rem;
    }

    p:last-child {
      margin-bottom: 0;
    }

    h3 {
      font-size: 1.25rem;
      margin-bottom: 1rem;
      line-height: 1.2;
    }

    h4 {
      font-size: 1.125rem;
      margin-bottom: 0;
      line-height: 1.33;
    }

    a:link {
      color: $sdc-article-image-grid-caption-link-colour;
      font-weight: bold;
      text-decoration: underline;
      border-bottom: none;
      text-decoration-skip: ink;
    }

    a:hover {
      text-decoration-color: fade-out($sdc-article-image-grid-caption-link-colour, .5);
    }

    a:focus {
      text-decoration: none;
      color: #fff;
      background: $sdc-article-image-grid-caption-link-colour;
    }
  }

  // Controls
  &__close-button {
    @extend %sdc-article-image-grid__svg-behaviour;
    float: right;
    padding: 20px;
    box-sizing: content-box;
    height: 18px;
    position: relative;
    z-index: 5;
    border-bottom: none;

    &[class] {
      color: $sdc-article-image-grid-background-panel-button-colour;
      border-bottom: none;
    }

    svg {
      vertical-align: top;
      width: 18px;
    }
  }

  &__directional-controls {
    max-width: $sdc-article-image-grid-caption-max-width;
    margin: 0 auto 2rem;
    padding: 0 20px;
    overflow: hidden;
  }

  &__directional-button {
    font-size: inherit;
    font-family: inherit;

    @extend %sdc-article-image-grid__svg-behaviour;

    &[class] {
      color: $sdc-article-image-grid-background-panel-button-colour;
      border-bottom: none;
    }

    &[class]:hover,
    &[class]:active {
      color: $sdc-article-image-grid-caption-link-colour;
    }

    &:after {
      content: "";
      display: block;
      box-sizing: border-box;
      position: absolute;
      top: 0;
      height: 100%;
      width: 25%;
    }

    svg {
      vertical-align: middle;
      width: 14px;
    }

    &--next {
      float: right;
    }

    &--next:after {
      right: 0;
    }

    &--next svg {
      margin-left: 12px;
    }

    &--prev {
      float: left;
    }

    &--prev:after {
      left: 0;
    }

    &--prev svg {
      margin-right: 12px;
    }
  }

  &__hidden-controls {
    display: none;
  }
}

@media screen and (min-width: $sdc-article-image-grid-small) {

  .sdc-article-image-grid {

    &__item {
      width: 33.332%;
    }

    // two items fill width
    &__item:nth-child(1):nth-last-child(2),
    &__item:nth-child(2):nth-last-child(1) {
      width: 50%;

      .no-js & {
        width: 100%;
      }
    }

    // four items fill width
    &__item:nth-child(1):nth-last-child(4),
    &__item:nth-child(2):nth-last-child(3),
    &__item:nth-child(3):nth-last-child(2),
    &__item:nth-child(4):nth-last-child(1) {
      width: 50%;
    }
  }
}

@media screen and (min-width: $sdc-article-image-grid-medium) {

  .sdc-article-image-grid {

    &__item {
      width: 25%;
    }

    &__panel-content {
      padding-left: 50px;
      padding-right: 50px;
    }

    // three items fill width
    &__item:nth-child(1):nth-last-child(3),
    &__item:nth-child(2):nth-last-child(2),
    &__item:nth-child(3):nth-last-child(1) {
      width: 33.3333%;

      .no-js & {
        width: 100%;
      }
    }

    // four items fill width
    &__item:nth-child(1):nth-last-child(4),
    &__item:nth-child(2):nth-last-child(3),
    &__item:nth-child(3):nth-last-child(2),
    &__item:nth-child(4):nth-last-child(1) {
      width: 25%;
    }
  }
}

%sdc-article-image-grid__svg-behaviour {

  svg {

    * {
      fill: $sdc-article-image-grid-background-panel-button-colour;
    }
  }

  &:focus,
  &:hover {
    color: $sdc-article-image-grid-background-panel-button-hover-colour;

    svg * {
      fill: $sdc-article-image-grid-background-panel-button-hover-colour;
    }
  }
}
