.sdc-article-body {
  font-size: font-size-in-rem($sdc-article-body-body-text-default);
  line-height: 1.44;

  @if $simplify-for-amp == false {

    @media all and (min-width: $sdc-article-body-break-medium) {
      font-size: font-size-in-rem($sdc-article-body-body-text-medium);
      line-height: 1.55;
    }
  }

  @if $simplify-for-amp == false {

    *::selection {
      text-shadow: none;
      color: #fff;
      background-image: none;
      background-color: $sdc-article-body-link-colour;
    }

    *::-moz-selection {
      text-shadow: none;
      color: #fff;
      background-image: none;
      background-color: $sdc-article-body-link-colour;
    }
  }

  %vertical-spacing {
    // this does not apply to article widgets
    margin-top: 0;
    margin-bottom: $sdc-article-body-text-margin-default;

    &:last-child {
      margin-bottom: 0;
    }
  }

  // Link underlines
  %link-behaviour {

    > strong a,
    > em a,
    > a {
      color: $sdc-article-body-link-colour;

      @include magic-underlines($sdc-article-body-link-colour);
    }
  }

  > hr {
    @extend %sdc-article-body-width-limiter !optional;
  }
}
