@media (min-width: $sdc-article-quote-break-xlarge) {
    .sdc-article-quote--half-width.sdc-article-quote--with-image {
        background-color: transparent;
        min-height: 0;
        display: block;

        &:before {
            display: none;
        }

        .sdc-article-quote {
            &__inner {
                height: auto;
                position: relative;
                background-color: $sdc-article-quote-image-transparency-color;
            }

            &__image:before {
                display: none;
            }
        }
    }
}
