/* ----------------------------------- */
/* sdc-news-footer skin */
/* ----------------------------------- */

/* dimensions */
$sdc-news-footer-max-width: 1024px !default;
$sdc-news-footer-max-width-indent: 14px !default;
$sdc-news-footer-max-width-indent-medium: 5vw !default;

$sdc-news-footer-mobile-padding: 1.1111em !default;
$sdc-news-footer-icon-right: 1rem !default;
$sdc-news-footer-icon-top: 1em !default;
$sdc-news-footer-icon-width: 16px !default;
$sdc-news-footer-padding: 2em 0 !default;
$sdc-news-footer-row-margin-bottom: 2em !default;
$sdc-news-footer-row-padding-bottom: 1.75em !default;
$sdc-news-footer-row-outline: 1px solid !default;
$sdc-news-footer-cell-margin: 0 0 2em 0 !default;
$sdc-news-footer-cell-padding-right: 1.5em !default;
$sdc-news-footer-heading-margin: 0 0 1em 0 !default;
$sdc-news-footer-heading-font-weight: 400 !default;
$sdc-news-footer-heading-font-size: 1.333em !default;
$sdc-news-footer-list-margin: 0 !default;
$sdc-news-footer-list-padding: 0 !default;
$sdc-news-footer-list-item-margin-bottom: 0.2em !default;
$sdc-news-footer-list-item-margin-right: 2.2em !default;

$sdc-news-footer-list-link-padding: 0.4em 0.1em !default;
$sdc-news-footer-list-link-padding-mobile: 0.8em 0.1em !default;

$sdc-news-footer-list-link-line-font-size: 0.888em !default;

$sdc-news-footer-list-link-line-height: 1 !default;
$sdc-news-footer-list-logo-margin: 0.5em 0 !default;
$sdc-news-footer-list-logo-image-margin-right: 0.5em !default;
$sdc-news-footer-list-logo-image-min-width: 70px !default;
$sdc-news-footer-list-logo-image-max-width: 140px !default;

$sdc-news-footer-link-hover-outline: 3px solid !default;
$sdc-news-footer-link-icon-top: -1px !default;
$sdc-news-footer-link-icon-margin-right: 0.34em !default;
$sdc-news-footer-link-icon-width: 1.2em !default;
$sdc-news-footer-link-icon-height: 1.2em !default;

$sdc-news-footer-medium-row-padding-bottom: 0 !default;
$sdc-news-footer-medium-cell-wrap-font-size: 0 !default;
$sdc-news-footer-medium-cell-padding-right: 2em !default;
$sdc-news-footer-medium-cell-width: 50% !default;
$sdc-news-footer-medium-cell-font-size: 16px !default;

$sdc-news-footer-mobile-padding: .1rem 0 1rem !default;
$sdc-news-footer-mobile-heading-padding: 1em 2.2rem 1em 0 !default;
$sdc-news-footer-mobile-list-margin: .5em 0 !default;
$sdc-news-footer-mobile-icon-logo-margin: 0 !default;
$sdc-news-footer-mobile-wrapper-padding: 0 !default;
$sdc-news-footer-mobile-row-margin-bottom: 0 !default;
$sdc-news-footer-mobile-row-padding-bottom: 0 !default;
$sdc-news-footer-mobile-column-border-bottom: 0 !default;
$sdc-news-footer-mobile-list-padding: 0 !default;
$sdc-news-footer-mobile-cell-margin-bottom: 0 !default;
$sdc-news-footer-mobile-cell-padding-right: 0 !default;
$sdc-news-footer-mobile-heading-font-size: 1.2em !default;
$sdc-news-footer-mobile-list-item-margin: 0.1em 0 !default;
$sdc-news-footer-mobile-icon-width: 1.5em !default;
$sdc-news-footer-mobile-icon-height: 1.5em !default;
$sdc-news-footer-mobile-icon-margin-right: 1.6em !default;
$sdc-news-footer-mobile-icon-max-width: 1.6em !default;

$sdc-news-footer-mobile-keyline-margin: 0 1rem !default;
$sdc-news-footer-keyline-outline: 1px solid !default;
$sdc-news-footer-keyline-item-margin-top: .5rem !default;
$sdc-news-footer-keyline-item-margin-bottom: .5rem !default;
$sdc-news-footer-keyline-list-max-height: 500px !default;

$sdc-news-footer-box-shadow: 1px 1px 15px 0 rgba(0, 0, 0, 0.14) !default;

$sdc-news-footer-mobile-legal-margin: 1.6em 0 !default;

/* palettes */
$sdc-news-footer-color: #707070 !default;
$sdc-news-footer-link-color: #707070 !default;
$sdc-news-footer-link-hover-background: rgba(0, 0, 0, 0.08) !default;
$sdc-news-footer-link-icon-color: currentColor !default;
$sdc-news-footer-color-light: #707070 !default;
$sdc-news-footer-bg: #ffffff !default;
$sdc-news-footer-keyline: #dcdcdc !default;

$sdc-news-footer-facebook: #3b5998 !default;
$sdc-news-footer-twitter: #00aced !default;
$sdc-news-footer-youtube: #bb0000 !default;
$sdc-news-footer-instagram: #125688 !default;
$sdc-news-footer-google: #dd4b39 !default;
$sdc-news-footer-email: #4a4a4a !default;

/* breaking point */
$sdc-news-footer-pad-breakingpoint: all and (max-width: 799px) !default;
$sdc-news-footer-mobile-breakingpoint: all and (max-width: 599px) !default;