$author-image-border-radius: 50px;
$sdc-article-author-link-colour: var(--link-color);
$sdc-article-author-role-colour: var(--text-color-secondary);
$sdc-article-author-text-default: 16px;

.sdc-article-author {
  @extend .site-component-vertical-margin !optional;
 
  @media all and (min-width: 600px) {
    padding-bottom: 6px !important;
  }

  @media all and (max-width: 600px) {
    padding-bottom: 0 !important;
  }

  &__role {
    line-height: 1.39 !important;
  }

  &__avatar {
    flex: 0 0 calc(57px + 1em) !important;
  }

  &__name {
    color: var(--link-color);
  }
}
