/* BLACKJACK CONTENT FACTBOX COMPONENT */
/* =================== */

.sdc-article-factbox {
  max-width: 100%;
  font-size: $sdc-article-factbox-base-font-size;
}

.sdc-article-factbox__text-wrapper {
  color: $sdc-article-factbox-text-color;
  max-width: $sdc-article-factbox-text-width;
  margin: 0 auto;
  padding: $sdc-article-factbox-vertical-padding $sdc-article-factbox-text-indent;
  border-top: $sdc-article-factbox-border-top;
  border-right: $sdc-article-factbox-border-right;
  border-bottom: $sdc-article-factbox-border-bottom;
  border-left: $sdc-article-factbox-border-left;
  border-radius: 8px;
  background: $sdc-article-factbox-text-background;
}

.sdc-article-factbox__title-wrapper {
  display: flex;
  margin: 0 0 1em 0;
}

.sdc-article-factbox__title-wrapper::before {
  content: "";
  width: 8px;
  background: var(--brand-3);
  margin-right: 1em;
}

.sdc-article-factbox__title {
  font-size: var(--display-3);
  font-weight: normal;
  text-align: $sdc-article-factbox-title-align;
  font-family: var(--branding-sky-text-font);
}

.sdc-article-factbox__list {
  list-style-position: inside;
  list-style-type: disc;
}

.sdc-article-factbox__text {
  line-height: 1.55;
  font-size: 1.1em;
  text-align: $sdc-article-factbox-text-align;
}

p.sdc-article-factbox__text + p.sdc-article-factbox__text {
  margin: 1.5em 0 0 0;
}

li.sdc-article-factbox__text + li.sdc-article-factbox__text {
  margin: 0.5em 0 0 0;
}


@media all and (max-width: $sdc-article-factbox-break-medium) {

  .sdc-article-factbox__text-wrapper {
    padding: $sdc-article-factbox-vertical-padding-small $sdc-article-factbox-text-indent/2;
  }
}


@media all and (max-width: $sdc-article-factbox-break-small) {

  .sdc-article-factbox__text-wrapper {
    padding: $sdc-article-factbox-vertical-padding-small $sdc-article-factbox-text-indent/3;
    font-size: 0.9em;
  }
}
