// TODO: Tidy this section
// Image and video caption overrides
.sdc-article-body {

  .sdc-article-widget {
    // Caption width limiter
    [class*="__caption-text"] {
      max-width: $sdc-article-body-text-width-default;

      @media screen and (min-width: $sdc-article-body-break-small) {
        max-width: $sdc-article-body-text-width-small;
      }

      @media screen and (min-width: $sdc-article-body-break-medium) {
        max-width: $sdc-article-body-text-width-medium;
      }
    }
  }
}
