.sdc-site-header {
  background: $sdc-site-header-background;
  color: $sdc-site-header-color;
  position: relative;
  z-index: $sdc-site-header-z-index;
}

.sdc-site-header__inner {
  max-width: $sdc-site-header-width;
  margin: 0 auto;
  position: relative;
  direction: rtl;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
}

.sdc-site-header__top {
  direction: ltr;
  flex: 1;
}

// site logo
.sdc-site-header__logo {
  display: block;
  height: $sdc-site-header-logo-height;
  direction: ltr;
  position: relative;
  min-width: 100px;
}

.sdc-site-header__logo > img {
  display: block;
  height: 100%;
}

// main navigation block
// hidden on mobile by default
.sdc-site-header__nav-wrap {
  display: none;
  clear: both;
  width: 100%;
}

.sdc-site-header__menu-cell {
  padding: 0;
  list-style: none;
}

.sdc-site-header__menu-item-link {
  white-space: nowrap;
  display: block;
}

// active menu item
.sdc-site-header__menu-item-link[aria-current="true"],
.sdc-site-header__menu-item-link[aria-expanded="true"] {
  font-weight: 600;
}

// action button - usually the red watch video button that appears right of main nav
.sdc-site-header__action-button {
  background: $sdc-site-header-action-button-background;
  color: $sdc-site-header-action-button-color;
  border-radius: $sdc-site-header-action-button-border-radius;
  display: inline-block;
  padding: .6em 1em;
  font-weight: bold;
  font-size: .9em;
}

// sub nav - login, more, search etc
.sdc-site-header__subnav {
  direction: ltr;
  white-space: nowrap;
  text-align: right;
}

.sdc-site-header__subnav-item {
  display: inline-block;
  position: relative;
  margin-left: 1.5em;
  vertical-align: middle;
  text-align: left;
}

// hide the first instance of the secondary nav on mobile
.sdc-site-header__subnav-item--more-nav {
  display: none;
}

.sdc-site-header__subnav-item--mobile-nav {
  z-index: $sdc-site-header-nav-mobile-z-index + 1;
  visibility: visible;
}

// subnav menu
.sdc-site-header__subnav-item-link {
  display: block;
  font-size: .95em;
  padding: .25em 0;
  position: relative;
}

.sdc-site-header__subnav-menu,
.sdc-site-header__menu-group-overflow {
  background: $sdc-site-header-subnav-menu-background;
  position: absolute;
  min-width: 200px;
  top: 100%;
  left: 50%;
  transform: translate(-50%, 10px);
  padding-top: 3px;
  padding-bottom: 3px;
  border-top: $sdc-site-header-subnav-menu-border;
  border-bottom: $sdc-site-header-subnav-menu-border;
  z-index: $sdc-site-header-subnav-menu-z-index;
}

.sdc-site-header__subnav-menu:after {
  border: $sdc-site-header-subnav-menu-border;
  border-bottom: 0;
  border-right: 0;
  content: "";
  position: absolute;
  left: calc(50% - 8px);
  top: -10px;
  background: $sdc-site-header-subnav-menu-background;
  width: 16px;
  height: 16px;
  transform: rotate(45deg);
}

.sdc-site-header__subnav-menu[aria-hidden="true"] {
  display: none;
}

.sdc-site-header__subnav-menu-item-link,
.sdc-site-header__menu-group-overflow .sdc-site-header__menu-item-link {
  height: auto;
  display: block;
  padding: .6em 0;
  position: relative;
  z-index: 2;
  color: $sdc-site-header-subnav-menu-color;
  border-top: $sdc-site-header-subnav-menu-item-border;
}

.sdc-site-header__menu-group-overflow .sdc-site-header__menu-item-link {
  border-top: 0;
  border-bottom: $sdc-site-header-subnav-menu-item-border;
}

.sdc-site-header__subnav-menu-item:first-of-type > .sdc-site-header__subnav-menu-item-link {
  border-top: 0;
}

.sdc-site-header__menu-group-overflow > .sdc-site-header__menu-item:last-of-type .sdc-site-header__menu-item-link {
  border-bottom: 0;
}

// icons
.sdc-site-header__icon {
  display: inline-block;
  width: 22px;
  height: 22px;
  position: relative;
  fill: $sdc-site-header-icon-color;
  vertical-align: middle;
}

.sdc-site-header__icon > svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  fill: inherit;
  pointer-events: none;
}

.sdc-site-header__icon-label {
  display: none;
}

.sdc-site-header__subnav-item .sdc-site-header__icon {
  margin-right: .6em;
  top: -1px;
}

// hamburger icon
.sdc-site-header__icon--hamburger {
  height: 18px;
}

.sdc-site-header__icon--hamburger:before,
.sdc-site-header__icon--hamburger:after {
  content: "";
  position: absolute;
  height: 50%;
  width: 100%;
  top: 1px;
  left: 0;
  border-style: solid;
  border-width: 0;
  border-top-width: 2px;
  border-bottom-width: 2px;
  border-color: inherit;
  box-sizing: border-box;
  transition: transform .166s;
}

.sdc-site-header__icon--hamburger:after {
  top: auto;
  bottom: 1px;
}

// hide hamburger label text
.sdc-site-header__subnav-item-link--hide-label .sdc-site-header__icon--hamburger {
  margin-right: 0;
}

// hamburger menu expanded
.sdc-site-header__subnav-item-link[aria-expanded="true"] .sdc-site-header__icon--hamburger:before {
  border-top: 0;
  top: 0;
  transform: rotate(-45deg);
  transform-origin: bottom;
}

.sdc-site-header__subnav-item-link[aria-expanded="true"] .sdc-site-header__icon--hamburger:after {
  transform: rotate(45deg);
  border-bottom: 0;
  transform-origin: top;
}

.sdc-site-header__subnav-item--login-nav {
  padding-right: .4em;
  margin-right: .4em;
  border-right: $sdc-site-header-nav-subnav-link-border;
  position: static;
}

.sdc-site-header__subnav-item--login-nav .sdc-site-header__icon {
  margin-right: 0;
}

.sdc-site-header__subnav-item--login-nav .sdc-site-header__subnav-item-link[aria-expanded="true"]:after {
  border: $sdc-site-header-subnav-menu-border;
  border-bottom: 0;
  border-right: 0;
  content: "";
  position: absolute;
  left: calc(50% - 8px);
  bottom: -20px;
  background: $sdc-site-header-subnav-menu-background;
  width: 16px;
  height: 16px;
  transform: rotate(45deg);
  z-index: 1;
}

.sdc-site-header__subnav-item--login-nav .sdc-site-header__subnav-menu:after {
  display: none;
}

.sdc-site-header__subnav-item--login-nav .sdc-site-header__icon-label {
  max-width: 130px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sdc-site-header__subnav-menu {
  position: absolute;
  left: 0;
  width: 100vw;
  transform: translate(0%, 0);
}

// skip to content link for screen readers
.sdc-site-header__skip-to-link {
  position: absolute;
  background: white;
  color: black;
  font-weight: bold;
  text-align: center;
  padding: 1em;
  top: 0;
  text-decoration: underline;
  z-index: $sdc-site-header-z-index + 1;
  left: 50%;
  transition: transform .166s;
  transform: translate(-50%, -110%);
  opacity: 0;
  pointer-events: none;
}

.sdc-site-header__skip-to-link:focus {
  transform: translate(-50%, 0%);
  opacity: 1;
}

@media #{$sdc-site-header-desktop-bp} {

  .sdc-site-header__inner {
    flex-wrap: wrap;
  }

  .sdc-site-header__subnav {
    flex: 0 0 50%;
    top: $sdc-site-header-top-padding-desktop + 8px;
  }

  .sdc-site-header__nav-wrap {
    flex: 0 0 100%;
    display: block;
  }

  .sdc-site-header__top {
    flex: 0 0 206px;
    padding: $sdc-site-header-top-padding-desktop 0;
  }

  // always show main nav on desktop
  .sdc-site-header__nav {
    display: flex;
    align-items: center;
    direction: ltr;
  }

  .sdc-site-header__nav-cell--1 {
    flex: 1;
    position: relative;
    left: -$sdc-site-header-menu-item-link-indent;
  }

  .sdc-site-header__menu-group--has-children {
    transform: translateX($sdc-site-header-menu-item-link-indent * 2);
  }

  // main site menu
  .sdc-site-header__menu {
    display: flex;
  }

  .sdc-site-header__menu-cell--1 {
    -ms-flex: 1;
    overflow: hidden;
    height: $sdc-site-header-menu-height;
  }

  .sdc-site-header__menu-item {
    display: inline-block;
  }

  .sdc-site-header__menu-item-link {
    padding: 0 $sdc-site-header-menu-item-link-indent;
    height: $sdc-site-header-menu-height;
    display: inline-flex;
    align-items: center;
    position: relative;
  }

  .sdc-site-header__menu-item-link-icon {
    position: relative;
    width: 1em;
    height: 1em;
    fill: inherit;
    display: inline-block;
    vertical-align: middle;
    margin: 0 0 0 .2em;
    top: 1px;
  }

  // float fixes incorrect padding on iOS9 iPad portrait view
  .sdc-site-header__menu-group {
    float: right;
    position: relative;
  }

  .sdc-site-header__menu-group-overflow[class] {
    top: 100%;
    right: 0;
    left: auto;
    transform: none;
    display: none;
    border: $sdc-site-header-subnav-menu-item-border;
  }

  .sdc-site-header__menu-group-overflow .sdc-site-header__menu-item {
    width: 100%;
    display: none;
  }

  .sdc-site-header__menu-item-link--overflow[aria-expanded] .sdc-site-header__menu-item-link-icon {
    transform: rotate(180deg);
  }

  .sdc-site-header__menu-item-link--overflow[aria-expanded] + .sdc-site-header__menu-group-overflow {
    display: block;
  }

  // active menu item
  .sdc-site-header__menu-item-link[aria-current="true"] {
    font-weight: 600;
  }

  .sdc-site-header__subnav-item {
    position: relative;
  }

  .sdc-site-header__subnav-menu[class],
  .sdc-site-header__menu-group-overflow[class] {
    padding-left: 1em;
    padding-right: 1em;
  }

  //show first instance of the secondary nav on desktop
  .sdc-site-header__subnav-item--more-nav {
    display: inline-block;
  }

  // hide hamburger menu and duplicated second nav on desktop
  .sdc-site-header__subnav-item--mobile-nav,
  .sdc-site-header__subnav--mobile {
    display: none;
  }

  .sdc-site-header__subnav--mobile + .sdc-site-header__nav-cell--2 {
    margin-left: 1em;
  }

  .sdc-site-header__subnav-item--login-nav {
    border-right: 0;
    margin-right: 0;
    padding-right: 0;
  }

  .sdc-site-header__subnav-menu,
  .sdc-site-header__menu-group-overflow {
    left: 50%;
    transform: translate(-50%, 10px);
    width: auto;
    border: $sdc-site-header-subnav-menu-border;
  }

  .sdc-site-header__icon-label {
    display: inline;
  }

  .sdc-site-header__subnav-item .sdc-site-header__icon {
    margin-right: .6em;
  }
}

@media #{$sdc-site-header-desktop-wide-bp} {

  .sdc-site-header__subnav-item--login-nav .sdc-site-header__icon-label {
    max-width: 300px;
  }
}

@media #{$sdc-site-header-mobile-bp} {
  // lock body when nav is opened
  body[body-locked="true"] {
    overflow: hidden;
    position: fixed;
    width: 100%;
  }

  .sdc-site-header__top {
    padding: $sdc-site-header-top-padding-mobile 0;
  }

  .sdc-site-header__logo {
    height: $sdc-site-header-logo-height - 8;
  }

  .sdc-site-header__nav-wrap {
    background: $sdc-site-header-nav-mobile-background;
    color: $sdc-site-header-nav-mobile-color;
    z-index: $sdc-site-header-nav-mobile-z-index;
    display: block;
    direction: ltr;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    padding: inherit;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    will-change: opacity;
    transition: opacity .166s;
  }

  .sdc-site-header__nav-wrap[aria-hidden="false"] {
    display: block;
    visibility: visible;
    opacity: 1;
    pointer-events: all;
  }

  .sdc-site-header__menu-item-link {
    padding: .6em 0;
    // @NOTE - do not remove this, it is needed for the sdc-ui-block-link-underline mixin in sdc-prpoject-base
    background-clip: padding-box !important;
    border-bottom: $sdc-site-header-nav-mobile-link-border;
  }

  .sdc-site-header__nav-cell--2 {
    padding: 1em 0 .25em;
  }

  .sdc-site-header__subnav-menu {
    margin-left: 0 !important;
  }

  .sdc-site-header__subnav-item {
    margin-left: 5px;
  }

  .sdc-site-header__subnav-item:not(.sdc-site-header__subnav-item--mobile-nav) > .sdc-site-header__subnav-item-link {
    font-size: 0;
    color: transparent;
  }

  .sdc-site-header__subnav-item--mobile-nav > .sdc-site-header__subnav-item-link[aria-expanded="true"] {
    color: black;
    fill: black;
  }

  .sdc-site-header__nav {
    padding: .1em 0;
    height: calc(100vh - 60px);
    top: 40px;
    padding-bottom: 60px;
    -webkit-overflow-scrolling: touch;
    overflow: auto;
    position: relative;
  }

  .sdc-site-header__subnav-item--login-nav .sdc-site-header__subnav-item-link[aria-expanded="true"]:after {
    bottom: -21px;
  }

  @supports (-webkit-overflow-scrolling: touch) {

    .sdc-site-header__nav:after {
      content: "";
      width: 1px;
      height: 110%;
      visibility: hidden;
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  // lock tabbing for everything without data-unlock-tabbing set
  // this scopes the tabbing/screenreader to just the open mobile nav on not anything underneath
  body[data-lock-tabbing="true"] > :not([data-unlock-tabbing]) {
    visibility: hidden !important;
  }

  .sdc-site-header__menu-cell--2[data-role="overflow-navigation"],
  .sdc-site-header__subnav-item--action-button {
    display: none;
  }

  .sdc-site-header__menu-cell--1 .sdc-site-header__menu-item[class] {
    visibility: visible !important;
  }
}

@media #{$sdc-site-header-small-mobile-bp} {

  .sdc-site-header__logo {
    height: $sdc-site-header-logo-height - 13;
  }

  .sdc-site-header__subnav-item--login-nav .sdc-site-header__subnav-item-link[aria-expanded="true"]:after {
    bottom: -20px;
  }
}
