.sdc-interactive-hot-spots__caption {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  min-height: 28%;
  transition: transform 0.33s;
  transform: translateY(100%);
  will-change: transform;
  pointer-events: none;
  padding: 0 9px;
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sdc-interactive-hot-spots__caption[aria-hidden="false"] {
  transform: translateY(0);
}

.sdc-interactive-hot-spots__caption-inner {
  max-width: 600px;
  margin: 0 auto;
  pointer-events: all;
  width: 100%;
  position: relative;
  border-radius: 3px;
}

.sdc-interactive-hot-spots__caption-title {
  font-size: 1.8em;
  font-weight: 500;
  margin-bottom: 0.5em;
}

.sdc-interactive-hot-spots__caption-text {
  line-height: 1.4;
}

.sdc-interactive-hot-spots__caption-text h4 {
  font-size: 1.6em;
  font-weight: 600;
  margin-bottom: 2px;
}

.sdc-interactive-hot-spots__caption-text h4 ~ h4 {
  margin-top: 0.5em;
}

.sdc-interactive-hot-spots__caption-text a {
  text-decoration: none;
  color: #0072c9;
  font-weight: 500;
}

.sdc-interactive-hot-spots__caption-close {
  position: absolute;
  right: 12px;
  top: 12px;
  font-size: 25px;
  font-weight: 200;
  text-decoration: none;
}

.sdc-interactive-hot-spots__caption-nav {
  pointer-events: all;
}

.sdc-interactive-hot-spots__caption-nav-links[class] {
  max-width: 600px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  list-style: none;
  justify-content: space-between;
  padding: 0 !important;
}

.sdc-interactive-hot-spots__caption-nav-links[class] > li {
  margin: 0 !important;
}

.sdc-interactive-hot-spots__caption-nav-link {
  display: inline-block;
  color: white;
  padding: 14px;
  font-size: 16px;
  text-decoration: none;
  appearance: none;
  background: none;
  border: none;
  cursor: pointer;
  font-family: inherit;
}

.sdc-interactive-hot-spots__caption-nav-icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 14px;
  height: 14px;
  margin: 0 2px;
  top: -1px;
}

.sdc-interactive-hot-spots__caption-nav-link[data-role="caption-nav-previous"] .sdc-interactive-hot-spots__caption-nav-icon {
  transform: rotate(180deg);
}

.sdc-interactive-hot-spots__caption-nav-icon > svg {
  position: absolute;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.sdc-interactive-hot-spots__caption-item {
  display: flex;
  margin: 3px 0;
  background: white;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.sdc-interactive-hot-spots__caption-item figure {
  width: 30%;
  min-width: 90px;
  position: relative;
}

.sdc-interactive-hot-spots__caption-item figure[data-src=""] {
  display: none;
}

.sdc-interactive-hot-spots__caption-item img {
  display: block;
  width: 100%;
  position: absolute;
  object-fit: cover;
  top: 50%;
  transform: translateY(-50%);
}

@supports (object-fit: cover) {

  .sdc-interactive-hot-spots__caption-item img {
    height: 100%;
  }
}


.sdc-interactive-hot-spots__caption-item-body {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2em;
  padding: calc(10px + 2vw);
}

.sdc-interactive-hot-spots__caption-item-body p {
  margin: 0 !important;
}

.sdc-interactive-hot-spots__caption-item-body p:empty {
  display: none;
}

.sdc-interactive-hot-spots__caption-item-body ul {
  padding: 0;
  list-style: none;
}

.sdc-interactive-hot-spots__caption-item-body ul ~ p {
  margin-top: 0.5em !important;
}

.sdc-interactive-hot-spots__caption-item-body a {
  color: $sdc-interactive-hot-spots-link-color;
}

.sdc-interactive-hot-spots__caption-item-body strong {
  font-weight: 500;
}

.sdc-interactive-hot-spots__caption-item-body h4 {
  font-size: calc(16px + 1vw);
  font-weight: 400;
  margin-bottom: 0.25em;
  padding-right: 15px;
  line-height: 1.1;
}

@media all and (max-width: 580px) {

  .sdc-interactive-hot-spots__caption-item-body {
    font-size: 15px;
  }

  .sdc-interactive-hot-spots__caption-nav-link {
    font-size: 15px;
  }
}

