@import "settings/settings";

// Override livefyre fotn declaration:
.fyre .fyre-comment-article[class],
.fyre .fyre-comment-stream[class] {
  font-family: var(--branding-sky-text-font), sans-serif;
}

.sdc-article-liveblog,
.sdc-article-liveblog * {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

.sdc-article-liveblog {
  @extend .site-wrap!optional;
  width: 100%;

  ul {
    list-style: none;
  }

  &-video {
    position: relative;
    padding-top: 56.25%;

    iframe {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      border: 0;
      overflow: hidden;
    }
  }
}

.fyre-liveblog {

  // Hide stuff
  .fyre-auth,
  .fyre-stream-stats,
  .fyre-help,
  .fyre-stream-sort-bar,
  .fyre-stream-header,
  .fyre-flag-link,
  .fyre-share-link {
    display: none !important;
  }

  // LIVEFYRE LOGIN BAR
  .fyre-login-bar > a {
    font-size: var(--base-font-size);
    line-height: 25px;
    margin-top: 0 !important;
    color: $link-text;
  }

  .fyre-stream-livecount {
    font-size: var(--supporting-text);
    color: $dark70;
  }

  .fyre-live-container {
    height: 25px;
  }

  // LIVEFYRE EDITOR
  .fyre-editor {
    top: -12px;
    clear: both;
    margin: 0;
    padding: $block-padding-s;
    background: $light90;
  }

  // LIVEFYRE STREAM HEADER
  .fyre-stream-header {
    position: relative;
    clear: both;
    margin: 0 0 $block-margin-m;
  }

  // LIVEFYRE FOLLOW BUTTON
  .fyre-follow-conversation {
    font-size: var(--supporting-text);
    position: absolute;
    right: $block-padding-s !important;
    bottom: 12px;
    display: inline-block;
    padding: 2px 4px 2px 1px;
    white-space: nowrap;
    text-decoration: none;
    color: #333;
  }

  .fyre-follow-conversation:before {
    font-family: icons;
    font-size: .75em;
    position: relative;
    left: 1px;
    display: inline-block;
    margin-right: 5px;
    content: "\e052";
    color: #999;
  }

  .fyre-stream-sort {
    float: none !important;
    margin: 0 !important;
  }

  // LIVEFYRE STREAM SORT OPTIONS
  .fyre-stream-sort-options {
    font-size: var(--base-font-size);
  }

  .fyre-stream-sort-options > .fyre-stream-sort-newest,
  .fyre-stream-sort-options > .fyre-stream-sort-oldest {
    display: block;
    display: inline-block;
    padding: $block-padding-s $site-indent-s;
    vertical-align: top;
    color: $dark90 !important;
  }

  .fyre-stream-sort-options > a:active {
    font-weight: normal !important;
  }

  .fyre-stream-sort-options > .fyre-stream-sort-selected {
    font-size: var(--base-font-size);
    font-weight: normal !important;
    pointer-events: none;
    color: $dark90 !important;
    background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAGQAAAAHAQMAAADETHrjAAAAA1BMVEUhTLgHgyq6AAAADElEQVQIHWNgoDUAAABiAAEjI2VLAAAAAElFTkSuQmCC");
    background-repeat: no-repeat;
    background-position: 0 155%;
    background-origin: content-box;
    background-size: 100% 2px;
  }
}

// LIVEFYRE LOGO
.livefyre-logo__link {
  background-image: url($livefyre-logo);
  background-repeat: no-repeat;
  display: block;
  height: 24px;
  font-size: 13px;
  text-decoration: none;
  color: #404040;
  padding-left: 28px;
  padding-top: 4px;
  font-family: sans-serif;

  &:hover {
    background-color: rgba(0, 0, 0, .08);
  }
}

// LIVEFYRE REPLIES
.sdc-article-liveblog .fyre {

  .fyre-stream-more {
    background: none !important;
  }

  .fyre-stream-more .fyre-stream-more-container {
    font-size: var(--supporting-text);
    color: $link-text;
    background: #fff;
    border: 1px solid #dcdcdc;
    display: block;
    height: auto;
    padding-top: .65em;
    padding-bottom: .65em;
    border-radius: 0;
  }

  .fyre-stream-more .fyre-stream-more-container:hover {
    background: $webkit-tap-highlight-color !important;
  }

  .fyre-stream-more .fyre-stream-more-container .fyre-text {
    font-size: var(--base-font-size);
    line-height: inherit !important;
    color: $text-primary;
  }
}

// MOBILE VIEW
@media all and ($bp30-max) {

  .site-layout-primary__col1 {
    padding: 0 $site-indent-bp30 !important;
  }

  // Scope everything to fyre-liveblog for specificity reasons...
  .fyre-liveblog {

    .fyre-stream-header {
      margin-bottom: $component-gap-s;
    }
  }

  .livefyre-logo__link {
    margin: 0 $component-gap-s $block-margin-s;
  }

  .article.article--livefyre {
    padding: $site-indent-s;
  }
}

// Importing other styles
@import "settings/pinned-items";
@import "settings/liveblog-comments";
