/* sdc-news-listing skin */

$sdc-news-listing-heading-size: 1.125rem !default;
$sdc-news-listing-intro-size: 1.125rem !default;
$sdc-news-listing-vertical-spacing: .6em !default;
$sdc-news-listing-vertical-padding: 1em !default;
$sdc-news-listing-horizontal-padding: .5em !default;
$sdc-news-listing-media-ratio: 75% !default;
$sdc-news-listing-vertical-spacing: .6em !default;
$sdc-news-listing-spacing-bottom: 2em !default;

$sdc-news-listing-media-padding: 1em !default;
$sdc-news-listing-media-max-width: 180px !default;
$sdc-news-listing-media-max-width-breakpoint: 600px !default;

$sdc-news-listing-item-border: 1px solid #dcdcdc !default;
$sdc-news-listing-media-border-color: #dcdcdc !default;
$sdc-news-listing-link-color: #333 !default;
$sdc-news-listing-link-hover-color: #3157a1 !default;
$sdc-news-listing-text-color: #333 !default;
$sdc-news-listing-misc-text-color: #666 !default;
