.sdc-article-header {
  position: relative;

  &:not(.sdc-article-header--feature) {
    padding-top: 2.5em;
    padding-bottom: 2.5em;
  }

  @media all and ($sdc-article-header-priority-3-breakpoint) {

    &:not(.sdc-article-header--feature) {
      padding-top: 2em;
      padding-bottom: 2em;
    }
  }

  @media all and ($sdc-article-header-priority-4-breakpoint) {

    &:not(.sdc-article-header--feature) {
      padding-top: 1.5em;
      padding-bottom: 1.5em;
    }
  }

  &--with-margin[class] {
    margin-bottom: $sdc-article-header-margin-bottom-desktop;

    @media all and ($sdc-article-header-priority-4-breakpoint) {
      margin-bottom: $sdc-article-header-margin-bottom-mobile;
    }
  }

  &__wrap:empty {
    display: none;
  }

  .sdc-article-header:not(.sdc-article-header--feature) &__wrap > * {
    // max-width: $sdc-article-header-inner-width;
  }

  &__details > .sdc-article-header__wrap {
    margin-top: $sdc-article-header-wrap-margin-top;

    @media all and ($sdc-article-header-priority-4-breakpoint) {
      margin-top: 1.25em;
    }
  }

  &__titles {
    position: relative;
  }

  &__title {
    // margin-bottom: .45em;
  }

  // DE requires same headline for desktop and mobile,
  // so exclude DE from the standard article's short mobile headline feature

  &__title[data-short-title]:not(:lang(de)):before {
    @media all and ($sdc-article-header-priority-4-breakpoint) {
      content: attr(data-short-title);
    }
  }

  // DE requires same headline for desktop and mobile,
  // so exclude DE from the standard article's short mobile headline feature

  @media all and ($sdc-article-header-priority-4-breakpoint) {

    &__long-title:not(:lang(de)) {
      display: none;
    }
  }

  &__sub-title {
    // margin-bottom: .75em;
    // color: $sdc-article-header-sub-title-colour
  }

  &__author-name {
    margin-bottom: .4em;
  }

  &__details {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    max-width: 100%;
  }

  .sdc-article-author[class] {
    // padding: 3% 0 2%;
    padding: 0;
    max-width: 100%;

    &:last-child {
      padding-bottom: 0;
    }
  }

  .sdc-article-date[class][class],
  .sdc-article-tags[class][class] {
    padding: 0 !important;
  }

  .sdc-article-date__wrap {
    white-space: nowrap;
  }

  @media all and (max-width: 601px) {

    &__details {
      flex-direction: column;
      align-items: flex-start;
    }

    &__date-time {
      margin-bottom: var(--component-gap);
    }

    &__tags {
      margin-left: -0.25em;
    }
  }
}
