  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }

.sdc-site-video {
  padding: 0;
  font-size: var(--component-text, 1em);
  margin-bottom: var(--component-margin); }

.sdc-site-video:focus {
  outline: 0; }

.sdc-site-video[data-amp-iframe-embed="true"][class] {
  margin-bottom: 0; }

.sdc-site-video__content {
  position: relative;
  /* set 16/9 as a fallback if no aspectRatio is specified */
  padding-bottom: 56.25%; }

/* note - amp provides its own aspect ratio management so exclude it from this */
.sdc-site-video__content > *:not(amp-iframe) {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0; }

.sdc-site-video__poster {
  z-index: 2;
  border: 0;
  appearance: none;
  -webkit-appearance: none;
  overflow: hidden;
  pointer-events: none; }

.sdc-site-video__poster-img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  object-fit: cover;
  background: black; }

.sdc-site-video__loader {
  position: absolute;
  width: 16%;
  max-width: 130px;
  background: url("data:image/svg+xml;base64,");
  background-size: 100%;
  left: var(--component-padding, 7%);
  bottom: var(--component-padding, 5%);
  pointer-events: none;
  margin: 0 0 -1px -1px; }

.sdc-site-video__loader:after {
  content: "";
  display: block;
  padding-bottom: 100%; }

.sdc-site-video__time {
  position: absolute;
  right: var(--component-padding, 1em);
  bottom: var(--component-padding, 1em);
  z-index: 2;
  color: var(--text-color-invert, white);
  background: var(--shade-5, black);
  opacity: .9;
  padding: .5% 1%;
  font-weight: bold; }

.sdc-site-video__caption {
  background: var(--component-shade, #eee);
  font-size: var(--supporting-text, 16px);
  padding: var(--component-gap, 1em) var(--component-padding, 1em);
  line-height: 1.3; }

.sdc-site-video__visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px); }

/* is pre/loading - show spinner (and disable play button) */
.sdc-site-video[data-state="loading"] .sdc-site-video__loader {
  background-image: url("data:image/svg+xml;base64,");
  animation: sdc-site-video-loader linear 1s infinite; }

.sdc-site-video[data-state="loading"] button {
  visibility: hidden; }

.sdc-site-video[data-state="loading"] .sdc-site-video__poster-img {
  pointer-events: all; }

/* has loaded - remove poster */
.sdc-site-video[data-state="loaded"] .sdc-site-video__poster {
  display: none; }

/* autoplay has no poster overlay */
.sdc-site-video[data-autoplay="true"] .sdc-site-video__poster {
  display: none; }

/* hide the dock shelf before player has loaded to avoid it capturing the keyboard focus */
.sdc-site-video[data-state="ready"] .vjs-dock-shelf {
  visibility: hidden; }

/* Ensure focus outline is visible before the player has loaded, so a user can navigate to it via keyboard */
.sdc-site-video[data-state="ready"]:not([data-autoplay="true"]) .sdc-site-video__tag[class] {
