// brexit countdown timer styles

.sdc-site-countdown-timer.sdc-site-countdown-timer--homepage {
  @extend .site-wrap !optional;
  @extend .site-wrap-padding !optional;
  @extend .site-wrap--mobile-edge !optional;
  background-clip: content-box;
  margin-bottom: 20px;
  padding-top: 20px;
  margin-top: 0;

  .sdc-site-countdown-timer__inner {
    padding-left: 0;
  }

  .sdc-site-countdown-timer__header {
    width: auto;
    min-width: 200px;

    &-logo {
      max-height: 87px;
      min-height: 44px;
      width: 100%;
      max-width: 480px;
    }
  }

  .sdc-site-countdown-timer__body {
    padding-right: 10px;
  }

  .sdc-site-countdown-timer__wrap {
    padding: 0;
  }

  .sdc-site-countdown-timer__digit {
    color: #002f54;
    font-weight: bold;
  }

  &[data-finished="true"] {
    display: none;
  }
}
