.supreme-court-live {
  width: 100%;
  max-width: 976px;
  margin: 0 auto 1.2em;

  &__video {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;

    & iframe, & object {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%
    }
  }
  &__caption {
    width: 100%;
    display: block;
    //padding: 10px 8px;
    min-height: 40px;

    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1.4;
    text-align: center;
    background: #f4f5f7;
    color: var(--text-color-secondary);
  }
}