// ----------------------------------------------------------
// weather-component skin overrides
// ----------------------------------------------------------

.weather-component__wrapper {
  font-size: 85%;
}

@media all and (min-width: 600px) {
  .weather-component__wrapper {
    font-size: 70%;
  }
}

@media all and (min-width: 600px) and (max-width: 859px) {
  .weather-component.sdc-site-tile[data-type~="hero-horizontal"]{
    width: 90%;
  }
}
