%spinner {
  content: " ";
  position: absolute;
  top: calc(50% - .55em);
  left: calc(50% - .55em);
  display: block;
  width: 1em;
  height: 1em;
  font-size: $sdc-site-load-more-spinner-size;
  color: $sdc-site-load-more-button-color;
  border: .1em solid;
  border-color: transparent currentColor currentColor;
  overflow: hidden;
  text-indent: 100%;
  border-radius: 100%;
  animation: spin $sdc-site-load-more-spinner-duration infinite linear;
}

@keyframes spin {

  to {
    transform: rotate(360deg);
  }
}
