// sdc-article-gallery skin

// FONTS
$sdc-article-gallery-caption-font-size-default: 1rem !default;
$sdc-article-gallery-caption-font-size-medium: 1.125rem !default;

// DIMENSIONS
$sdc-article-gallery-paragraph-spacing: 1.2em !default;
$sdc-article-gallery-caption-min-height-default: 4.44em !default;
$sdc-article-gallery-caption-min-height-medium: 5em !default;

// COLORS
$sdc-article-gallery-background: #f4f4f4 !default;
$sdc-article-gallery-caption-color: #4a4a4a !default;
$sdc-article-gallery-control-background: rgba(0, 0, 0, .3) !default;
$sdc-article-gallery-control-background-hover: rgba(0, 0, 0, .6) !default;
$sdc-article-gallery-caption-label-color: #000 !default;

$sdc-article-gallery-caption-v-padding: 1em !default;
$sdc-article-gallery-caption-h-padding: 1em !default;

// Breakpoints
$sdc-article-gallery-small: 481px !default;
$sdc-article-gallery-medium: 681px !default;
$sdc-article-gallery-large: 701px !default;
$sdc-article-gallery-xlarge: 851px !default;
$sdc-article-gallery-xxlarge: 1020px !default;

// AMP
$sdc-article-gallery-amp-caption-height: 2em;
$sdc-article-gallery-amp-button-size: 28px;

// Other options
$simplify-for-amp: false !default;
