// TYPOGRAPHY: Body text
.sdc-article-body {

  > p {
    @extend %vertical-spacing !optional;
    @extend #{$sdc-article-body-wrap-class} !optional;
    @extend %sdc-article-body-width-limiter !optional;
    @extend %link-behaviour !optional;
    color: $sdc-article-body-text-color;
    word-break: break-word;

    img {
      width: 100%;
    }

    > sup,
    > sub {
      vertical-align: baseline;
      position: relative;
      top: -.4em;
    }

    > sub {
      top: .4em;
    }

    // Apply link styling to links within elements directly inside p
    > strong,
    > b,
    > em,
    > i {

      a {
        @extend %link-behaviour !optional;
      }
    }
  }

  // Lead paragraph
  &--lead > p:first-of-type {
    font-weight: $sdc-article-body-lead-font-weight;
  }
}
