// Widgets outside article body: Match article body copy width
[class*="--match-article-body-copy-width"] {
  @extend %sdc-article-body-width-limiter !optional;

  padding-bottom: $sdc-article-body-v-margin;
  padding-left: $sdc-article-body-edge-margin;
  padding-right: $sdc-article-body-edge-margin;
}

