// Topic banner overrides
div.sdc-site-topic-banner--article-body-width {
  @extend %sdc-article-body-width-limiter !optional;
  padding: 0 $sdc-article-body-edge-margin;
  margin-top: $sdc-article-body-widget-margin-large;

  @media all and (min-width: $sdc-article-body-break-xlarge) {
    margin-top: $sdc-article-body-widget-margin-xlarge;
  }
}
