/* The main structural styles to create a vanilla version of this component */
.sdc-site-localnav {
  position: relative;
  background: $sdc-site-localnav-background;
  color: $sdc-site-localnav-color;
  z-index: $sdc-site-localnav-z-index;
  margin-bottom: $sdc-site-localnav-margin-bottom;
  margin-top: $sdc-site-localnav-margin-top-default;
}

// if one version of localnav sits after enough, make sure first in order has the higher z index
.sdc-site-localnav ~ .sdc-site-localnav {
  z-index: $sdc-site-localnav-z-index - 1;
}

.sdc-site-localnav__icon {
  position: relative;
  width: 1em;
  height: 1em;
  fill: $sdc-site-localnav-mobile-icon-color;
  display: inline-block;
  vertical-align: middle;
}

.sdc-site-localnav__header .sdc-site-localnav__icon {
  left: -0.7em;
}

.sdc-site-localnav__header-logo-wrap {
  display: block;
  padding-right: 1.3em;
  padding-left: .7em;
}

.sdc-site-localnav__header-logo {
  display: inline-block;
  height: 2.88em;
  min-width: 2.5em;
  vertical-align: middle;
}

// item
.sdc-site-localnav__item {
  display: inline-block;
  position: relative;
}

// item link && current section name
.sdc-site-localnav__current-section-name,
.sdc-site-localnav__item-link {
  line-height: $sdc-site-localnav-links-height-desktop;
  padding: 0 $sdc-site-localnav-links-indent;
  position: relative;
}

// display item link
.sdc-site-localnav__item-link {
  display: block;
}

.sdc-site-localnav__logo--additional {
  vertical-align: text-top;
  position: relative;
  top: -1px;
}

// hide current section name by default
.sdc-site-localnav__current-section-name {
  display: none;
}

.sdc-site-localnav__more .sdc-site-localnav__item-link {
  padding: 0;
}

// current/active link
.sdc-site-localnav__item-link[aria-current="true"] {
  font-weight: bold;
}

.sdc-site-localnav__item-link--additional[target="_blank"]:after {
  display: none;
}

// disable external link icon if not skysport.de
.sdc-site-localnav__item-link[target="_blank"]:not(:lang(de)):after {
  display: none;
}

// hover and highlighted styles
// .sdc-site-localnav:not(.sdc-site-localnav--sticky) .sdc-site-localnav__item-link:focus:before,
// .sdc-site-localnav__item-link:hover:before {
//   height: $sdc-site-localnav-links-border-hover;
// }

// .sdc-site-localnav__item-link[aria-current="true"]:before {
//   height: $sdc-site-localnav-links-border-current;
// }

// localnav can optionally filter a series of elements on a page by a hash value
[data-hash-target][aria-hidden="true"] {
  display: none;
}

// filter nav has no background
.sdc-site-localnav--filter {
  background: $sdc-site-localnav-background-filter;
  margin-top: -.9em;
}

.sdc-site-localnav--filter > .sdc-site-localnav__inner {
  margin-bottom: -.5em;
}

.sdc-site-localnav--filter .sdc-site-localnav__more {
  background: $sdc-site-localnav-background-filter-more;
}

.sdc-site-localnav--sticky {
  @include sticky;
}

@media all and (min-width: $sdc-site-localnav-break-medium) {
  @include desktop-view;

  .sdc-site-localnav--single-item-mobile-nav {
    @include mobile-single-item-view-desktop-tweaks;
  }

  .js .sdc-site-localnav__items {
    height: $sdc-site-localnav-links-height-desktop;
  }

  .sdc-site-localnav__header-title--mobile {
    display: none;
  }
}

@media all and (max-width: $sdc-site-localnav-break-medium - 1) {

  .sdc-site-localnav {
    font-size: .94em;
  }

  .sdc-site-localnav--mobile {
    @include mobile-view;
  }

  .sdc-site-localnav:not(.sdc-site-localnav--mobile) {
    @include desktop-view;
  }

  .sdc-site-localnav--single-item-mobile-nav {
    @include mobile-single-item-view;
  }

  .js .sdc-site-localnav:not(.sdc-site-localnav--mobile) .sdc-site-localnav__items {
    height: $sdc-site-localnav-links-height-desktop;
  }

  .js .sdc-site-localnav__body {
    display: none;
  }

  .js .sdc-site-localnav__body[data-state-hidden="false"] {
    display: block;
  }

  .sdc-site-localnav__items > .sdc-site-localnav__item > .sdc-site-localnav__item-link {
    padding-left: $sdc-site-localnav-mobile-indent;
    padding-right: $sdc-site-localnav-mobile-indent;
  }

  .sdc-site-localnav__header-title--desktop {
    display: none !important;
  }
}

@media all and (min-width: $sdc-site-localnav-break-xlarge) {

  .sdc-site-localnav {
    margin-top: $sdc-site-localnav-margin-top-xlarge;
  }
}
