// Feature Styles
.sdc-article-header--feature {
  margin-top: 0;
  display: flex;
  width: 100%;

  .sdc-article-header {

    &__wrap {
      width: 100%;
    }

    &__main {
      background-color: $sdc-article-header-background-colour;
      min-height: $sdc-article-header-feature-height;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      position: relative;
    }

    &__titles {
      color: $sdc-article-header-feature-text-colour;
      width: 100%;
      z-index: 5;
      text-shadow: 0 .1em .3em rgba(0, 0, 0, .5);
      padding-top: 2em;
      padding-bottom: 2em;

      > * {
        position: relative;
        z-index: 2;
      }

      &:after {
        content: "";
        background: $sdc-article-header-body-background;
        position: absolute;
        width: 100vw;
        height: 100%;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        z-index: 1;
      }
    }

    &__image {
      top: 0;
      height: 100%;
      object-fit: cover;
      z-index: 0;
      position: absolute;
      max-width: 100vw;
      width: 100vw;
      left: 50%;
      transform: translateX(-50%);
      background-color: $sdc-article-header-background-colour;
    }

    @media all and (max-width: 601px) {

      &__details {
        display: block;
      }

      &__date-time {
        margin-bottom: 1.75em;
      }
    }
  }

  .sdc-article-header__main + .sdc-article-author,
  .sdc-article-header__main + .sdc-article-header__details {
    margin-top: var(--component-margin);
  }
}
