// Utility styles available to across all components and apps (if they use sdc-project-base)
// -- All classes start with .u- to namespace as "utilities"
// -----------------------------------------------------------------------------------------

// Available only to screenreaders + keyboard users
.u-hide-visually:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}