/* sdc-news-listing */

.sdc-news-listing {
    display: block;
    margin: 0 0 $sdc-news-listing-spacing-bottom;

    &__list {
        margin: $sdc-news-listing-vertical-padding 0 0;
        padding: 0;
        border-top: $sdc-news-listing-item-border;
        list-style: none;

        &--topics {
            margin-top: 0;
            border-top: 0;
        }
    }
    &__item {
        overflow: hidden;
        margin: 0;
        padding: $sdc-news-listing-vertical-padding 0;
        border-bottom: $sdc-news-listing-item-border;
    }
    &__media {
        float: left;
        overflow: hidden;
        box-sizing: border-box;
        width: 40%;

        &-img {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            max-height: 100%;
            width: 100%;
            height: auto;
            border: 1px solid $sdc-news-listing-media-border-color;
        }
    }
    &__ratio {
        position: relative;
        padding-bottom: $sdc-news-listing-media-ratio;
    }
    &__headline {
        box-sizing: border-box;
        margin: 0 0 0 calc(40% + #{$sdc-news-listing-media-padding});
        padding: 0;
        color: $sdc-news-listing-link-color;
        font-size: $sdc-news-listing-heading-size;
        line-height: 120%;
    }
    &__link {
        color: $sdc-news-listing-link-color;
        text-decoration: none;

        &:hover,
        &:focus {
            color: $sdc-news-listing-link-hover-color;
        }
    }
    &__body {
        clear: both;
        box-sizing: border-box;
        padding: $sdc-news-listing-vertical-spacing 0 0;
    }
    &__intro {
        margin: 0 0 $sdc-news-listing-vertical-spacing;
        color: $sdc-news-listing-text-color;
        font-size: $sdc-news-listing-intro-size;
    }
    &__source, &__updated {
        color: $sdc-news-listing-misc-text-color;
    }
}

@media screen and (min-width: 375px) {
    .sdc-news-listing {
        &__media {
            width: 30%;
        }
        &__headline,
        &__body {
            margin-left: calc(30% + #{$sdc-news-listing-media-padding});
            clear: none;
        }
    }
}

@media screen and (min-width: $sdc-news-listing-media-max-width-breakpoint) {
    .sdc-news-listing {
        &__media {
            max-width: $sdc-news-listing-media-max-width;
        }
        &__headline,
        &__body {
            margin-left: calc(#{$sdc-news-listing-media-max-width} + #{$sdc-news-listing-media-padding});
        }
    }
}
