// Article widget: layout variations
.sdc-article-body {

  [class*="--half-width"] {
    @media all and (min-width: #{$sdc-article-body-break-xlarge}-5px) {
      transition: margin-left $sdc-article-body-no-transition;
    }

    @media all and (min-width: $sdc-article-body-break-xlarge) {
      width: 100%;
      overflow: visible;
      margin-bottom: 0;
      height: 1px;

      > figure,
      > [class*="_inner"] {
        width: 50%;
        float: right;
        margin-left: $sdc-article-body-widget-margin-default;
        margin-top: .5rem;
        margin-bottom: $sdc-article-body-widget-margin-default;
      }
    }
  }
}
