.sdc-article-quote {
    overflow: hidden;
    margin-left: $sdc-article-quote-margin-left;
    margin-right: $sdc-article-quote-margin-right;
    margin-top: $sdc-article-quote-margin-top;
    margin-bottom: $sdc-article-quote-margin-bottom;
    font-size: $sdc-article-quote-base-font-size;
    position: relative;
    max-width: $sdc-article-quote-max-width;
    box-sizing: border-box;
    clear: both;

    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
    }

    &__content {
        margin: 0;
        text-align: center;
        border-radius: $sdc-article-quote-border-radius;
        padding: $sdc-article-quote-padding/2;
        box-sizing: border-box;
        background: $sdc-article-quote-text-bg;
        border-top: $sdc-article-quote-border-top;
        border-right: $sdc-article-quote-border-right;
        border-bottom: $sdc-article-quote-border-bottom;
        border-left: $sdc-article-quote-border-left;
        width: 100%;
        z-index: 10;
    }

    &__text {
        display: block;
        color: $sdc-article-quote-text-colour;
        font-size: $sdc-article-quote-text-default;
        margin-bottom: 0.75em;
        line-height: 1.2;

        &:before {
            content: "\201C";
        }

        &:after {
            content: "\201D";
        }
    }

    &__citation {
        color: $sdc-article-quote-citation-colour;
        font-size: $sdc-article-quote-citation-text-default;
        font-style: normal;

        &:before {
            content: "\2014\00a0";
        }
    }
}

@media (min-width: $sdc-article-quote-break-medium) {
    .sdc-article-quote {
        &__content {
            padding: $sdc-article-quote-padding;
        }

        &__text {
            font-size: $sdc-article-quote-text-medium;
        }

        &__citation {
            font-size: $sdc-article-quote-citation-text-medium;
        }
    }
}
