// ARTICLE STRAPLINE COMPONENT
// ===================

.sdc-article-strapline--button-layout {
  text-align: center;

  .sdc-article-strapline {
      &__text {
        margin-bottom: 0.25em;
        color: $sdc-article-strapline-text-color;
      }

      &__button {
        font-weight: bold;
        font-size: $sdc-article-strapline-button-font-size-default;
        color: $sdc-article-strapline-button-color;
      }
  }
}
