/* TILE */
.sdc-site-tile {
  font-size: var(--component-text);
  // @note - don't use combined background syntax here
  background-color: var(--component-bg);
  // background-image: $sdc-site-tile-background-image;
  position: relative;
}

// image
.sdc-site-tile__figure {
  overflow: hidden;
  position: relative;
  z-index: 3;
}

.sdc-site-tile__image-wrap {
  position: relative;
}

.sdc-site-tile__image-wrap:after {
  content: "";
  display: block;
  padding-bottom: 56.25%;
}

.sdc-site-tile__image {
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
  min-height: 1px;
  transition: transform var(--sdc-site-tile-transition-time);
  will-change: transform;
}

// body
.sdc-site-tile__body {
  padding: var(--component-padding, 1em);
  background: inherit;
  word-break: break-word;
}

// tile info
.sdc-site-tile__info {
  position: relative;
  cursor: default;
  z-index: 5;
  pointer-events: none;
  margin: 0 0 2px 1px;
}

.sdc-site-tile__info:empty {
  display: none;
}

// tag
.sdc-site-tile__tag[class] {
  display: inline-table;
  font-weight: normal;
  font-size: var(--label-text);
  color: var(--link-color);
  margin: 0 .6em 0.2em 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  cursor: default;
  vertical-align: middle;
}

.sdc-site-tile__tag-link {
  display: inline-block;
  color: inherit;
  pointer-events: all;
}

.sdc-site-tile__tag--haslink > .sdc-site-tile__tag-link,
.sdc-site-tile__tag:not(.sdc-site-tile__tag--haslink) {
  padding: .2em 0;
}

// time
.sdc-site-tile__date-time {
  font-size: var(--label-text);
  color: var(--secondary-text-color);
  padding: 0.2em 0;
  white-space: nowrap;
  overflow: hidden;
}

// headline
.sdc-site-tile__headline {
  font-size: var(--display-4);
  line-height: 1.15;
  font-weight: normal;
  margin-bottom: 0.25em;
  color: inherit;
}

.sdc-site-tile__headline-text {
  display: block;
  z-index: 3;
  position: relative;
  color: inherit;
  background: inherit;
}

// ensure main tile link covers full panel
.sdc-site-tile__headline-link:before,
.sdc-site-tile__headline:after {
  content: "";
  position: absolute;
  left: -5px;
  top: -5px;
  bottom: -5px;
  right: -5px;
  z-index: 4;
  background: rgba(255, 255, 255, .001);
}

.sdc-site-tile__headline:after {
  opacity: 0;
  transition: opacity var(--sdc-site-tile-transition-time);
  will-change: opacity;
  background: var(--sdc-site-tile-hover-background);
  z-index: 2;
}

// change colors on hover
.sdc-site-tile--has-link:hover .sdc-site-tile__body,
.sdc-site-tile--has-link:hover .sdc-site-tile__related,
.sdc-site-tile--has-link:hover .sdc-site-tile__tag {
  color: var(--sdc-site-tile-hover-color);
}

// show colored overlay on hover
.sdc-site-tile--has-link:hover .sdc-site-tile__headline:after {
  opacity: 1;
}

// animate image on hover
.sdc-site-tile--has-link:hover .sdc-site-tile__image {
  transform: var(--sdc-site-tile-hover-image-transform);
}

// snippet
.sdc-site-tile__snippet {
  font-size: var(--component-text);
  line-height: 1.4;
  position: relative;
  z-index: 3;
  color: inherit;
}

// related
.sdc-site-tile__related {
  margin: 1.25em 0 0;
  padding: 0.16em 0;
  position: relative;
  z-index: 4;
}

.sdc-site-tile__related-link {
  display: inline;
  line-height: 1.2;
}

// badges
.sdc-site-tile__badge {
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 40%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 5%, rgba(0, 0, 0, 0) 100%);
  z-index: 2;
  pointer-events: none;
}

.sdc-site-tile__badge > svg {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 14%;
  min-width: 50px;
  max-width: 115px;
  transform: translate(30%, -30%);
  z-index: 2;
  pointer-events: none;
  fill: white;
}

.sdc-site-tile__duration {
  display: block;
  position: absolute;
  z-index: 5;
  line-height: 1;
  right: 5%;
  bottom: 8%;
  padding: 0.2em 0.4em;
  font-size: var(--label-text);
  background-color: var(--shade-5);
  color: var(--text-color-invert);
}

@media #{$sdc-site-tiles-bp-tablet} {

  .sdc-site-tile:not([data-type*="hero"]) {
    padding: calc(var(--component-padding) - 1%) var(--component-padding);
  }

  .sdc-site-tile:not([data-type*="hero"]) .sdc-site-tile__body {
    padding: 0 0 0 var(--component-gap);
  }
}
