// ARTICLE STRAPLINE COMPONENT
// ===================

.sdc-article-strapline--default-layout {
  @extend %sdc-article-strapline--columns;

  .sdc-article-strapline {

    &__link {
      border-radius: $sdc-article-strapline-border-radius;
      padding: $sdc-article-strapline-link-padding;
      background-color: $sdc-article-strapline-background-color;
      border: $sdc-article-strapline-border;

      &:hover,
      &:active {
        background-color: #fff;
      }
    }

    &__title {
      font-size: $sdc-article-strapline-title-font-size-default;
      font-weight: normal;
      margin-bottom: 0.5em;
      line-height: 1.2;
      color: $sdc-article-strapline-text-color;
    }

    &__text {
      font-size: $sdc-article-strapline-text-font-size-default;
      line-height: 1.375;
      color: $sdc-article-strapline-text-color;
    }

    &__button {
      margin-top: $sdc-article-strapline-button-margin-top;

      // expand hover area to whole component
      &:before {
        content: "";
        position: absolute;
        left: -1px;
        top: -1px;
        bottom: -1px;
        right: -1px;
        z-index: 4;
      }
    }
  }
}

@media all and (min-width: $sdc-article-strapline-break-medium) {
  .sdc-article-strapline--default-layout {
      .sdc-article-strapline {

        &__title {
            font-size: $sdc-article-strapline-title-font-size-medium;
        }

        &__text {
          font-size: $sdc-article-strapline-text-font-size-medium;
        }
      }
  }
}
