// ARTICLE STRAPLINE COMPONENT
// ===================

.sdc-article-strapline {
  max-width: 100%;
  position: relative;
  font-size: $sdc-article-strapline-base-font-size;

  .sdc-article-strapline {
      &__link {
        color: inherit;
      }
  }
}

// Columns layout
%sdc-article-strapline--columns {
    .sdc-article-strapline {
        &__link {
            display: block;
            width: 100%;
            position: relative;
            z-index: 2;
        }
        &__body {
          text-align: center;
        }
        &__image-wrap {
            width: 100%;
            box-sizing: content-box;
            margin-bottom: 1em;
        }
        &__image {
            display: block;
            width: 100%;
            height: auto;
        }
        &__body {
            width: 100%;
        }
        &__text {
            margin-bottom: 0;
        }

        &__button {
            font-size: $sdc-article-strapline-button-font-size-default;
        }

        @media all and (min-width: $sdc-article-strapline-break-small) {
            &__link {
                display: table;
                table-layout: fixed;
            }
            &__body {
              text-align: left;
            }
            &__image-wrap {
              width: 100px;
              padding-right: 21px;
              margin-bottom: 0;
            }
            &__image-wrap,
            &__body {
                display: table-cell;
                vertical-align: top;
            }
        }

        @media all and (min-width: $sdc-article-strapline-break-medium) {
            &__image-wrap {
              width: 160px;
              padding-right: 28px;
            }
            &__body {
                vertical-align: middle;
            }
        }
    }
}
