@mixin sticky {
  height: $sdc-site-localnav-links-height-desktop;

  .sdc-site-localnav__sticky-content[data-stuck="true"] {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: $sdc-site-localnav-z-index;
    background: $sdc-site-localnav-background;
  }

  .sdc-site-localnav__items {
    height: $sdc-site-localnav-links-height-desktop;
  }

  .sdc-site-localnav__item-link[aria-current="true"] {
    font-weight: normal;
    text-shadow: 0 0 0.25px currentColor;

    @media only screen and (-webkit-min-device-pixel-ratio: 1.5) {
      text-shadow: 0 0 0.5px currentColor;
    }
  }
}
