/* SDC site header skin
  =========================================== */

// variables
$sdc-site-header-z-index: $z-index-top +1 !default;
$sdc-site-header-background: linear-gradient(to bottom, #0b0b44 0%,#0e0e79 100%) !default;
$sdc-site-header-color: white !default;
$sdc-site-header-mobile-nav-link-divide-color: 1px solid var(--border-color) !default;
$sdc-site-header-hamburger-color: white !default;
$sdc-site-header-hamburger-color-open: var(--text-color) !default;
$sdc-site-header-nav-link-icon-color: white !default;
$sdc-site-header-nav-link-icon-color-mobile: var(--text-color) !default;
$sdc-site-header-nav-wrap-border-color: var(--border-color) !default;
$sdc-site-header-nav-link-underline-color: white !default;
$sdc-site-header-drop-down-link-color: var(--text-color) !default;
$sdc-site-header-drop-down-link-border-color: var(--border-color) !default;
$sdc-site-header-drop-down-background: var(--border-color) !default;

// overrides
.sdc-site-header {
  margin-bottom: var(--component-margin);
}

.sdc-site-header__subnav-menu-item-link[target="_blank"],
.sdc-site-header__menu-item-link[target="_blank"] {
  @extend .sdc-ui-external-link !optional;
}

.sdc-site-header__inner,
.sdc-site-header__subnav-menu {
  @extend .site-wrap !optional;
  @extend .site-wrap-padding !optional;
}

// main nav active style
.sdc-site-header__menu-item-link[aria-current="true"] {
  @include sdc-ui-block-link-underline($background-clip: content-box);
}

// main nav hover style
// ensure background clip is sent to content box so line only spans text width
.sdc-site-header__menu-item-link:hover {
  @include sdc-ui-block-link-underline($background-clip: content-box);
}

.sdc-site-header__subnav-item-link:hover {
  @include sdc-ui-block-link-underline($thickness: 2px);
}

.sdc-site-header__subnav-menu-item-link:hover {
  @include sdc-ui-block-link-underline;
}

@media (hover: none){
  .sdc-site-header__menu-item-link:hover,
  .sdc-site-header__subnav-item-link:hover,
  .sdc-site-header__subnav-menu-item-link:hover {
    background-image: none
  }
}

.sdc-site-header__action-button {
  @include sdc-ui-button($color: white, $background-color: var(--live), $heavy: true);
}


@media #{$bp-xs}{
  .sdc-site-header {
    margin-bottom: 0
  }
}
