.sdc-article-related-stories {
    &--hidden {
        display: none;
    }
    
    &__content {
        border-top: 1px solid $sdc-article-related-stories-border-colour;
        border-bottom: 1px solid $sdc-article-related-stories-border-colour;
        padding: $sdc-article-related-stories-spacing;
    }

    &__title {
        font-weight: bold;
        font-size: $sdc-article-related-stories-title-font-size;
        line-height: 1.4;
        text-align: center;
        margin-top: 0;
        margin-bottom: $sdc-article-related-stories-spacing;
        color: $sdc-article-related-stories-title-colour;
    }
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__item {
        width: 100%;
        box-sizing: border-box;
        margin-bottom: $sdc-article-related-stories-spacing/2;

        &:last-child {
            margin-bottom: 0;
        }
    }
    &__link {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        color: $sdc-article-related-stories-link-colour;
        flex-direction: row-reverse;
    }
    &__image {
        width: $sdc-article-related-stories-thumbnail-size;
        display: block;
        margin-right: $sdc-article-related-stories-spacing/2;
    }
    &__headline {
        line-height: 1.2;
        color: $sdc-article-related-stories-headline-colour;
        font-size: $sdc-article-related-stories-headline-font-size;
        flex-grow: 1;
        margin: 0;
    }
}

@media screen and (min-width: $sdc-article-related-stories-two-col-breakpoint) {
    .sdc-article-related-stories {
        &__item {
            width: calc(50% - #{$sdc-article-related-stories-spacing} / 2);

            &:nth-last-child(2) {
                margin-bottom: 0;
            }
        }
    }
}
